<template>
    <div class="rddialogg" ref="yinones">
        <div class="box">
            <div class="boxtop"><span @click="yinnone">{{$t('shezhiye.quxiao')}}</span><span>{{$t('shezhiye.redmoshi')}}</span>
            <span @click="xgcanshu">{{$t('shezhiye.wancheng')}}</span></div>
            <div class="boxbotm">
             <div class="newwork">{{$t('shezhiye.wluocanshu')}}</div>
             <div class="SSID"><span>{{$t('shezhiye.SSID')}}</span><span @click="spanbh">{{workset}}</span>
             <span v-if="flgespan"></span><span v-else>{{$t('shezhiye.bukexiugai')}}</span></div>
             <div class="boxhavi" :class="{boxhavi1:boxhavs1}"></div>
             <div class="passwords"><div><span>{{$t('user.mima')}}</span><input v-if="sfmwen" type="password" v-model="textpwd" placeholder maxlength="32" @input="zzyanzheng" @focus="focus1" @blur="blur1">
             <input v-else type="text" v-model="textpwd" placeholder maxlength="32" @input="zzyanzheng" @focus="focus1" @blur="blur1"></div>
             <span @click="qfsfmwen"> <img v-if="sfmwen" src="../../../assets/yanjing.png" alt="">
             <img v-else src="../../../assets/biyanJing.png" alt=""></span></div>
             <div class="boxhavi" :class="{'boxhavi1':boxhavi11}"></div>
             <div class="zhworkfather"><div class="zhwork" v-show="noneflge" ref="shaker">{{$t('tsytongyong.shifouanxushuru8')}}</div></div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    flgespan: true,
    textpwd: '',
    boxhavs1: false,
    workset: '',
    noneflge: false,
    sfmwen: true,
    boxhavi11: false,
    timea: null,
    scal: 0
  }),
  created() {
    this.networkset()
  },
  inject: ['reload'],
  methods: {
    blur1() {
      this.boxhavi11 = false
      if (this.textpwd === '') {
        this.noneflge = false
      }
    },
    focus1() {
      this.boxhavi11 = true
    },
    qfsfmwen() {
      this.sfmwen = !this.sfmwen
    },
    // 正则校验 正则不为8到32位的时候就不许进入下一步
    zzyanzheng() {
      const reg = /^\w{8,32}$/
      if (!reg.test(this.textpwd)) {
        this.noneflge = true
      } else {
        this.noneflge = false
      }
    },
    spanbh() {
      this.flgespan = false
      this.boxhavs1 = true
    },
    yinnone() {
      this.$refs.yinones.style.display = 'none'
      this.textpwd = ''
      this.noneflge = false
    },
    // generator() {
    //   for (var i = 0; i < this.times; i++) {
    //     var offset = Math.ceil((Math.random() + 3) * 3)
    //     if (i % 2 === 0) {
    //       this.offsets.push(offset)
    //     } else {
    //       this.offsets.push(-offset)
    //     }
    //   }
    //   this.scale = 0 // 记录目前表单已经摇晃的次数
    // },
    // counter() {
    //   if (this.scale < this.times) {
    //     var offset = this.offsets[this.scale]
    //     var position = parseInt(getComputedStyle(this.$refs.shaker).left)
    //     var distance = Math.abs(position) + Math.abs(offset)
    //     if (offset > 0) {
    //       this.mover(1, distance)
    //     } else {
    //       this.mover(-1, distance)
    //     }
    //     this.scale += 1
    //     setTimeout(() => {
    //       this.counter()
    //     }, 50)
    //   } else {
    //     this.$refs.shaker.style.left = '0px'
    //   }
    // },
    // mover(sign, distance) {
    //   var speed = sign * Math.ceil(distance * 0.6)
    //   this.$refs.shaker.style.left = parseInt(getComputedStyle(this.shaker).left) + speed + 'px'
    //   distance -= Math.abs(speed)
    //   if (distance > 0) {
    //     setTimeout(() => {
    //       this.mover()
    //     }, 10)
    //   }
    // },
    async xgcanshu() {
      if (this.textpwd === '') {
        this.$Toast({
          message: this.$t('tsytongyong.dqsrweikong'),
          position: 'top'
        })
        return
      }
      // 正则触发的规则还在的情况下提示
      if (this.noneflge) {
        // this.$Toast({
        //   message: this.$t('tsytongyong.qanxuqiushuru'),
        //   position: 'top'
        // })
        clearInterval(this.timea)
        console.log(this.$refs.shaker)
        this.timea = setInterval(() => {
          this.scal += 1
          this.$refs.shaker.style.left = 0.18 + 'rem'
          setTimeout(() => {
            this.$refs.shaker.style.left = 0.22 + 'rem'
          }, 50)
          if (this.scal >= 2) {
            this.scal = 0
            clearInterval(this.timea)
          }
          console.log(this.scal)
        }, 100)
        return
      }
      // 发送请求时候 提示用户等待层出现 当成功或者失败的时候 消失并且给一个轻提示
      this.$emit('wlyccchuxian', '')
      try {
        const verifypad = {
          cmd: 'update_network_settings',
          mode: 'AP',
          enabled: true,
          ssid: this.workset,
          wifi_pwd: this.textpwd
        }
        console.log(verifypad)
        const res = await this.Api.Statuto(verifypad)
        console.log(res)
        this.$emit('wlyccyincang', '')
        // this.$Toast.success(this.$t('tsytongyong.xiugaiAPcg'))
        this.$dialog.alert({
          title: this.$t('tsytongyong.xgchenggong'),
          message: this.$t('tsytongyong.xiugaiAPcg'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
        // 模态框确认以后 局部刷新
        this.reload()
      } catch {
        this.$emit('wlyccyincang', '')
        // this.$Toast.fail(this.$t('tsytongyong.qingqiushibai'))
        this.$dialog.alert({
          title: this.$t('tsytongyong.shezhishibai'),
          message: this.$t('tsytongyong.qingqiushibai'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
      }
      // const verifypad = {
      //   cmd: 'update_network_settings',
      //   mode: 'AP',
      //   enabled: true,
      //   ssid: this.workset,
      //   wifi_pwd: this.textpwd
      // }
      // console.log(verifypad)
      // this.Api.Statuto(verifypad).then((res) => {
      //   this.$Toast.success(this.$t('tsytongyong.xiugaiAPcg'))
      //   this.reload()
      // }).catch(() => {
      //   this.$Toast.fail(this.$t('tsytongyong.qingqiushibai'))
      // })
    },
    async networkset() {
      try {
        const poshms = {
          cmd: 'get_network_settings',
          mode: 'AP'
        }
        const refsa = await this.Api.Statuto(poshms)
        console.log(refsa)
        this.workset = refsa.data.ssid
      } catch (err) {
      }
    }
  }
}
