<script>
import upLevel from '@/components/upLevel/index'
let testverson = false
export default {
  data: () => ({
    currentPage: 1,
    id: '',
    modesbb: ''
  }),
  created() {
    this.id = this.$route.params.id
    console.log(this)
    this.modes()
  },
  beforeRouteEnter(to, from, next) {
    next(() => {
      if (from.fullPath === '/login') {
        testverson = true
      }
    })
  },
  methods: {
    async modes() {
      try {
        const huoqumoshi = {
          cmd: 'get_robot_info'
        }
        const resmode = await this.Api.Statuto(huoqumoshi)
        this.modesbb = resmode.data.robot_mode
      } catch {
      }
    },
    fn2() {
      this.active += 1
    },
    fn1() {
      this.$router.push(`/home/${this.id}`)
    },
    async activehome() {
      try {
        const paractiv = {
          cmd: 'activate'
        }
        const resqsa = await this.Api.Statuto(paractiv)
        this.$Toast({
          message: this.$t('tsytongyong.jihuochenggong'),
          position: 'top'
        })
        // this.$Toast.success(this.$t('tsytongyong.jihuochenggong'))
        localStorage.setItem('actived', true)
        this.$router.push(`/home/${this.id}`)
        console.log(resqsa)
      } catch (err) {
        // this.$Toast.fail(this.$t('tsytongyong.jihuoshibai'))
        this.$Toast({
          message: this.$t('tsytongyong.jihuoshibai'),
          position: 'top'
        })
      }
    },
    // async activshutdown() {
    //   try {
    //     const aashutdown = {
    //       cmd: 'shutdown'
    //     }
    //     const rshutdown = await this.Api.Statuto(aashutdown)
    //     this.$Toast.success('正在关机，请稍后')
    //     localStorage.removeItem('token')
    //     setTimeout(() => {
    //       this.$router.push('/login')
    //     }, 1500)
    //     console.log(rshutdown)
    //   } catch (err) {
    //     this.$Toast.fail('关机失败')
    //   }
    // },
    activshutdown() {
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.shifouguanji'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
        // 如果确认退出以后发送请求删除后台Token并且本地Token也删除
        // 延时500毫秒
      }).then(() => {
        const aashutdown = {
          cmd: 'shutdown'
        }
        this.Api.Statuto(aashutdown).then(() => {
          this.$Toast.success(this.$t('tsytongyong.guanjichenggong'))
          localStorage.removeItem('token')
          setTimeout(() => {
            this.$router.push('/login')
          }, 1500)
        }).catch(() => {
          this.$Toast.fail(this.$t('tsytongyong.guanjishibai'))
        })
        // console.log(rshutdown)
      })
    }
  },
  mounted() {
    if (testverson === true) {
      const paradereq = {
        cmd: 'check_ota_upgrade'
      }
      const gettaskreq = {
        cmd: 'get_task_state',
        include_detailed_state: false
      }
      this.Api.getotsstate(gettaskreq).then(() => {
      }).catch((err) => {
        if (err.request.status === 404) {
          this.Api.Statuto(paradereq).then((toparaders) => {
            if (toparaders.data.code === 0) {
              if (toparaders.data.isForced) {
                this.$refs.upLevel.showDialog('now', toparaders.data)
                return
              }
              if (!localStorage.getItem('hasUpLevel')) {
                this.$refs.upLevel.showDialog('noNow', toparaders.data)
              }
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  },
  destroyed() {
  },
  components: {
    upLevel
  }
  // computed: {
  //   useID() {
  //     return this.$store.state.usenameID
  //   }
  // }
}
</script>
