<template>
    <div class="bluetoonth">
         <div class="binju1">{{$t('shezhiye.menjinpaipeidui')}}:</div>
         <div class="binju2"><input type="text" v-model.trim="doorsener" @input="zhengzejy" maxlength="12"><button @click="tijiao">{{$t('xiaosharw.queren')}}</button></div>
        <div v-show="falge" class="pandingceng">{{$t('shezhiye.qinganxuqiushuru')}}</div>
           <div class="binju1">{{$t('nenbanben.yaokongqipeiduimac')}}:</div>
         <div class="binju2"><input type="text" v-model.trim="yaokongqi" @input="zhengzejyykq" maxlength="12"><button @click="tijiaoykq">{{$t('xiaosharw.queren')}}</button></div>
        <div v-show="falge1" class="pandingceng">{{$t('shezhiye.qinganxuqiushuru')}}</div>
        <!-- <div><span>工程模式Pir</span><input type="text" v-model.trim="textareavaluepir">
        </div>
         <div><span>工程模式wifi</span><input type="text" v-model.trim="textareavaluewifi">
        </div>
         <div><span>工程模式Al</span><input type="text" v-model.trim="textareavalueal">
        </div>
        <button @click="gongchengmoshi">工程模式提交</button> -->
       <div class="yincangceng" v-show="showyaokongqi">
          <div>{{peiduizhong}}</div>
        </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    doorsener: '',
    falge: false,
    falge1: false,
    textareavaluepir: '',
    textareavaluewifi: '',
    textareavalueal: '',
    yaokongqi: '',
    showyaokongqi: false,
    peiduizhong: ''
  }),
  created() {
    this.huoqudoor()
    this.huoquyaokong()
  },
  // 设置蓝牙 获取后台给的门禁牌值 修改时候自动补齐五个： 并且只能输入16进制
  methods: {
    async huoqudoor() {
      try {
        const huoquzhi = {
          cmd: 'get_door_sensor_settings'
        }
        const resdoor = await this.Api.Statuto(huoquzhi)
        console.log(resdoor)
        this.doorsener = resdoor.data.mac_addr.replace(/:/g, '')
      } catch {
      }
    },
    async huoquyaokong() {
      try {
        const huoquyqk = {
          cmd: 'get_remote_control_setting'
        }
        const resyqk = await this.Api.Statuto(huoquyqk)
        this.yaokongqi = resyqk.data.mac_addr.replace(/:/g, '')
        console.log(resyqk)
      } catch {
      }
    },
    kongge() {
      if (this.doorsener === '') {
        this.falge = false
      }
    },
    zhengzejyykq() {
      const reg = /^[0-9a-fA-F]{12}$|^$/
      if (!reg.test(this.yaokongqi)) {
        this.falge1 = true
      } else {
        this.falge1 = false
      }
    },
    zhengzejy() {
      const reg = /^[0-9a-fA-F]{12}$|^$/
      if (!reg.test(this.doorsener)) {
        this.falge = true
      } else {
        this.falge = false
      }
    },
    async tijiaoykq() {
      if (this.falge1) {
        this.$Toast({
          message: this.$t('tsytongyong.qanxuqiushuru'),
          position: 'top'
        })
        return
      }
      let newStr1 = this.yaokongqi.slice(0, 2) + ':' + this.yaokongqi.slice(2, 4) + ':' + this.yaokongqi.slice(4, 6) + ':' + this.yaokongqi.slice(6, 8) + ':' + this.yaokongqi.slice(8, 10) + ':' + this.yaokongqi.slice(10, 12)
      if (this.yaokongqi === '') {
        newStr1 = null
        this.peiduizhong = this.$t('nenbanben.jiebangzhongykq')
      } else {
        this.peiduizhong = this.$t('nenbanben.yaokongqipeiduizhong')
      }
      console.log(newStr1)
      this.showyaokongqi = true
      try {
        const updatamcu = {
          cmd: 'update_remote_control_setting',
          mac_addr: newStr1
        }
        const resupdatamcu = await this.Api.Statuto(updatamcu)
        this.showyaokongqi = false
        if (newStr1 === null) {
          this.$Dialog.alert({
            message: this.$t('nenbanben.jbcgykq'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
        } else {
          this.$Dialog.alert({
            message: this.$t('nenbanben.xiugaiykqcg'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
          localStorage.removeItem('token')
        }
        console.log(resupdatamcu)
      } catch (err) {
        console.log(err)
        this.showyaokongqi = false
        if (err.request.status === 403) {
          this.$Dialog.alert({
            title: this.$t('nenbanben.xiugaiykqsb'),
            message: this.$t('tsytongyong.zzzxxsrw'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
        } else if (err.request.status === 497) {
          this.$Dialog.alert({
            title: this.$t('nenbanben.xiugaiykqsb'),
            message: this.$t('nenbanben.jqrybdykqqxjbjqr'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
        } else {
          this.$Dialog.alert({
            message: this.$t('nenbanben.xiugaiykqsb'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
        }
      }
    },
    async tijiao() {
      if (this.falge) {
        this.$Toast({
          message: this.$t('tsytongyong.qanxuqiushuru'),
          position: 'top'
        })
        return
      }
      let newStr = this.doorsener.slice(0, 2) + ':' + this.doorsener.slice(2, 4) + ':' + this.doorsener.slice(4, 6) + ':' + this.doorsener.slice(6, 8) + ':' + this.doorsener.slice(8, 10) + ':' + this.doorsener.slice(10, 12)
      if (this.doorsener === '') {
        newStr = null
      }
      console.log(newStr)
      try {
        const updatamcu = {
          cmd: 'update_door_sensor_settings',
          mac_addr: newStr
        }
        const resupdatamcu = await this.Api.Statuto(updatamcu)
        this.$Dialog.alert({
          message: this.$t('shezhiye.xiugaiMACcg'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
        localStorage.removeItem('token')
        console.log(resupdatamcu)
      } catch (err) {
        console.log(err)
        if (err.request.status === 403) {
          this.$Dialog.alert({
            title: this.$t('shezhiye.xiugaiMACsb'),
            message: this.$t('tsytongyong.zzzxxsrw'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
        } else {
          this.$Dialog.alert({
            message: this.$t('shezhiye.xiugaiMACsb'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
        }
      }
    }
  }
}
</script>
<style lang="less">
.bluetoonth{
    height: 5.72rem;
    margin-top: 0.14rem;
    background: #ffffff;
    .yincangceng{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 99;
    >div{
        background: #ffffff;
        max-width: 1.65rem;
        line-height: 0.3rem;
        margin: 2.5rem auto;
        text-align: center;
    }
}
    input{
      width: 1.6rem;
      margin-right: 0.1rem;
    }
    .binju2{
      margin: 0 0.23rem;
      padding: 0.1rem 0;
      display: flex;
      justify-content: space-between;
      button{
        width: 0.96rem;
        height: 0.32rem;
        background: linear-gradient(180deg, #0BE7E5 0%, #099FA5 100%);
        box-shadow: 0px 15px 45px 0px rgba(85, 155, 158, 0.15);
        border-radius: 4px;
        color: #FFFFFF;
        border: none;
      }
    }
    .binju1{
font-weight: 600;
color: rgba(0, 0, 0, 0.85);
line-height: 0.22rem;
padding-top: 0.19rem;
margin: 0  0.2rem;
// word-wrap: break-word;
  text-align:justify;
             text-justify:inter-ideograph;
    }
    .pandingceng{
      margin: 0 0.23rem;
      color:red;
    }
}
</style>
