<template>
    <div class="video">
        <div class="videotop">
           <img src="../../../assets/iconbgguanli.png" alt=""> {{restaskxq.task_name}}
        </div>
        <div class="luxiangzongrw">{{$t('baogaoxqing.rwlx')}} {{lishilxlist.total_cnt}} {{$t('baogaogl.fen')}}</div>
        <div class="videocenter">
            <div class="videosonsp" v-for="ele in lishilxlist.list" :key="ele">
              <video controls height="80%" width="100%">
              <source :src="`/non_api/task/video?id=${ele}&App-Token=${token}`" type="video/mp4"><source>
              </video>
            <!-- <video controls height="80%" width="100%" preload="none"></video> -->
            <!-- <video src="../../../assets/22.mp4" controls height="80%" width="100%" preload="none"></video> -->
            <div><span>{{$t('baogaoxqing.luxiang')}}:{{restaskxq.task_name}}-{{ele}}</span> <span @click="xiazailuxiang2(ele)"><img src="../../../assets/xiangqingxiazai.png" alt=""></span></div>
          </div>
        </div>
        <div class="mantbottom"><button @click="currentmatjian">{{$t('baogaogl.shangyiye')}}</button> <div><span>{{currentsmath}}</span><span>/{{maxcurrent}}</span></div><button @click="currentmatjia">{{$t('baogaogl.xiayiye')}}</button></div>
    </div>
</template>
<script>
export default {
  props: ['restaskxq'],
  data: () => ({
    currentsmath: 1,
    maxcurrent: 1,
    luxiangid: '',
    lishilxlist: '',
    token: localStorage.getItem('token')
  }),
  created() {
    this.luxiangid = this.$route.params.taskid
    console.log(this.luxiangid)
    this.lxlb()
  },
  methods: {
    // 点击视频以后 交给浏览器处理
    xiazailuxiang2(id) {
      console.log(`${window.location.host}/non_api/task/video?id=${id}&App-Token=${localStorage.getItem('token')}`)
      window.location.href = `/non_api/task/video?id=${id}&App-Token=${localStorage.getItem('token')}`
      // window.open(`/non_api/task/video?id=${id}&App-Token=${localStorage.getItem('token')}`)
    },
    xiazailuxiang(id) {
      console.log(id)
      this.Api.getshipinxz(id, localStorage.getItem('token'), {
        responseType: 'blob'
      }).then(response => {
        const blob = new Blob([response.data], {
          type: response.headers['content-type']
          // fileName: appi2[1]
        })
        const url = window.URL.createObjectURL(blob)
        console.log(url)
        window.location.href = url
      }).catch(error => { this.$message.error(error) })
      // try {
      //   const resvideo = await this.Api.getshipinxz(id, localStorage.getItem('token'))
      //   console.log(resvideo)
      // } catch {
      // }
    },
    async lxlb() {
      try {
        const lxidreq = {
          cmd: 'get_video_list',
          task_id: Number(this.luxiangid),
          page_size: 4,
          page_index: (this.currentsmath - 1) < 0 ? 0 : this.currentsmath - 1
        }
        const reslxlb = await this.Api.gethistoryte(lxidreq)
        this.lishilxlist = reslxlb.data
        this.maxcurrent = reslxlb.data.page_cnt
        if (this.maxcurrent === 0) {
          this.currentsmath = 0
        }
        console.log(reslxlb)
      } catch {
      }
    },
    currentmatjian() {
      if (this.maxcurrent === 0) {
        this.currentsmath = 0
        return
      }
      if (this.currentsmath <= 1) {
        this.currentsmath = 1
        return
      }
      this.currentsmath--
      this.lxlb()
    },
    currentmatjia() {
      if (this.currentsmath >= this.maxcurrent) {
        this.currentsmath = this.maxcurrent
        console.log(this.currentsmath)
        return
      }
      this.currentsmath++
      this.lxlb()
    }
  }
}
</script>
<style lang="less" scoped>
.video{
    width: 3.28rem;
    height: 5.64rem;
    background: #ffffff;
    margin-left: 0.24rem;
    margin-top: 0.13rem;
    .luxiangzongrw{
      font-size: 0.12rem;
           font-family: PingFangSC-Regular, PingFang SC;
           font-weight: 400;
           color: rgba(0, 0, 0, 0.65);
           margin-bottom: 0.15rem;
           margin-left: 0.12rem;
    }
    .videotop{
           font-size: 0.15rem;
           font-family: PingFangSC-Medium, PingFang SC;
           font-weight: 500;
           color: rgba(0, 0, 0, 0.85);
           line-height: 0.48rem;
        img{
            height: 0.48rem;
            width: 0.48rem;
            vertical-align: middle;
        }
    }
    .videocenter{
        display: flex;
         flex-wrap:wrap;
         justify-content: space-around;
        .videosonsp{
            width: 1.44rem;
            height: 1.8rem;
            padding-bottom: 0.15rem;
            >div{
              margin-top: 0.08rem;
              font-size: 0.12rem;
              font-family: PingFangSC-Regular, PingFang SC;
              font-weight: 400;
              color: rgba(0, 0, 0, 0.85);
              line-height: 0.2rem;
                img{
                width: 0.18rem;
                height: 0.18rem;
                vertical-align: middle;
              }
            }
        }
    }
   .mantbottom{
      display: flex;
      position: fixed;
      bottom: 0.21rem;
      justify-content:space-between;
      width: 87%;
      button{
        height: 0.24rem;
        border: 0.01rem solid #17A69F;
        background: #ffffff;
        color: #17A69F;
        border-radius: 0.05rem;
      }
      button:nth-child(1){
        margin-left: 0.3rem;
      }
      button:nth-child(3){
        margin-right: 0.3rem;
      }
      div{
          height: 0.24rem;
          font-size: 0.14rem;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          line-height: 0.24rem;
          margin: 0 0.24rem;
          span:nth-child(1){
            color: #17A69F;
          }
      }
    }
}
</style>
