<template>
    <div class="zzxs">
    <div class="contents2one">
        <span>{{falge2?'0':''}}{{ss}}:{{falge1?'0':''}}{{ff}}:{{mm}}</span>
      </div>
      <div class="contents2two">
     <ul>
       <li><img src="../../../../../assets/Icon／MAP.png" alt=""></li>
       <li>{{$t('homes.fangjian')}}</li>
       <li><div class="van-ellipsis">{{mapname?mapname:$t('homes.weizhi')}}</div></li>
     </ul>
      <ul>
       <li><img src="../../../../../assets/Icon／State.png" alt=""></li>
       <li>{{$t('homes.zhuangtai')}}</li>
       <li>{{$t('homes.ztzhong')}}</li>
     </ul>
      <ul>
       <li><img src="../../../../../assets/Icon／Mode.png" alt=""></li>
       <li>{{$t('homes.moshi')}}</li>
       <li v-if="modes==='LOW'">{{$t('homes.diglmoshi')}}</li>
       <li v-else-if="modes==='FULL'">{{$t('homes.quanglmoshi')}}</li>
     </ul>
      </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    mm: '00',
    ff: 0,
    ss: 0,
    falge1: true,
    falge2: true,
    dur: '',
    timers: '',
    mapname: ''
  }),
  props: ['modes'],
  inject: ['reload'],
  created() {
    this.gettime()
    this.nopan()
  },
  methods: {
    nopan() {
      this.timers = setInterval(async() => {
        const pamsrams = {
          cmd: 'get_task_state'
        }
        try {
          // 原来和消杀详情页一样 如果是暂停转态把数据返回出去定时器 并且一秒刷新一次根据状态码判定
          const resmin = await this.Api.getotsstate(pamsrams)
          console.log(resmin)
          if (resmin.data.status === 'PAUSE') {
            localStorage.setItem('shifoujxrw', false)
            this.mm = ((this.dur * 60 - resmin.data.disinfect_dur) % 60)
            if (this.mm < 10) {
              this.mm = '0' + this.mm
            }
            this.ff = Math.floor(((this.dur * 60 - resmin.data.disinfect_dur) / 60) % 60)
            if (this.ff > 9) {
              this.falge1 = false
            }
            this.ss = Math.floor((this.dur * 60 - resmin.data.disinfect_dur) / 3600)
            if (this.ss > 9) {
              this.falge2 = false
            }
          } else if (resmin.data.status === 'PAUSE') {
            clearInterval(this.timers)
            this.reload()
          } else if (resmin.data.status === 'RESUME_COUNT_DOWN' || resmin.data.status === 'START_COUNT_DOWN') {
            clearInterval(this.timers)
            this.reload()
          }
        } catch (err) {
          if (err.request.status === 404) {
            clearInterval(this.timers)
            this.reload()
          }
        }
      }, 1000)
    },
    async gettime() {
      const gettask = {
        cmd: 'get_task_config'
      }
      try {
        const restime = await this.Api.getotsstate(gettask)
        console.log(restime.data.dur)
        this.dur = restime.data.dur
        this.mapname = restime.data.map_name
      } catch {
      }
    }
  },
  beforeDestroy() {
    console.log(111, this.timers)
    if (this.timers) {
      console.log(222)
      clearInterval(this.timers)
    }
    clearInterval(this.timers)
  }
}
</script>

<style lang="less" scoped>
     .contents2one{
                width: 1.92rem;
