              //  background-size:cover;
              background-size:1.92rem 1.18rem;
               span{
                 display: block;
                 margin-top: 0.72rem;
                 text-align: center;
                 font-size: 0.25rem;
                 font-family: DINCondensed-Bold, DINCondensed;
                 font-weight: bold;
                 color: rgba(0, 0, 0, 0.85);
                 line-height: 0.3rem;
                 height: 0.3rem;
               }
              }
              .contents2two{
                display: flex;
                justify-content: space-around;
                font-size: 0.12rem;
                ul{
                  width: 0.72rem;
                  height:0.8rem;
                  background: #E6FFFB;
                  li{
                  margin-left: 0.08rem;
                  }
                  li:nth-child(2){
                   font-size: 0.12rem;
                   font-family: PingFangSC-Regular, PingFang SC;
                   font-weight: 400;
                   color: rgba(0, 0, 0, 0.45);
                   line-height: 0.2rem;
                  }
                  li:nth-child(3){
                     font-size: 0.1rem;
