<script>
export default {
  data: () => ({
    tyepe: 'B',
    currentRate: 0,
    falgeclo: false,
    colorjbs: {
      '0%': '#FFA970',
      '100%': '#FF090A'
    },
    gradientColor: {
      '0%': '#3fecff',
      '100%': '#6149f6'
    },
    moredopowd: '',
    hh: '',
    ff: '',
    mm: '',
    sjhh: '',
    sjff: '',
    sjmm: '',
    fordurtske: '',
    sshh: '',
    ssff: '',
    ssmm: ''
  }),
  props: ['recordstatus'],
  created() {
    // 此页面终止页面把数据渲染上去
    // this.moredopowd = localStorage.getItem('modulepowd')
    console.log(this.modeseflow)
    this.gettime(this.$props.recordstatus.id)
  },
  methods: {
    async gettime(id) {
      const gettask = {
        cmd: 'get_details',
        task_id: Number(id),
        get_point_logs: true
      }
      try {
        const restime = await this.Api.gethistoryte(gettask)
        console.log(restime.data.point_records[0].disinfect_dur_config)
        this.fordurtske = ((restime.data.point_records[0].disinfect_dur_config - this.$props.recordstatus.disinfect_dur) / (restime.data.point_records[0].disinfect_dur_config)) * 100
        console.log(this.fordurtske)
        this.hh = Math.floor(restime.data.point_records[0].disinfect_dur_config / 3600)
        if (this.hh < 10) {
          this.hh = '0' + this.hh
        }
        this.ff = Math.floor(restime.data.point_records[0].disinfect_dur_config / 60 % 60)
        if (this.ff < 10) {
          this.ff = '0' + this.ff
        }
        this.mm = Math.floor(restime.data.point_records[0].disinfect_dur_config % 60)
        if (this.mm < 10) {
          this.mm = '0' + this.mm
        }
        this.sjhh = Math.floor((this.$props.recordstatus.disinfect_dur / 3600))
        if (this.sjhh < 10) {
          this.sjhh = '0' + this.sjhh
        }
        this.sjff = Math.floor((this.$props.recordstatus.disinfect_dur) / 60) % 60
        if (this.sjff < 10) {
          this.sjff = '0' + this.sjff
        }
        this.sjmm = Math.floor((this.$props.recordstatus.disinfect_dur % 60))
        if (this.sjmm < 10) {
          this.sjmm = '0' + this.sjmm
        }
        this.sshh = Math.floor((restime.data.point_records[0].disinfect_dur_config - this.$props.recordstatus.disinfect_dur) / 3600)
        if (this.sshh < 10) {
          this.sshh = '0' + this.sshh
        }
        this.ssff = Math.floor((restime.data.point_records[0].disinfect_dur_config - this.$props.recordstatus.disinfect_dur) / 60) % 60
        if (this.ssff < 10) {
          this.ssff = '0' + this.ssff
        }
        this.ssmm = Math.floor((restime.data.point_records[0].disinfect_dur_config - this.$props.recordstatus.disinfect_dur)) % 60
        if (this.ssmm < 10) {
          this.ssmm = '0' + this.ssmm
        }
      } catch {
      }
    }
  }
}
</script>
