<script>
export default {
  data: () => ({
    flge: false,
    menjinpai: '',
    checked3: true,
    dur: '',
    power_mode: '',
    numbjj: 0,
    arr: [3, 5, 10, 15, 30, 60],
    span01: '',
    safety_auto_resume: '',
    roomno: '',
    operator: '',
    paizhao: false,
    pirhf: true,
    roomflge: false,
    operatoflage: false,
    menjinpai1: '',
    pir1: false,
    renlian1: '',
    noenlist: false,
    pirtimejia: 0
  }),
  created() {
    // this.getotsstatus()
    // if (this.power_mode === '') {
    //   this.power_mode = 'LOW'
    // }
    // this.getshezhimjpiral()
  },
  methods: {
    pirtimefn() {
      this.pirtimejia++
    },
    zhankai() {
      this.noenlist = !this.noenlist
    },
    zhixinghanshu(obj) {
      console.log(obj)
      if (obj === null) {
        this.getotsstatus()
        return
      }
      if (obj.taskid !== '') {
        this.getchongzuostatus(obj)
      } else {
        this.getotsstatus()
      }
    },
    operatonome() {
      // const reg = /^\S{1,1}[\s\S]{1,31}$/
      // if (!reg.test(this.operator)) {
      //   this.operatoflage = true
      // } else {
      //   this.operatoflage = false
      // }
      // console.log(this.operator)
    },
    roomone() {
      // const reg = /^\S{1,1}[\s\S]{1,31}$/
      // if (!reg.test(this.roomno)) {
      //   this.roomflge = true
      // } else {
      //   this.roomflge = false
      //  }
      // console.log(this.roomno)
    },
    foflge() {
      this.flge = false
    },
    // 点击减1 并且不小于1
    numbjian() {
      console.log(this.arr.indexOf(this.dur))
      if (this.arr.indexOf(this.dur)) {
        const index = this.arr.indexOf(this.dur)
        this.dur = this.arr[index - 1]
      }
      if (this.dur === this.arr[0]) {
        this.dur = this.arr[0]
      }
    },
    // 加1
    numbjia() {
      if (this.arr.indexOf(this.dur) >= 0 && this.arr[this.arr.length - 1]) {
        const index = this.arr.indexOf(this.dur)
        this.dur = this.arr[index + 1]
      }
      if (this.arr.indexOf(this.dur) === -1) {
        console.log(1111, this.arr[5])
        this.dur = this.arr[5]
      }
      // if (this.arr.indexOf(this.dur)) {
      //   const index = this.arr.indexOf(this.dur)
      //   this.dur = this.arr[index + 1]
      // } else if (this.dur === 60) {
      // }
    },
    // 模式默认为低功率模式 点击对应切换 并且点击后隐藏 类似于下拉菜单的做法
    estabt1() {
      this.span01 = this.$t('xiaosharw.diglmoshi')
      this.power_mode = 'LOW'
      this.flge = false
      // this.$refs.span01.textContent = this.$refs.estabotop2.textContent
      // if (this.$refs.span01.textContent === '低功率模式') {
      //   this.power_mode = 'LOW'
      // } else {
      //   this.power_mode = 'FULL'
      // }
      // console.log(this.power_mode)
      this.$refs.estabotop2.style.display = 'none'
      this.$refs.estabotop3.style.display = 'none'
    },
    estabt2() {
      this.span01 = this.$t('xiaosharw.quanglmoshi')
      this.power_mode = 'FULL'
      this.flge = false
      // this.$refs.span01.textContent = this.$refs.estabotop3.textContent
      // if (this.$refs.span01.textContent === '全功率模式') {
      //   this.power_mode = 'FULL'
      // } else {
      //   this.power_mode = 'LOW'
      // }
      console.log(this.power_mode)
      this.$refs.estabotop2.style.display = 'none'
      this.$refs.estabotop3.style.display = 'none'
    },
    // 如果是重做任务获取他的mapid 和消杀任务的模式
    async getchongzuostatus(obj) {
      const getchongzuostatus = {
        cmd: 'get_redo_task_config',
        task_id: obj.taskid
      }
      try {
        const resload = await this.Api.getotsstate(getchongzuostatus)
        console.log(resload)
        this.dur = resload.data.dur ? resload.data.dur : 5
        this.power_mode = resload.data.power_mode
        this.safety_auto_resume = resload.data.safety_auto_resume
        this.roomno = resload.data.map_name
        this.paizhao = resload.data.face_detect_enabled
        // this.paizhao = false
        this.pirhf = resload.data.pir_enabled
        this.menjinpai = resload.data.door_sensor_enabled
        this.getshezhimjpiral()
        this.operator = 'admin'
        console.log(resload.data, '保存的配置')
        if (this.power_mode === 'LOW') {
          this.span01 = this.$t('xiaosharw.diglmoshi')
        } else if (this.power_mode === 'FULL') {
          this.span01 = this.$t('xiaosharw.quanglmoshi')
        }
      } catch (err) {
        console.log(err)
        // 如果404就是没有保存的消杀策略自己给默认配置自己定义
        if (err.response.status === 404) {
          this.dur = 5
          this.power_mode = 'FULL'
          this.span01 = this.$t('xiaosharw.quanglmoshi')
          this.safety_auto_resume = false
          this.paizhao = false
          this.pirhf = true
          this.menjinpai = true
          this.getshezhimjpiral()
        }
      }
    },
    // 进来获取后台保存的配置文件
    async getotsstatus() {
      const getloadtask = {
        cmd: 'load_task_config'
      }
      try {
        const resload = await this.Api.getotsstate(getloadtask)
        this.dur = resload.data.dur
        this.power_mode = resload.data.power_mode
        this.safety_auto_resume = resload.data.safety_auto_resume
        this.roomno = resload.data.map_name
        this.paizhao = resload.data.face_detect_enabled
        // this.paizhao = false
        this.pirhf = resload.data.pir_enabled
        this.menjinpai = resload.data.door_sensor_enabled
        this.getshezhimjpiral()
        console.log(resload.data, '保存的配置')
        if (this.power_mode === 'LOW') {
          this.span01 = this.$t('xiaosharw.diglmoshi')
        } else if (this.power_mode === 'FULL') {
          this.span01 = this.$t('xiaosharw.quanglmoshi')
        }
      } catch (err) {
        // 如果404就是没有保存的消杀策略自己给默认配置自己定义
        if (err.response.status === 404) {
          this.dur = 5
          this.power_mode = 'FULL'
          this.span01 = this.$t('xiaosharw.quanglmoshi')
          this.safety_auto_resume = false
          this.paizhao = false
          this.pirhf = true
          this.menjinpai = true
          this.getshezhimjpiral()
        }
      }
    },
    async getshezhimjpiral() {
      const pamaros = {
        cmd: '',
        requests: [{
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            door_sensor_enabled: true
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            pir_enabled: true
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            face_detect_enabled: true
          }
        }]
      }
      try {
        // 用api聚合请求机器人名字 版本号还有生命状态
        const res = await this.Api.Apiaggr(pamaros)
        console.log(res.data)
        this.menjinpai1 = res.data.responses[0].msg.door_sensor_enabled
        if (!(this.menjinpai1)) {
          this.menjinpai = false
        }
        // this.pir1 = res.data.responses[1].msg.pir_enabled
        // if (!(this.pir1)) {
        //   this.pirhf = false
        // }
        this.renlian1 = res.data.responses[2].msg.face_detect_enabled
        if (!(this.renlian1)) {
          this.paizhao = false
        }
      } catch {
      }
    }
  }
}
</script>
