<script>
export default {
  data: () => ({
    time: '',
    taskname: '',
    nomone: false,
    dur: '',
    mm: '00',
    ff: 0,
    ss: 0,
    falge1: true,
    falge2: true,
    timerrms: null,
    currentRate: 0,
    falgeclo: false,
    modes: '',
    mapname: '',
    colorjbs: {
      '0%': '#23FAFF',
      '100%': '#04D2D7'
    },
    // durtrome: '',
    forduer: '',
    lauges: localStorage.getItem('languageSet'),
    zzhh: '00',
    zzff: '00',
    zzmm: '00',
    sjhh: '00',
    sjff: '00',
    sjmm: '00'
  }),
  inject: ['reload'],
  created() {
    // 页面加载时候获取消杀时长渲染此页面为正在进行页面获取正在进行状态码 并且设置定时器 获取后台提供的秒数自己转化成计时器
    // 如果为其他状态码局部刷新此路由组件 并且重新走流程就是主页面判定
    this.gettime()
    this.timerrms = setInterval(async() => {
      const pamsrams = {
        cmd: 'get_task_state'
      }
      try {
        const resmin = await this.Api.getotsstate(pamsrams)
        console.log(resmin)
        this.taskname = resmin.data.task_name
        if (resmin.data.status === 'DISINFECT') {
          localStorage.setItem('shifoujxrw', true)
          this.mm = ((this.dur * 60 - resmin.data.disinfect_dur) % 60)
          // this.durtrome = resmin.data.disinfect_dur
          this.forduer = ((this.dur * 60 - resmin.data.disinfect_dur) / (this.dur * 60)) * 100
          if (this.mm < 10) {
            this.mm = '0' + this.mm
          }
          this.ff = Math.floor(((this.dur * 60 - resmin.data.disinfect_dur) / 60) % 60)
          console.log(this.ff, this.mm)
          if (this.ff > 9) {
            this.falge1 = false
          }
          this.ss = Math.floor((this.dur * 60 - resmin.data.disinfect_dur) / 3600)
          if (this.ss > 9) {
            this.falge2 = false
          }
          this.sjhh = Math.floor((resmin.data.disinfect_dur / 3600))
          if (this.sjhh < 10) {
            this.sjhh = '0' + this.sjhh
          }
          this.sjff = Math.floor((resmin.data.disinfect_dur / 60)) % 60
          if (this.sjff < 10) {
            this.sjff = '0' + this.sjff
          }
          this.sjmm = resmin.data.disinfect_dur % 60
          if (this.sjmm < 10) {
            this.sjmm = '0' + this.sjmm
          }
        } else if (resmin.data.status === 'PAUSE') {
          clearInterval(this.timerrms)
          this.timerrms = null
          this.reload()
        } else if (resmin.data.status === 'RESUME_COUNT_DOWN' || resmin.data.status === 'START_COUNT_DOWN') {
          clearInterval(this.timerrms)
          this.timerrms = null
          this.reload()
        }
      } catch (err) {
        if (err.request.status === 404) {
          clearInterval(this.timerrms)
          this.timerrms = null
          this.reload()
        }
      }
    }, 1000)
  },
  methods: {
    async gettime() {
      const gettask = {
        cmd: 'get_task_config'
      }
      try {
        const restime = await this.Api.getotsstate(gettask)
        console.log(restime)
        this.modes = restime.data.power_mode
        this.dur = restime.data.dur
        this.zzhh = Math.floor((restime.data.dur * 60 / 3600))
        if (this.zzhh < 9) {
          this.zzhh = '0' + this.zzhh
        }
        this.zzff = Math.floor((restime.data.dur % 60))
        if (this.zzff < 9) {
          this.zzff = '0' + this.zzff
        }
        this.zzmm = Math.floor((restime.data.dur * 60 % 60))
        if (this.zzmm < 9) {
          this.zzmm = '0' + this.zzmm
        }
        this.mapname = restime.data.map_name
      } catch {
      }
    },
    async getstop() {
      const stopcmd = {
        cmd: 'pause_task'
      }
      try {
        const resstop = await this.Api.getotsstate(stopcmd)
        console.log(resstop)
      } catch {
      }
    },
    aborttask() {
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.xiaoshazhongzhi'),
        message: this.$t('tsytongyong.shifouzhongzhixs'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
        // 如果确认退出以后发送请求删除后台Token并且本地Token也删除
        // 延时500毫秒
      }).then(() => {
        const abortask = {
          cmd: 'abort_task'
        }
        this.Api.getotsstate(abortask).then((res) => {
          console.log(res)
        })
        // this.reload()
      })
    }
  },
  beforeDestroy() {
    clearInterval(this.timerrms)
  }
}
</script>
