<template>
    <div class="safety">
        <div class="safetytop">
          <div class="safenoe1">{{$t('shezhiye.xiangying')}}</div>
          <!-- <div class="safenoe2"><span>响应遥控器</span> <van-switch v-model="responrbt.remote_ctrl_enabled" active-color="#07c160" inactive-color="#a0a0a0" @change="enable1" disabled /></div>
          <div class="safedix"></div>
          <div class="safenoe3"><span>响应警示牌</span><van-switch v-model="responrbt.door_sensor_enabled" active-color="#07c160" inactive-color="#a0a0a0" @change="enable2" disabled /></div>
          <div class="safedix"></div> -->
          <div class="safenoe2"><span>{{$t('shezhiye.xiangyingykq')}}</span> <van-switch v-model="check1" active-color="#07c160" inactive-color="#a0a0a0" @change="enable1" disabled /></div>
          <div class="safedix"></div>
          <!-- <div class="safenoe2"><van-slider v-model="value" @change="onChange" /></div> -->
          <!-- <div class="safenoe6"><span>{{$t('shezhiye.xiangyingpir')}}</span><van-switch v-model="pir" active-color="#07c160" inactive-color="#a0a0a0"  @change="enable3"/></div>
          <div class="safedix"></div> -->
          <div class="safenoe2"><span>{{$t('shezhiye.xyshextou')}}</span> <van-switch v-model="renlian" active-color="#07c160" inactive-color="#a0a0a0" @change="enable4"  /></div>
          <div class="safedix"></div>
          <div class="safenoe3"><span>{{$t('shezhiye.xiangyingjsp')}}</span><van-switch v-model="menjinpai" active-color="#07c160" inactive-color="#a0a0a0" @change="enable2"  /></div>
          <div class="safedix"></div>
          <div class="safenoe4" v-show="menjinpai">{{$t('shezhiye.mjplmd')}}</div>
           <div class="safeonemenjin" v-show="menjinpai"><div class="slider2"><van-slider active-color="#36CFC9" bar-height="0.08rem" min="1"  step="1" max="5"  v-model="mjpvalue" @change="menjinpaio" /></div><div class="slider3">{{mjpvalue}}</div></div>
          <div class="safedix" v-show="menjinpai"></div>
           <div class="safenoe4">{{$t('shezhiye.xsjgbobao')}}</div>
          <div class="safenoe5"><span>{{disreportval}}{{$t('shezhiye.miao')}}</span><span  @click="bobaoxiugai">{{$t('shezhiye.genggai')}} <img src="../../../assets/youbianjiao2.png" alt=""></span></div>
          <div class="safedix"></div>
          <div class="safenoe4">{{$t('shezhiye.rqzdhfsj')}}</div>
          <div class="safenoe5"><span>{{rqzdhfsj}}{{$t('shezhiye.miao')}}</span><span  @click="huifushezhi">{{$t('shezhiye.genggai')}} <img src="../../../assets/youbianjiao2.png" alt=""></span></div>
          <div class="safedix"></div>
          <div class="safenoe4">{{$t('shezhiye.lichangjishu')}}</div>
          <div class="safenoe5"><span>{{coutown}}{{$t('shezhiye.miao')}}</span><span  @click="lichangshezhi">{{$t('shezhiye.genggai')}} <img src="../../../assets/youbianjiao2.png" alt=""></span></div>
          <div class="safedix"></div>
          <div class="safenoe4">{{$t('user.mima')}}</div>
          <div class="safenoe5"><span>{{$t('shezhiye.mimashezhi')}}</span><span @click="safeloc">{{$t('shezhiye.genggai')}} <img src="../../../assets/youbianjiao2.png" alt=""></span></div>
          <div class="safedix"></div>
        <!-- <p v-for="item in items" :key="item.id">{{item.message}}</p>
        <button class="btn" @click="handClick()">更改数据</button> -->
        </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    check1: true,
    menjinpai: '',
    pir: '',
    renlian: '',
    disreportval: '',
    value: 1,
    mjpvalue: 1,
    rqzdhfsj: '',
    coutown: '',
    shibaivalue: null
    // items: [
    //   { message: 'noe', id: 1 },
    //   { message: 'two', id: 2 },
    //   { message: 'three', id: 3 }
    // ]
  }),
  // 接受父组件传过来的值 不过已经用其他东西替代了
  props: ['responrbt'],
  created() {
    // this.check1 = this.responrbt.remote_ctrl_enabled
    // this.check2 = this.responrbt.door_sensor_enabled
    // this.bbjg()
    this.apiaggrs()
    this.volumeea()
  },
  methods: {
    // 离场计数时间传值过去对应为默认值
    lichangshezhi() {
      this.$emit('lichangshezhi', this.coutown)
    },
    async menjinpaio(value) {
      console.log(value, this.mjpvalue)
      const updategyd = {
        cmd: 'update_robot_settings',
        door_sensor_sensitivity: value
      }
      try {
        const resmgdcg = await this.Api.Statuto(updategyd)
        this.shibaivalue = value
        console.log(resmgdcg)
        this.$Toast({
          message: this.$t('shezhiye.xgmjplmdcg'),
          position: 'top'
        })
      } catch (err) {
        console.log(err)
        this.mjpvalue = this.shibaivalue
        this.$Toast({
          message: this.$t('shezhiye.xgmjplmdsb'),
          position: 'top'
        })
      }
    },
    // 入侵自动恢复时间传值过去对应为默认值
    huifushezhi() {
      this.$emit('hfrtszchuxian', this.rqzdhfsj)
    },
    // 获取四个后台给定的默认值 修改也是如此
    async volumeea() {
      const pamaros = {
        cmd: '',
        requests: [{
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            door_sensor_sensitivity: true
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            auto_resume_delay: true
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            disinfect_report_interval: true
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            count_down_dur: true
          }
        }]
      }
      try {
        const res = await this.Api.Apiaggr(pamaros)
        console.log(res)
        this.mjpvalue = res.data.responses[0].msg.door_sensor_sensitivity
        this.shibaivalue = res.data.responses[0].msg.door_sensor_sensitivity
        this.rqzdhfsj = res.data.responses[1].msg.auto_resume_delay
        this.disreportval = res.data.responses[2].msg.disinfect_report_interval
        this.coutown = res.data.responses[3].msg.count_down_dur
      } catch {
      }
    },
    async onChange() {
    },
    async bbjg() {
      try {
        const reqbbjg = {
          cmd: 'get_robot_settings',
          disinfect_report_interval: true
        }
        const resbbjgsj = await this.Api.Statuto(reqbbjg)
        console.log(resbbjgsj)
        this.disreportval = resbbjgsj.data.disinfect_report_interval
      } catch {
      }
    },
    // 播报间隔时间传值过去对应为默认值
    // 点击播报间隔 以及密码层出现这
    bobaoxiugai() {
      this.$emit('bobaojiange', this.disreportval)
    },
    // handClick() {
    //   // this.items[0] = { message: 'ceshi', id: 4 }
    //   // this.$set(this.items, 3, { message: '更改的值', id: 4 })
    // },
    safeloc() {
      this.$emit('safdialo', '')
    },
    async enable1(value) {
      console.log(111, value)
      try {
        const newvolume = {
          cmd: 'update_robot_settings',
          remote_ctrl_enabled: value
        }
        const newsvlue = await this.Api.Statuto(newvolume)
        console.log(newsvlue)
        this.$Toast({
          message: '修改遥控器成功',
          position: 'top'
        })
      } catch {
      }
    },
    async enable2(valuee) {
      console.log(111, valuee)
      try {
        const newvolume = {
          cmd: 'update_robot_settings',
          door_sensor_enabled: valuee
        }
        const newsvlue = await this.Api.Statuto(newvolume)
        console.log(newsvlue)
        this.$Toast({
          message: this.$t('xinzeng.jspcg'),
          position: 'top'
        })
      } catch {
        this.$Toast({
          message: this.$t('xinzeng.jspsb'),
          position: 'top'
        })
      }
    },
    async enable3(value) {
      try {
        const newvolume = {
          cmd: 'update_robot_settings',
          pir_enabled: value
        }
        const newsvlue = await this.Api.Statuto(newvolume)
        console.log(newsvlue)
        this.$Toast({
          message: this.$t('xinzeng.pircg'),
          position: 'top'
        })
      } catch {
        this.$Toast({
          message: this.$t('xinzeng.pirsb'),
          position: 'top'
        })
      }
    },
    async enable4(value) {
      try {
        const newvolume = {
          cmd: 'update_robot_settings',
          face_detect_enabled: value
        }
        const newsvlue = await this.Api.Statuto(newvolume)
        console.log(newsvlue)
        this.$Toast({
          message: this.$t('xinzeng.xgsxtcg'),
          position: 'top'
        })
      } catch {
        this.$Toast({
          message: this.$t('xinzeng.xgsxtsb'),
          position: 'top'
        })
      }
    },
    async apiaggrs() {
      const pamaros = {
        cmd: '',
        requests: [{
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            door_sensor_enabled: true
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            pir_enabled: true
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_settings',
            face_detect_enabled: true
          }
        }]
      }
      try {
        // 用api聚合请求机器人名字 版本号还有生命状态
        const res = await this.Api.Apiaggr(pamaros)
        console.log(res.data)
        this.menjinpai = res.data.responses[0].msg.door_sensor_enabled
        this.pir = res.data.responses[1].msg.pir_enabled
        this.renlian = res.data.responses[2].msg.face_detect_enabled
      } catch {
      }
    }
  },
  mounted() {
  }
}
</script>
