import axios from 'axios'
import Vue from 'vue'
import Vant from 'vant'
import router from '../router'
// 配置根路径
// axios.defaults.baseURL = 'http://192.168.77.66'
axios.defaults.baseURL = process.env.NODE_ENV === 'production' ? '' : '/ape'
axios.interceptors.request.use(config => {
  if (config.push === '/') {
  } else {
    if (localStorage.getItem('token')) {
      config.headers['App-token'] = localStorage.getItem('token') || ''
      // if (localStorage.getItem('actived') === 'false') {
      //   router.push('/wizardpage/' + localStorage.getItem('usenameID'))
      // }
    }
    // if (config.url === '/non_api/file_upload') {
    //   console.log(11)
    //   config.headers['Content-Type'] = 'multipart/form-data'
    // }
    // if (config.push === '/non_api/file_upload') {
    // console.log(11)
    // config.headers['Content-Type'] = 'multipart/form-data'
    // }
  }
  return config
},
error => {
  return Promise.reject(error)
}
)
