axios.interceptors.response.use(function (response) {
  // Do something with response data
  return response
}, function (error) {
  // 当token过期时，获取错误信息，并清除本地存储的过期token
  if (error.response.status === 401) {
    localStorage.removeItem('token')
    localStorage.removeItem('shifoujxrw')
    localStorage.removeItem('activeshezhiye')
    Vant.Toast({
      message: localStorage.getItem('lang') === 'zh_CN' && localStorage.getItem('languageSet') === 'zh' ? 'Token失效，请重新登录' : 'token invalid, return login page',
      // localStorage.getItem('languageSet') === 'zh' || !localStorage.getItem('languageSet') ? '尚未登录，请先登录' : 'Please login first'
      position: 'top'
    })
    setTimeout(function() {
      // window.location.href = 'http://192.168.77.12'
      router.push('/login')
    }, 2000)
    // window.location.href = 'http://localhost:8081/' // 跳转到登录组件
  }
  return Promise.reject(error)
})

