<script>
export default {
  data: () => ({
    time: '',
    taskname: '',
    nomone: false,
    dur: '',
    mm: '00',
    ff: 0,
    ss: 0,
    falge1: true,
    falge2: true,
    timers: null,
    currentRate: 0,
    falgeclo: false,
    modes: '',
    colorjbs: {
      '0%': '#23FAFF',
      '100%': '#04D2D7'
    },
    fordurtske: '',
    lauges: localStorage.getItem('languageSet'),
    mapname: '',
    sjhh: '00',
    sjff: '00',
    sjmm: '00',
    sjisj: '00',
    fjisj: '00',
    mjisj: '00'
  }),
  props: ['taskreason'],
  inject: ['reload'],
  created() {
    console.log(this.taskreason)
    // 页面加载时候获取消杀时长渲染此页面为暂停页面获取暂停状态码 并且设置定时器 获取后台提供的秒数自己转化成计时器
    // 如果为其他状态码局部刷新此路由组件 并且重新走流程就是主页面判定
    this.gettime()
    this.timers = setInterval(async() => {
      const pamsrams = {
        cmd: 'get_task_state'
      }
      try {
        const resmin = await this.Api.getotsstate(pamsrams)
        this.taskname = resmin.data.task_name
        if (resmin.data.status === 'PAUSE') {
          localStorage.setItem('shifoujxrw', false)
          this.mm = ((this.dur * 60 - resmin.data.disinfect_dur) % 60)
          this.sjisj = Math.floor(resmin.data.disinfect_dur / 3600)
          this.fjisj = Math.floor(resmin.data.disinfect_dur / 60) % 60
          this.mjisj = resmin.data.disinfect_dur % 60
          if (this.sjisj < 10) {
            this.sjisj = '0' + this.sjisj
          }
          if (this.fjisj < 10) {
            this.fjisj = '0' + this.fjisj
          }
          if (this.mjisj < 10) {
            this.mjisj = '0' + this.mjisj
          }
          this.fordurtske = ((this.dur * 60 - resmin.data.disinfect_dur) / (this.dur * 60)) * 100
          if (this.mm < 10) {
            this.mm = '0' + this.mm
          }
          this.ff = Math.floor(((this.dur * 60 - resmin.data.disinfect_dur) / 60) % 60)
          if (this.ff > 9) {
            this.falge1 = false
          }
          this.ss = Math.floor((this.dur * 60 - resmin.data.disinfect_dur) / 3600)
          if (this.ss > 9) {
            this.falge2 = false
          }
        } else if (resmin.data.status === 'DISINFECT') {
          clearInterval(this.timers)
          this.timers = null
          this.reload()
        } else if (resmin.data.status === 'RESUME_COUNT_DOWN' || resmin.data.status === 'START_COUNT_DOWN') {
          clearInterval(this.timers)
          this.timers = null
          this.reload()
        }
      } catch (err) {
        if (err.request.status === 404) {
          clearInterval(this.timers)
          this.timers = null
          console.log(this.timers)
          this.reload()
        }
      }
    }, 1000)
  },
  methods: {
    async gettime() {
      const gettask = {
        cmd: 'get_task_config'
      }
      try {
        const restime = await this.Api.getotsstate(gettask)
        console.log(restime.data)
        this.modes = restime.data.power_mode
        this.dur = restime.data.dur
        this.sjhh = Math.floor(this.dur / 60)
        this.sjff = this.dur % 60
        this.sjmm = (this.dur * 60) % 60
        if (this.sjhh < 10) {
          this.sjhh = '0' + this.sjhh
        }
        if (this.sjff < 10) {
          this.sjff = '0' + this.sjff
        }
        if (this.sjmm < 10) {
          this.sjmm = '0' + this.sjmm
        }
        this.mapname = restime.data.map_name
      } catch {
      }
    },
    async getresume() {
      const gettask = {
        cmd: 'resume_task'
      }
      try {
        const ressume = await this.Api.getotsstate(gettask)
        console.log(ressume)
      } catch (err) {
        if (err.request.status === 496) {
          this.$Toast({
            message: this.$t('nenbanben.mjpwljwfhfrw'),
            position: 'top'
          })
        }
      }
    },
    aborttask() {
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.xiaoshazhongzhi'),
        message: this.$t('tsytongyong.shifouzhongzhixs'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
        // 如果确认退出以后发送请求删除后台Token并且本地Token也删除
        // 延时500毫秒
      }).then(() => {
        const abortask = {
          cmd: 'abort_task'
        }
        this.Api.getotsstate(abortask).then((res) => {
          console.log(res)
        })
        // this.reload()
      })
    }
  },
  beforeDestroy() {
    clearInterval(this.timers)
  }
}
</script>
