<template>
    <div class="safedialogg" ref="yinones">
        <div class="box">
            <div class="boxtop"><span @click="yinnone">{{$t('shezhiye.quxiao')}}</span><span>{{$t('shezhiye.mimashezhi')}}</span>
            <span @click="alter">{{$t('shezhiye.genggai')}}</span></div>
            <div class="boxbotm">
             <div class="newwork"></div>
            <div class="passwords"><span>{{$t('shezhiye.yuanmima')}}</span><input type="password" v-model="textwork1"  @input="input1" maxlength="32" @blur="blur1" @focus="foucs1"></div>
             <div class="boxhavi" :class="{'boxhavi1':boxhavii1}"></div>
             <div class="passwords"><span>{{$t('shezhiye.xinmima')}}</span><input type="password" v-model="textwork2"  @input="input2" maxlength="32" @blur="blur2" @focus="foucs2"></div>
             <div class="boxhavi" :class="{'boxhavi1':boxhavii2}"></div>
             <div class="zhwork" v-show="flgetext1" ref="flgetexthtml1"></div>
             <div class="zhwork" v-show="flgetext2" ref="flgetexthtml2"></div>
             <div class="zhwork" v-show="flgetext3" ref="flgetexthtml3"></div>
             <div class="passwords"><span>{{$t('shezhiye.querenxmm')}}</span><input type="password" v-model="textwork3" @input="input3" maxlength="32" @blur="blur3" @focus="foucs3" class="inputthree"></div>
           <div class="boxhavi" :class="{'boxhavi1':boxhavii3}"></div>
           </div>
        </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    flgespan: true,
    textwork1: '',
    textwork2: '',
    textwork3: '',
    boxhavs1: false,
    flgetext1: false,
    flgetext2: false,
    flgetext3: false,
    boxhavii1: false,
    boxhavii2: false,
    boxhavii3: false
  }),
  methods: {
    spanbh() {
      this.flgespan = false
      this.boxhavs1 = true
    },
    yinnone() {
      this.$refs.yinones.style.display = 'none'
      this.textwork1 = ''
      this.textwork2 = ''
      this.textwork3 = ''
      this.flgetext1 = false
      this.flgetext2 = false
      this.flgetext3 = false
    },
    foucs1() {
      // 失去焦点 聚焦时候css的样式呈现
      this.boxhavii1 = true
    },
    foucs2() {
      this.boxhavii2 = true
    },
    foucs3() {
      this.boxhavii3 = true
    },
    blur1() {
      this.boxhavii1 = false
    },
    blur2() {
      this.boxhavii2 = false
    },
    blur3() {
      this.boxhavii3 = false
    },
    input1() {
      // 正则校验 原密码 新密码 确认新密码 给他三条提示
      const reg = /^\w{6,32}$/
      if (this.textwork1 === '') {
        this.flgetext1 = true
        this.$refs.flgetexthtml1.textContent = this.$t('tsytongyong.ymmbuweikong')
      } else if (!reg.test(this.textwork1)) {
        this.flgetext1 = true
        this.$refs.flgetexthtml1.textContent = this.$t('tsytongyong.ymmcdyw32')
      } else {
        this.flgetext1 = false
      }
    },
    input2() {
      const reg = /^\w{6,32}$/
      if (this.textwork2 === '') {
        this.flgetext2 = true
        this.$refs.flgetexthtml2.textContent = this.$t('tsytongyong.xmmbwk')
      } else if (!reg.test(this.textwork2)) {
        this.flgetext2 = true
        this.$refs.flgetexthtml2.textContent = this.$t('tsytongyong.xmmcdyw32')
      } else {
        this.flgetext2 = false
      }
    },
    input3() {
      const reg = /^\w{6,32}$/
      if (this.textwork3 === '') {
        this.flgetext3 = true
        this.$refs.flgetexthtml3.textContent = this.$t('tsytongyong.qrxmmbwk')
      } else if (!reg.test(this.textwork3)) {
        this.flgetext3 = true
        this.$refs.flgetexthtml3.textContent = this.$t('tsytongyong.qrxmmcdyw32')
      } else {
        this.flgetext3 = false
      }
    },
    async alter() {
      if (this.textwork1 === '' || this.textwork2 === '' || this.textwork3 === '') {
        this.$Toast({
          message: this.$t('tsytongyong.qbysrwk'),
          position: 'top'
        })
        return
      }
      // 当上面有一个输入为空时候都会提示 并且下面有一条正则存在也会提示
      if (this.flgetext1 || this.flgetext2 || this.flgetext3) {
        this.$Toast({
          message: this.$t('tsytongyong.qanxuqiushuru'),
          position: 'top'
        })
        return
      }
      // 当新密码 与确认新密码不一样时候 也会触发提示不会继续往下走
      if (!(this.textwork2 === this.textwork3)) {
        this.$Toast({
          message: this.$t('tsytongyong.xjmmbuyizhi'),
          position: 'top'
        })
        return
      }
      // 校验用户名以及原密码 如果原密码错误 不继续并且报错
      const verifypad = {
        cmd: 'verify_pwd',
        username: localStorage.getItem('usenameID'),
        pwd: this.textwork1
      }
      try {
        const res = await this.Api.getusername(verifypad)
        console.log(res)
        if (res.data.result) {
          const uptapwd = {
            cmd: 'update_pwd',
            new_pwd: this.textwork3
          }
          // 走到这一步的时候 如果新密码与原密码一直 则不继续走下去并且提示用户新旧密码一致无需修改
          if (verifypad.pwd === this.textwork3) {
            this.$Toast(this.$t('tsytongyong.xinjiumimayizhi'))
            return
          }
          try {
            const refsu = await this.Api.getusername(uptapwd)
            console.log(refsu)
            this.$dialog.alert({
              title: this.$t('tsytongyong.xgchenggong'),
              message: this.$t('tsytongyong.xcdlssx'),
              confirmButtonText: this.$t('xiaosharw.wozhidaole')
            })
            // 修改成功以后给一个模态确认框 并且 把数据全部清零并且隐藏
            // this.$Toast.success(this.$t('tsytongyong.xgcgxiacidlsx'))
            this.textwork1 = ''
            this.textwork2 = ''
            this.textwork3 = ''
            this.$refs.yinones.style.display = 'none'
          } catch {
            // 失败也一样
            // this.$Toast.fail(this.$t('tsytongyong.qingqiushibai'))
            this.$dialog.alert({
              title: this.$t('tsytongyong.shezhishibai'),
              message: this.$t('tsytongyong.qshchongshi'),
              confirmButtonText: this.$t('xiaosharw.wozhidaole')
            })
          }
        } else {
          this.$dialog.alert({
            title: this.$t('tsytongyong.shezhishibai'),
            message: this.$t('tsytongyong.ymmcw'),
            confirmButtonText: this.$t('xiaosharw.wozhidaole')
          })
          // this.$Toast.fail(this.$t('tsytongyong.ymmcw'))
        }
      } catch {
      }
    }
  }
}
</script>
<style lang="less" scoped>
.safedialogg{
    display: none;
    width: 100%;
    height: 6.67rem;
    background: rgba(0, 0, 0, 0.6);
    position: absolute;
    top:0;
    z-index: 3;
    input{
      caret-color:#36CFC9;
    }
    .box{
        width: 3.28rem;
        height: 3.2rem;
        background: #ffffff;
        border-radius: 8px;
        margin: 0.93rem 0.23rem 0px 0.24rem;
        overflow: auto;
        .boxtop{
            padding: 0.16rem 0.24rem 0px 0.24rem;
            display: flex;
            justify-content: space-between;
            span:nth-child(1){
             font-size: 0.14rem;
             font-family: PingFangSC-Regular, PingFang SC;
             font-weight: 400;
             color: rgba(0, 0, 0, 0.65);
             line-height: 0.22rem;
            }
            span:nth-child(2){
            font-size: 0.14rem;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: rgba(0, 0, 0, 0.85);
            line-height: 0.22rem;
            }
            span:nth-child(3){
             font-size: 0.14rem;
             font-family: PingFangSC-Regular, PingFang SC;
             font-weight: 400;
             color: #1890FF;
             line-height: 0.22rem;
            }
        }
        .boxbotm{
            .boxhavi{
                margin-top: 0.09rem;
                height: 0.01rem;
                background: rgba(0, 0, 0, 0.15);
                transition: all 2s ease-in;
            }
            .boxhavi1{
           background: #36CFC9;
           transition: all 2s ease-in;
            }
            .newwork{
              height: 0.22rem;
             font-size: 0.14rem;
             font-family: PingFangSC-Medium, PingFang SC;
             font-weight: 500;
             color: rgba(0, 0, 0, 0.85);
             line-height: 0.22rem;
            }
            margin:0.32rem 0.24rem 0px 0.24rem;
            .passwords{
                margin-top: 0.16rem;
                 span{
                display: inline-block;
                   font-size: 0.14rem;
                   font-family: PingFangSC-Regular, PingFang SC;
                   font-weight: 500;
                   color: rgba(0, 0, 0, 0.85);
                   line-height: 0.22rem;
                   margin-right: 0.18rem;
            }
            input{
                border: none;
                width: 1.2rem;
            }
            .inputthree{
              width: 1.15rem;
            }
            }
            .passwords1{
                margin-top: 0.16rem;
                input{
                    border: none;
                    width: 1.2rem;
                }
            }
            .zhwork{
                margin-left: 0.48rem ;
                font-size: 0.12rem;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: #FF4D4F;
                line-height: 0.2rem;
            }
        }
    }
}
</style>
