<template>
    <div class="bldask">
         <Zzjxdask v-if="mustatus==='DISINFECT'" @newtypeno="newtypeno" />
         <Ztxsdask v-if="mustatus==='PAUSE'" :taskreason="taskreason" @newtypeno="newtypeno" />
    </div>
</template>
<script>
import Zzjxdask from './zzjxtask/index'
import Ztxsdask from './ztzttask/index'
export default {
  // 接受父组件传过来的数据判定显示响应的界面
  props: ['mustatus', 'taskreason'],
  methods: {
    newtypeno() {
      this.$emit('gettaskstate', '')
    }
  },
  components: {
    Zzjxdask,
    Ztxsdask
  }
}
</script>
<style lang="less" scoped>
.bldask{
    position: relative;
}
</style>
