<template>
    <div class="dialog">
        <van-dialog v-model="show" :title="title" show-cancel-button :confirmButtonText="$t('homes.gengxin')" :cancelButtonText="$t('homes.zanbu')" confirmButtonColor="#1890FF"  :before-close="beforeClose" @close="setdataversion">
            <p class="dialogsonp">
                {{info.releaseNote}}
            </p>
        </van-dialog>
        <van-dialog v-model="nowShow" :title="title"  :confirmButtonText="$t('homes.ljgx')" confirmButtonColor="#1890FF"  :before-close="beforeClose">
            <p class="dialogsonp">
                {{info.releaseNote}}
            </p>
        </van-dialog>
        <!-- <van-overlay :show="overlayshow" z-index="5"> <div class="overlayson">正在准备升级请稍后</div> </van-overlay> -->
    </div>
</template>
<script>
import Vue from 'vue'
import { Dialog } from 'vant'

// 全局注册
Vue.use(Dialog)
export default {
  data: () => ({
    show: false,
    nowShow: false,
    info: {},
    title: '',
    overlayshow: false
  }),
  created() {
  },
  methods: {
    setdataversion() {
      localStorage.setItem('hasUpLevel', this.info.newVersion)
      console.log(this.info.newVersion)
    },
    showDialog(key, data) {
      this.title = this.$t('homes.xbb') + data.newVersion
      this.info = data
      if (key === 'now') {
        this.nowShow = true
      } else {
        this.show = true
      }
    },
    confirm() {
      // this.overlayshow = true
      this.newversion()
    },
    beforeClose(action, done) {
      if (action === 'confirm') {
        this.newversion(done)
      } else {
        done()
      }
    },
    async newversion(done) {
      const newverreq = {
        cmd: 'ota_upgrade',
        file_handle: this.info.file_handle
      }
      try {
        const newversonres = await this.Api.Statuto(newverreq)
        setTimeout(done, 3000)
        setTimeout(() => {
          window.location.href = `${window.location.protocol + '//' + window.location.hostname + ':1996'}`
        }, 3000)
        console.log(newversonres)
      } catch (err) {
        if (err.request.status === 411) {
          done()
          this.$Toast({
            message: this.$t('tsytongyong.sjbcuowu'),
            position: 'top'
          })
        } else if (err.request.status === 410) {
          done()
          this.$Toast({
            message: this.$t('tsytongyong.sjbbucz'),
            position: 'top'
          })
        } else if (err.request.status === 403) {
          done()
          this.$Toast({
            message: this.$t('tsytongyong.dqbuxunxcaozuo'),
            position: 'top'
          })
        } else if (err.request.status === 500) {
          done()
          this.$Toast({
            message: this.$t('tsytongyong.scwjbcw'),
            position: 'top'
          })
        } else {
          done()
        }
      }
    }
  }
}
</script>
<style lang="less">
.dialog{
  .dialogsonp{
    align-items: center;
    min-height: 0.3rem;
    font-size: 0.14rem;
    display: flex;
    justify-content: center;
    padding: 0 0.15rem;
  }
}
</style>
