<template>
    <div class="achdask">
        <Wcbldask v-if="recordstatus.task_status==='COMPLETE'" :recordstatus="recordstatus"  />
        <Zzbldask v-else-if="recordstatus.task_status==='ABORT'" :recordstatus="recordstatus" />
        <div class="editor" @click="txqrenwu">
            <span>{{$t('xiaosharw.xsrwmingcheng')}}: {{recordstatus.task_name}}</span> <span>{{$t('xiaosharw.chakanbg')}}></span>
        </div>
        <div class="solider"></div>
        <!-- <div @click="newdask" class="newdask"><span>+</span>
        <span>{{$t('xiaosharw.chuangjianxsrw')}}</span>
        </div> -->
        <div class="bottomnoe">
          <div class="bottomson1" @click="newchongzuo">
            <div><img src="../../../assets/iconchongzuo.png" alt=""></div>
            <div>{{$t('xinzeng.czrenwu')}}</div>
          </div>
          <div class="bottomson2" @click="newdask">
            <div><img src="../../../assets/iconchuangjian.png" alt=""></div>
            <div>{{$t('xinzeng.cjxsrenwu')}}</div>
          </div>
       </div>
        <Newtask ref="newtask"/>
    </div>
</template>
<script>
import Wcbldask from '../bldaskWc/index'
import Newtask from '../nodask/newtask/index'
import Zzbldask from '../bldaskZz/index'
export default {
  data: () => ({
    tyepp: 'C',
    timerss: null,
    task_id: ''
  }),
  inject: ['reload'],
  props: ['recordstatus'],
  created() {
    // 如果是完成或者终止状态下 也要时刻刷新页面以防用遥控器控制
    // 不能及时刷新数据一旦请求成功就局部刷新页面并且清楚定时器
    console.log(this.$props.recordstatus)
    this.task_id = this.$props.recordstatus.id
    this.timerss = setInterval(async() => {
      try {
        const pamsrams = {
          cmd: 'get_task_state'
        }
        const taskSstate = await this.Api.getotsstate(pamsrams)
        this.reload()
        // console.log(this)
        // if (taskSstate.data.status === 'START_COUNT_DOWN' || taskSstate.data.status === 'RESUME_COUNT_DOWN') {
        //   // console.log(111, taskSstate, this.reload)
        //   this.reload()
        //   // this.$router.push('/countdown')
        //   clearInterval(this.timerss)
        // } else {
        //   this.reload()
        //   // clearInterval(this.timerss)
        // }
        clearInterval(this.timerss)
        console.log(taskSstate)
      } catch {
      }
    }, 1000)
  },
  methods: {
    newchongzuo() {
      console.log(this.$refs.newtask)
      this.$refs.newtask.$el.style.display = 'block'
      this.$refs.newtask.establish({ taskid: this.$props.recordstatus.id })
    },
    txqrenwu() {
      this.$router.push(`/reportdetails/${this.task_id}`)
    },
    newdask() {
      this.$refs.newtask.$el.style.display = 'block'
      this.$refs.newtask.establish(null)
    }
  },
  components: {
    Wcbldask,
    Newtask,
    Zzbldask
  },
  beforeDestroy() {
    clearInterval(this.timerss)
    this.timerss = null
  }
}
</script>
<style lang="less" scoped>
.achdask{
  .bottomnoe{
    height: 0.72rem;
    background: #13A8A8;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    >div{
      display: inline-block;
    }
     .bottomson1{
        width: 0.8rem;
          margin-left: 1.08rem;
          margin-top: 0.17rem;
          >div{
            font-size: 0.12rem;
             font-weight: 400;
             color: #FFFFFF;
          }
          >div>img{
            width: 0.16rem;
            height: 0.16rem;
            margin-left: 0.16rem;
            margin-bottom: 0.04rem;
          }
     }
  }
  .bottomson2{
          margin-top: 0.17rem;
          margin-left: 0.2rem;
          >div{
             font-size: 0.12rem;
             font-weight: 400;
             color: #FFFFFF;
          }
          >div>img{
            width: 0.16rem;
            height: 0.16rem;
            margin-left: 0.24rem;
            margin-bottom: 0.04rem;
     }
  }
    .editor{
        display: flex;
        justify-content: space-between;
        margin: 0.24rem 0.23rem 0.09rem 0.24rem;
        span:nth-child(1){
          font-size: 0.14rem;
          font-weight: 400;
           color: rgba(0, 0, 0, 0.85);
         line-height: 0.22rem;
        }
                span:nth-child(2){
              font-size: 0.12rem;
              font-weight: 400;
              color: rgba(0, 0, 0, 0.65);
              line-height: 0.2rem;
        }
    }
    .solider{
        height: 0.01rem;
        background: rgba(0, 0, 0, 0.15);
        width: 3.28rem;
        margin: 0 auto;
    }
     .newdask{
      width: 1.84rem;
      height: 0.48rem;
      background: linear-gradient(180deg, #0BE7E5 0%, #099FA5 100%);
      box-shadow: 0px 0.15rem 0.45rem 0px rgba(85, 155, 158, 0.15);
      border-radius: 24px;
      margin: 0.88rem 0 0px 0.95rem;
      font-size: 0.14rem;
      font-weight: 500;
      color: #FFFFFF;
      line-height: 0.48rem;
      span:nth-child(1){
          margin: 0 0.22rem 0px 0.34rem;
      }
    }
}
</style>
