             <!-- <div class="SSID"><span>SSID</span><input type="text" v-model="workset"><span><img src="../../../assets/yuyangou.png" alt=""></span>
             <span v-if="flgespan1"></span><span v-else>{{$t('shezhiye.bukexiugai')}}</span></div> -->
             <!-- <div>是否启用: <input type="checkbox"></div> -->
