             <input type="text" v-model.trim="workset" @input="siidyanzheng"   @focus="focus1" @blur="blur1" maxlength="32"></div><span @click="ycssidcx">
             <img v-if="!ycssid" src="../../../assets/yuyangou.png" alt="">
             <img v-else src="../../../assets/shangla.png" alt="">
             </span>
             <div v-show="ycssid" class="ycssid">
              <div v-for="(ele, index) in obj1" :key="index" @click="indexssid(index)">{{ele.ssid}}</div>
             </div>
             </div>
             <div class="boxhavi" :class="{'boxhaicolor':boxhavi11}"></div>
             <div class="passwords"><div><span>{{$t('user.mima')}}</span><input v-if="sfmwen" type="password" v-model.trim="textpwd" @input="zzyanzheng"  @focus="focus2" @blur="blur2" maxlength="32">
             <input type="text" v-else v-model.trim="textpwd" @input="zzyanzheng"  @focus="focus2" @blur="blur2" maxlength="32" style="ime-mode: disabled"></div><span @click="qfsfmwen">
              <img v-if="sfmwen" src="../../../assets/yanjing.png" alt="">
             <img v-else src="../../../assets/biyanJing.png" alt=""></span>
             </div>
             <div class="boxhavi" :class="{'boxhaicolor':boxhavi12}"></div>
             <div class="zhwork" v-show="noneflge">{{$t('tsytongyong.shifouanxushuru')}}</div>
