</template>
<script>
export default {
  data: () => ({
    flgespan1: true,
    flgespan2: true,
    textpwd: '',
    workset: '',
    staip: '',
    sfmwen: true,
    noneflge: false,
    twoflge: false,
    ycssid: false,
    obj1: [],
    // wlflage: false,
    boxhavi11: false,
    boxhavi12: false
  }),
  props: ['getshujuwkrd'],
  inject: ['reload'],
  created() {
    this.networkset()
    console.log(this.getshujuwkrd)
    this.wissid()
  },
  methods: {
    focus1() {
      this.boxhavi11 = true
      console.log(1)
    },
    focus2() {
      this.boxhavi12 = true
    },
    blur1() {
      console.log(2)
      this.boxhavi11 = false
    },
    blur2() {
      this.boxhavi12 = false
    },
    indexssid(index) {
      this.workset = this.obj1[index].ssid
      this.textpwd = this.obj1[index].pwd
      this.ycssid = false
    },
    ycssidcx() {
      this.ycssid = !this.ycssid
      console.log(11)
    },
    qfsfmwen() {
      this.sfmwen = !this.sfmwen
    },
    siidyanzheng() {
      console.log(this.workset)
    },
    zzyanzheng() {
      // const reg = /^.{8,32}$/
      // if (!reg.test(this.textpwd)) {
      //   this.noneflge = true
      // } else {
      //   this.noneflge = false
      // }
      this.$nextTick(() => {
        if (this.textpwd !== null) {
          this.textpwd = this.textpwd.replace(/[^\w.?!@#$%^&/]/ig, '')
        }
      })
      console.log(this.textpwd)
    },
    spanbh2() {
      this.flgespan2 = false
    },
    yinnone() {
      this.$refs.yinones.style.display = 'none'
      this.ycssid = false
      this.textpwd = ''
      this.networkset()
    },
    async wissid() {
      try {
        const reqssid = {
          cmd: 'get_wifi_settings_list'
        }
        const resssidlb = await this.Api.Statuto(reqssid)
        this.obj1 = resssidlb.data.wifi_settings_list
        console.log(resssidlb, JSON.parse(JSON.stringify(this.obj1)))
      } catch {
      }
    },
    async xgcanshu() {
      console.log(this.textpwd)
      // if (this.wlflage) {
      if (this.textpwd === '' || this.workset === '') {
        this.$Toast({
          message: this.$t('tsytongyong.qbysrwk'),
          position: 'top'
        })
        return
      }
      // }
      if (this.noneflge || this.twoflge) {
        this.$Toast({
          message: this.$t('tsytongyong.qanxuqiushuru'),
          position: 'top'
        })
        return
      }
      this.$emit('wlyccchuxian', '')
      const verifypad = {
        cmd: 'update_network_settings',
        mode: 'STA',
        // enabled: this.wlflage,
        enabled: true,
        ssid: this.workset,
        wifi_pwd: this.textpwd
      }
      try {
        const ressta = await this.Api.Statuto(verifypad)
        console.log(ressta)
        // this.$Toast.success(this.$t('tsytongyong.xgSTAchengg'))
        this.$emit('wlyccyincang', '')
        this.$dialog.alert({
          title: this.$t('tsytongyong.xgchenggong'),
          message: this.$t('tsytongyong.xgSTAchengg'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
        this.reload()
      } catch {
        this.$emit('wlyccyincang', '')
        // this.$Toast.fail(this.$t('tsytongyong.qingqiushibai'))
        this.$dialog.alert({
          title: this.$t('tsytongyong.shezhishibai'),
          message: this.$t('tsytongyong.qingqiushibai'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
      }
    },
    async networkset() {
      try {
        const poshms = {
          cmd: 'get_network_settings',
          mode: 'STA'
        }
        const refsa = await this.Api.Statuto(poshms)
        console.log(refsa)
        this.workset = refsa.data.ssid
        this.staip = refsa.data.ip_addr
        // this.wlflage = refsa.data.enabled
      } catch (err) {
      }
    }
  }
}
</script>
