        // 延时500毫秒
      }).then(() => {
        const fuweicanshu = {
          cmd: 'reset_part_lifetime',
          part_ids: ['uv_group1']
        }
        this.Api.Statuto(fuweicanshu).then(res => {
          console.log(res)
          this.$Toast({
            message: this.$t('tsytongyong.fwchenggong'),
            position: 'top'
          })
          this.top = 100
          this.syxs = 720000
        }).catch((err) => {
          console.log(err)
          this.$Toast({
            message: this.$t('tsytongyong.fwshibai'),
            position: 'top'
          })
        })
        // this.Api.getotsstate(abortask).then((res) => {
        //   console.log(res)
        // })
