<template>
    <div>
        <Wcfod v-if="recordstatus.task_status === 'COMPLETE'" :recordstatus="recordstatus" />
        <Zzfod v-else-if="recordstatus.task_status === 'ABORT'" :recordstatus="recordstatus" />
    </div>
</template>
<script>
import Wcfod from './wcfod'
import Zzfod from './zzfod'
export default {
  data: () => ({
    timerss: null
  }),
  props: ['recordstatus'],
  inject: ['reload'],
  created() {
    this.timerss = setInterval(async() => {
      try {
        const pamsrams = {
          cmd: 'get_task_config'
        }
        const taskSstate = await this.Api.getotsstate(pamsrams)
        this.reload()
        clearInterval(this.timerss)
        console.log(taskSstate)
      } catch {
      }
    }, 1000)
  },
  components: {
    Wcfod,
    Zzfod
  },
  beforeDestroy() {
    clearInterval(this.timerss)
    this.timerss = null
  }
}
</script>
