import Vue from 'vue'
import Element from 'element-ui'
import {
  Toast,
  NavBar,
  Tabbar, TabbarItem,
  Swipe, SwipeItem,
  Grid, GridItem,
  Card,
  Button,
  Tab, Tabs,
  Lazyload,
  ImagePreview, RadioGroup, Radio,
  Panel,
  Stepper, Form,
  Field,
  Checkbox, CheckboxGroup, DatetimePicker,
  Slider,
  Pagination, Progress, Loading, Picker,
  Divider, Switch, Dialog, Notify, CountDown, Circle, Uploader, Calendar, cell, Popup, Icon, overlay
} from 'vant'

// 注册时可以配置额外的选项
Vue.use(Lazyload, {
  loading: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1596601999300&di=c45cac6846fd8afb23428fb0627b4c57&imgtype=0&src=http%3A%2F%2Fimg1.imgtn.bdimg.com%2Fit%2Fu%3D966724446%2C3729827248%26fm%3D214%26gp%3D0.jpg',
  lazyComponent: true
})

Vue
  .use(NavBar).use(Loading).use(DatetimePicker).use(Picker).use(RadioGroup).use(Radio).use(Calendar).use(cell).use(Popup).use(overlay)
  .use(Tabbar).use(TabbarItem).use(Divider).use(Switch).use(CountDown).use(Circle)
  .use(Swipe).use(SwipeItem)
  .use(Grid).use(GridItem).use(Progress)
  .use(Card)
  .use(Button)
  .use(Tab).use(Tabs)
  .use(Panel).use(Slider).use(Pagination).use(Uploader)
  .use(Stepper).use(Form).use(Field).use(Checkbox).use(CheckboxGroup).use(Icon)

Vue.use(Element, { size: 'small', zIndex: 3000 })
Vue.prototype.$Toast = Toast
Vue.prototype.$ImagePreview = ImagePreview
Vue.prototype.$Dialog = Dialog
Vue.prototype.$Notify = Notify
