<script>
import Moment from 'moment'
import Absoluts from '../../components/tutorabsoluts'
export default {
  data: () => ({
    data: '',
    show: false,
    minDate: new Date(2020, 8, 31),
    currentsmath: 1,
    maxcurrent: '',
    xingbiao: false,
    fangjian: false,
    xingbiaomath: 0,
    fangjianmath: 0,
    listobject: '',
    sfylsjl: 'A',
    zgrenwu: '',
    obj: [],
    cxtiaojian: '',
    listmapname: '',
    xingbiaoyoumath: 1,
    ele: '',
    date_from: null,
    date_to: null,
    nowenjian: '',
    map_name: null,
    star: null
  }),
  inject: ['reload'],
  created() {
    // this.getlistls()
    // this.getmapnamelist()
  },
  activated() {
    if (!this.$route.meta.isBack) {
      this.$refs.fangjianchazhao.textContent = this.$t('homes.fangjian')
      this.$refs.xingbaobianhu.textContent = this.$t('baogaogl.xingbiao')
      this.$refs.calendar.reset()
      this.star = null
      this.map_name = null
      this.date_from = null
      this.date_to = null
      this.currentsmath = 1
      this.xingbiaomath = 0
      this.fangjianmath = 0
      this.getlistls()
      this.getmapnamelist()
    }
    this.$route.meta.isBack = false
  },
  methods: {
    Moment,
    async getmapnamelist() {
      try {
        const getmapna = {
          cmd: 'get_map_name_list'
        }
        const resname = await this.Api.gethistoryte(getmapna)
        this.listmapname = resname.data.map_name_list
        // this.listmapname.push(this.$t('xiaosharw.yaokongqi'))
        console.log(resname, this.listmapname)
      } catch {
      }
    },
    async xingbiaoqufan(xbindex, value, id) {
      console.log(xbindex)
      try {
        const resxingbiao = {
          cmd: 'set_star',
          task_id: Number(id),
          star: !(value)
        }
        console.log(resxingbiao)
        const resquxb = await this.Api.gethistoryte(resxingbiao)
        this.obj[xbindex].star = !this.obj[xbindex].star
        // this.$Toast({
        //   message: this.$t('xinzeng.xgxbchenggong'),
        //   position: 'top'
        // })
        console.log(resquxb)
      } catch {
        // this.$Toast({
        //   message: this.$t('xinzeng.xgxbshibai'),
        //   position: 'top'
        // })
      }
    },
    async getlistls() {
      this.$refs.absoluts.$el.style.display = 'none'
      const getlistlishi = {
        cmd: 'get_list',
        page_size: 5,
        page_index: this.currentsmath - 1,
        map_name: this.map_name,
        star: this.star,
        date_from: this.date_from,
        date_to: this.date_to
      }
      try {
        const reslistls = await this.Api.gethistoryte(getlistlishi)
        this.sfylsjl = 'A'
        this.maxcurrent = reslistls.data.page_cnt
        this.obj = reslistls.data.list
        // if (ele === null) {
        //   this.obj = []
        //   Array.from(reslistls.data.list).map((el, index) => {
        //     if (el.map_name === null) {
        //       this.obj.push(reslistls.data.list[index])
        //     }
        //   })
        // }
        this.zgrenwu = reslistls.data.total_cnt
        if (this.maxcurrent <= 0) {
          this.currentsmath = 0
        }
        console.log(reslistls)
      } catch (err) {
        if (err.request.status === 404) {
          this.sfylsjl = 'B'
        }
      }
    },
    fangjiantow(ele, index) {
      // 2.1改
      console.log(ele)
      this.currentsmath = 1
      this.$refs.fangjianchazhao.textContent = ele
      // if (ele === this.$t('xiaosharw.yaokongqi')) {
      //   ele = null
      // }
      this.map_name = ele
      // this.$refs.fangjianchazhao.textContent = ele
      this.fangjianmath = index + 1
      this.fangjian = false
      this.getlistls()
    },
    fangjianone() {
      this.$refs.fangjianchazhao.textContent = this.$t('baogaogl.quanbu')
      this.currentsmath = 1
      this.xingbiaomath = 0
      this.star = null
      this.map_name = null
      this.date_from = null
      this.date_to = null
      this.$refs.xingbaobianhu.textContent = this.$t('baogaogl.xingbiao')
      this.getlistls()
      this.fangjianmath = 0
      this.fangjian = false
    },
    async xingbiaothree() {
      this.$refs.xingbaobianhu.textContent = this.$t('baogaogl.meixingbiao')
      this.star = false
      this.currentsmath = 1
      this.getlistls()
      this.xingbiaomath = 2
      this.xingbiao = false
    },
    async xingbiaotow() {
      this.currentsmath = 1
      this.star = true
      this.$refs.xingbaobianhu.textContent = this.$t('baogaogl.youxingbiao')
      this.getlistls()
      this.xingbiaomath = 1
      this.xingbiao = false
    },
    xingbiaoone() {
      this.$refs.fangjianchazhao.textContent = this.$t('homes.fangjian')
      this.fangjianmath = 0
      this.currentsmath = 1
      this.star = null
      this.date_from = null
      this.date_to = null
      this.map_name = null
      this.getlistls()
      this.$refs.xingbaobianhu.textContent = this.$t('baogaogl.quanbu')
      this.xingbiaomath = 0
      this.xingbiao = false
    },
    fangjianfalge() {
      this.fangjian = !(this.fangjian)
      this.xingbiao = false
    },
    xingbiaofalge() {
      this.xingbiao = !(this.xingbiao)
      this.fangjian = false
    },
    goReportetils(taskid) {
      this.$router.push(`/reportdetails/${taskid}`)
    },
    currentmatjian() {
      if (this.maxcurrent === 0) {
        return
      }
      if (this.currentsmath <= 1) {
        this.currentsmath = 1
        return
      }
      this.currentsmath--
      this.getlistls()
    },
    currentmatjia() {
      console.log(1)
      if (this.currentsmath >= this.maxcurrent) {
        this.currentsmath = this.maxcurrent
        return
      }
      console.log(2)
      this.currentsmath++
      this.getlistls()
    },
    calendarbutton() {
      this.$refs.calendar.reset()
      this.$Toast({
        message: this.$t('richangweihu.hfwmrsj')
      })
      // this.date_from = Moment(date[0]).format(('YYYY-MM-DD'))
      // this.date_to = Moment(date[1]).format(('YYYY-MM-DD'))
    },
    imgforta() {
      this.show = true
      this.xingbiao = false
      this.fangjian = false
    },
    downshow() {
      this.$refs.absoluts.$el.style.display = 'block'
    },
    formatDate(date) {
      return ` ${date.getMonth() + 2}/ ${date.getDate()}`
    },
    async onConfirm(date) {
      const [start, end] = date
      this.show = false
      this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`
      console.log(date[0])
      this.currentsmath = 1
      this.date_from = Moment(date[0]).format(('YYYY-MM-DD'))
      this.date_to = Moment(date[1]).format(('YYYY-MM-DD'))
      this.getlistls()
    }
  },
  components: {
    Absoluts
  },
  beforeRouteEnter(to, from, next) {
    if (from.name === 'reportdetails') {
      to.meta.isBack = true
    }
    next()
  }
}
</script>
