<template>
    <div class="norswk">
        <div class="safetytop">
          <div class="safenoe1">{{$t('shezhiye.jiqirenmc')}}</div>
          <div class="safenoe5"><span>{{responrbt.robot_name?responrbt.robot_name:'UVBOT'}}</span><span @click="safeloc">{{$t('shezhiye.genggai')}} <img src="../../../assets/youbianjiao2.png" alt=""> </span></div>
          <div class="safedix"></div>
          <div class="safenoe1">{{$t('shezhiye.yuyintsyl')}}</div>
          <div class="safeone2"><div class="slider2"><van-slider active-color="#36CFC9" bar-height="0.08rem" min="10"  step="10" v-model="volume" @change="fuchang1" /></div><div class="slider3">{{volume}}%</div></div>
           <!-- <div class="safenoe11" v-show="dataversion==='STANDARD' || dataversion==='ADVANCED'">回冲电量下限</div>
          <div class="safeone2" v-show="dataversion==='STANDARD' || dataversion==='ADVANCED'"><div class="slider2"><van-slider active-color="#36CFC9" bar-height="0.08rem" v-model="limit" @change="fychang2" /></div><div class="slider3">{{limit}}%</div></div> -->
           <div class="safenoe11">{{$t('shezhiye.xtyyshezhi')}}</div>
           <div class="safenoe6" @click="laguaf1"><span>中文</span><span class="langua1" v-show="langua1&&langua1==='zh'"><img src="../../../assets/checkgou.png" alt=""></span></div>
            <div class="safedix"></div>
         <div class="safenoe6" @click="laguaf2"><span>English</span><span class="langua2" v-show="langua1&&langua1==='en'"><img src="../../../assets/checkgou.png" alt=""></span></div>
         <div class="safedix"></div>
          <div class="safenoe6" @click="laguaf3"><span>日本語</span><span class="langua2" v-show="langua1&&langua1==='jp'"><img src="../../../assets/checkgou.png" alt=""></span></div>
         <div class="safedix"></div>
          <div class="safenoe1">{{$t('shezhiye.shijianshezhi')}}</div>
          <div class="safenoe7"><span>{{newtime}}</span><span @click="safeloc2">{{$t('shezhiye.genggai')}} <img src="../../../assets/youbianjiao2.png" alt=""> </span></div>
          <div class="safedix"></div>
          <!-- <div class="safenoe1">{{$t('shezhiye.shiqushezhi')}}</div>
          <div class="safenoe7"><span>{{$t('shezhiye.UTC')}}{{UTCsq}}</span><span @click="safeloc3">{{$t('shezhiye.genggai')}} <img src="../../../assets/youbianjiao2.png" alt=""> </span></div>
          <div class="safedix"></div> -->
        </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    volume: 10,
    langua1: 'zh',
    limit: '',
    newtime: '',
    UTCsq: ''
  }),
  inject: ['reload'],
  props: ['responrbt', 'dataversion'],
  created() {
    // this.volume = this.responrbt.volume
    // console.log(this.volume)
    // this.limit = this.responrbt.recharge_low_limit
    this.volumeea()
    // 获取音量 及语言设置 时间设置
    if (localStorage.getItem('lang') === 'en_US') {
      this.langua1 = 'en'
    } else if (localStorage.getItem('lang') === 'ja_JP') {
      this.langua1 = 'jp'
    } else {
      this.langua1 = 'zh'
    }
    this.timeshijian()
  },
  methods: {
    async volumeea() {
      try {
        const voluea = {
          cmd: 'get_robot_settings',
          volume: true
        }
        const resvolue = await this.Api.Statuto(voluea)
        this.volume = resvolue.data.volume
      } catch {
      }
    },
    async timeshijian() {
      const netimea = {
        cmd: 'get_time_settings'
      }
      try {
        const fortime = await this.Api.Statuto(netimea)
        console.log(fortime)
        this.UTCsq = fortime.data.tz
        this.newtime = fortime.data.date + ' ' + fortime.data.time
      } catch {
      }
    },
    // 点击以后让 时区 或者 时间设置框出现
    safeloc3() {
      this.$emit('shiqulog', '')
    },
    safeloc2() {
      this.$emit('timelog', '')
    },
    setname(newrobtoname) {
    },
    safeloc() {
      this.$emit('nordialog', '')
    },
    laguaf1() {
      if (localStorage.getItem('languageSet') === 'zh' && localStorage.getItem('lang') === 'zh_CN') {
        return
      }
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.qiehuanyuyan'),
        message: this.$t('tsytongyong.shifouqhyy'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
        // 如果确认退出以后发送请求删除后台Token并且本地Token也删除
        // 延时500毫秒
      }).then(() => {
        const languashh = {
          cmd: 'update_robot_settings',
          lang: 'zh_CN'
        }
        this.Api.Statuto(languashh).then(reslang => {
          this.langua1 = 'zh'
          localStorage.setItem('lang', 'zh_CN')
          this.$i18n.locale = 'zh'
          localStorage.setItem('languageSet', 'zh')
          localStorage.setItem('activeshezhiye', 3)
          this.reload()
        })
      })
    },
    laguaf2() {
      if (localStorage.getItem('languageSet') === 'en' && localStorage.getItem('lang') === 'en_US') {
        return
      }
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.qiehuanyuyan'),
        message: this.$t('tsytongyong.shifouqhyy'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
      }).then(() => {
        const languashh = {
          cmd: 'update_robot_settings',
          lang: 'en_US'
        }
        this.Api.Statuto(languashh).then(reslang => {
          this.langua1 = 'en'
          localStorage.setItem('lang', 'en_US')
          this.$i18n.locale = 'en'
          localStorage.setItem('languageSet', 'en')
          localStorage.setItem('activeshezhiye', 3)
          this.reload()
        })
      })
    },
    laguaf3() {
      if (localStorage.getItem('languageSet') === 'jp' && localStorage.getItem('lang') === 'ja_JP') {
        return
      }
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.qiehuanyuyan'),
        message: this.$t('tsytongyong.shifouqhyy'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
      }).then(() => {
        const languashh = {
          cmd: 'update_robot_settings',
          lang: 'ja_JP'
        }
        this.Api.Statuto(languashh).then(reslang => {
          this.langua1 = 'jp'
          localStorage.setItem('lang', 'ja_JP')
          this.$i18n.locale = 'jp'
          localStorage.setItem('languageSet', 'jp')
          localStorage.setItem('activeshezhiye', 3)
          this.reload()
        })
      })
    },
    async fuchang1(value) {
      console.log(value)
      // if (value < 1) {
      //   value = 1
      //   this.$Toast({
      //     message: '音量最低为1',
      //     position: 'top'
      //   })
      //   this.volume = value
      // }
      try {
        const newvolume = {
          cmd: 'update_robot_settings',
          volume: value
        }
        const newsvlue = await this.Api.Statuto(newvolume)
        this.$Toast({
          message: this.$t('tsytongyong.xgylchenggong'),
          position: 'top'
        })
        console.log(newsvlue)
      } catch {
      }
    },
    async fychang2(vuelue) {
      try {
        const newvolume = {
          cmd: 'update_robot_settings',
          recharge_low_limit: vuelue
        }
        const newsvluee = await this.Api.Statuto(newvolume)
        this.$Toast({
          message: '修改回冲电量成功',
          position: 'top'
        })
        console.log(newsvluee)
      } catch {
      }
    }
  }
}
</script>
