        color: rgba(0, 0, 0, 0.85);
        line-height: 0.22rem;
               img{
          width: 0.16rem;
          height: 0.16rem;
        }
        .langua1{
            margin-left: 2.74rem;
            color: #36CFC9;
        }
        .langua2{
            margin-left: 2.55rem;
            color: #36CFC9;
        }
    }
    .safenoe7{
        margin-top: 0.16rem;
        font-size: 0.14rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(0, 0, 0, 0.85);
        line-height: 0.22rem;
        display: flex;
        justify-content: space-between;
        padding-right: 0.45rem;
        span:nth-child(1){
            height: 0.2rem;
        }
       img{
         width: 0.14rem;
         height: 0.14rem;
         vertical-align: text-bottom;
       }
        span:nth-child(2){
            font-size: 0.12rem;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: rgba(0, 0, 0, 0.65);
            line-height: 0.2rem;
        }
    }
}
