    ]
    // console.log(this.columns[0].values, this.hh)
    // console.log(this.columns[0].values.indexOf(this.hh))
  },
  methods: {
    sfmshu(value) {
      console.log(value)
      this.sfmzhongshi = value[0].slice(0, 2)
      this.sfmzhongfen = ':' + value[1].slice(0, 2)
      this.sfmzhongmiao = ':' + value[2].slice(0, 2)
      this.sfmzhong = this.sfmzhongshi + this.sfmzhongfen + this.sfmzhongmiao
    },
    nome() {
      this.$refs.nontime.style.display = 'none'
    },
    async matshu(value) {
      this.$refs.pickers.confirm()
      // console.log(this.sfmzhong)
      const newtimedata1 = Moment(value).format(('YYYY-MM-DD'))
      // console.log(newtimedata1 + this.sfmzhong)
      console.log(newtimedata1, this.sfmzhong)
      try {
        const setTimea = {
          cmd: 'update_time_settings',
          date: newtimedata1,
          time: this.sfmzhong
        }
        const gettimeo = await this.Api.Statuto(setTimea)
        console.log(gettimeo)
        this.$Toast({
          message: this.$t('shezhiye.xgshijiancg'),
          position: 'top'
        })
        // 设置成功以后给之提示  并且安全设置页tab的默认值为3
        localStorage.setItem('activeshezhiye', 3)
        this.reload()
      } catch (err) {
        if (err.request.status === 403) {
          this.$Toast({
            message: this.$t('shezhiye.xgshijiansb') + this.$t('tsytongyong.dqbuxunxcaozuo'),
            position: 'top'
          })
        } else {
          this.$Toast({
            message: this.$t('shezhiye.xgshijiansb'),
            position: 'top'
          })
        }
      }
    },
    formatter(type, val) {
      if (type === 'month') {
        return `${val}` + this.$t('shezhiye.yue')
      } else if (type === 'day') {
        return `${val}` + this.$t('shezhiye.ri')
      } else if (type === 'year') {
        return `${val}` + this.$t('shezhiye.nian')
      }
      return val
    },
    async timeshijian() {
      const netimea = {
        cmd: 'get_time_settings'
      }
      try {
        const fortime = await this.Api.Statuto(netimea)
        // this.time = fortime.data.time
        this.currentime = new Date(fortime.data.date)
        // 查找下标 默认下标值
        this.time = fortime.data.time.split(':')
        console.log(this.time)
        this.hh = this.columns[0].values.indexOf(this.time[0] + this.$t('shezhiye.shi'))
        this.ss = this.columns[1].values.indexOf(this.time[1] + this.$t('shezhiye.fen'))
        this.ff = this.columns[2].values.indexOf(this.time[2] + this.$t('shezhiye.miao'))
        this.columns[0].defaultIndex = this.hh
        this.columns[1].defaultIndex = this.ss
        this.columns[2].defaultIndex = this.ff
        // this.UTCsq = fortime.data.tz
        // this.newtime = fortime.data.date + ' ' + fortime.data.time + ' UTC' + fortime.data.tz
      } catch {
      }
    }
