import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/vantUI'
import './filters'
import './api/index'
import Vant from 'vant'
import VueI18n from 'vue-i18n'
import htmlToPdf from './print/htmlToPdf'
import { VueJsonp } from 'vue-jsonp'
Vue.use(VueJsonp)
Vue.use(VueI18n)
Vue.use(htmlToPdf)
const i18n = new VueI18n({
  locale: localStorage.getItem('languageSet') || 'zh', // 从localStorage里获取用户中英文选择，没有则默认中文
  messages: {
    zh: require('./components/language/zh'),
    en: require('./components/language/en'),
    jp: require('./components/language/jp')
  }
})
Vue.config.productionTip = false

// document.body.addEventListener('touchmove', eve => {
//   eve.preventDefault()
//   eve.stopPropagation()
// }, { passive: false })

router.beforeEach((to, form, next) => {
  // 如果进入到的路由是登录页或者注册页面，则正常展示
  // 除了进入登录页面外 所有的页面都必须有token如果没有则不需他访问
  if (to.path === '/login') {
    if (localStorage.getItem('token')) {
      // 字符串 false true 因为localStorage里面是字符串
      if (localStorage.getItem('actived') === 'false') {
        router.push('/wizardpage/' + localStorage.getItem('usenameID'))
        return
      }
      router.push(`/home/${localStorage.getItem('usenameID')}`)
    }
    next()
  } else if (to.path === '/wipedata' || to.path === '/gcmsshezhi') {
    next()
  } else if (!localStorage.getItem('token')) {
    Vant.Toast({
      message: localStorage.getItem('languageSet') === 'zh' || !localStorage.getItem('languageSet') ? '尚未登录，请先登录' : 'Please login first',
      // message: Vue.$t('tsytongyong.qxdenglu'),
      position: 'top'
    })
    next('/login')
  } else if (localStorage.getItem('token')) {
    // console.log(111)
    next()
    if (localStorage.getItem('actived') === 'false') {
      router.push('/wizardpage/' + localStorage.getItem('usenameID'))
    }
  } else {
    next()
  }
})
new Vue({
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
