<template>
    <div class="establish" ref="establishs">
          <div class="establishtop">
      <span @click="nonemore">{{$t('xiaosharw.quxiao')}}</span> <span>{{$t('xiaosharw.xsrw')}}</span>
      <div class="establicenter">
        <div v-show="false" :class="[{establicenterson1:actitive>=0},{establicenterson2:actitive>=1}]" v-if="actitive>0"><img src="../../../../../assets/checkgou.png" alt=""></div>
        <div v-show="false" :class="[{establicenterson1:actitive>=0},{establicenterson2:actitive>=1}]" v-else>1</div>
        <div v-show="false" class="solidx" :class="{solidxblue:actitive>=1}"></div>
        <div :class="[{establicenterson1:actitive>=1},{establicenterson2:actitive>=2}]" v-if="actitive>1"><img src="../../../../../assets/checkgou.png" alt=""></div>
        <div :class="[{establicenterson1:actitive>=1},{establicenterson2:actitive>=2}]" v-else>1</div>
        <div class="solidx" :class="{solidxblue:actitive>=2}"></div>
        <div :class="{establicenterson1:actitive>=2}">2</div>
      </div>
      <div class="establicenterbtm">
         <div v-show="false" :class="{establicenterbtmson:actitive>=0}">{{$t('xiaosharw.chakanxsd')}}</div>
         <div :class="{establicenterbtmson:actitive>=1}">{{$t('xiaosharw.peizhicl')}}</div>
         <div :class="{establicenterbtmson:actitive>=2}">{{$t('xiaosharw.rwgailan')}}</div>
      </div>
        </div>
        <div class="establibottom">
            <div class="estabtmson" :class="{estabtmsonxs:actitive===0}"><img class="modemapdi" src="../../../../../assets/mapcreat.png" alt="">
            <img src="../../../../../assets/xsdweizhi.png" alt="" class="xswzhi"></div>
           <div class="estabtmson" :class="{estabtmsonxs:actitive===1}">
               <Estabson2 ref="moudasds"/>
           </div>
            <div class="estabtmson" :class="{estabtmsonxs:actitive===2 }">
              <Estabson3 :toestbs3="toestbs3"  ref="moudastms"/>
            </div>
        </div>
        <div class="btnesta">
         <button class="btn1" :class="{btn1bh:actitive>=2}" @click="actimathjian">{{$t('xiaosharw.shangyibu')}}</button>
         <button class="btn2" @click="actimathjia" :class="{btn2bh:actitive>=2}">{{actitive>=2?$t('xiaosharw.kaishi'):$t('xiaosharw.xiayibu')}}</button>
        </div>
    </div>
</template>
<script>
import Estabson2 from './estabtmson2/index'
import Estabson3 from './estabtmson3/index'
export default {
  data: () => ({
    actitive: 1,
    toestbs3: {},
    checkedpaduan: ''
  }),
  inject: ['reload'],
  created() {
  },
  // 获取app.vue里面的方法局部刷新
  methods: {
    estabchongzuo(obja) {
      this.$refs.moudasds.zhixinghanshu(obja)
    },
    actimathjian() {
      // 点击按钮减一
      this.actitive--
      if (this.actitive < 0) {
        this.actitive = 0
      }
    },
    async actimathjia() {
      // 点击按钮加一 最多为2 当大于2时候就继续为2
      if (this.actitive === 1) {
        if (this.$refs.moudasds.roomno === '') {
          this.$Toast({
            message: this.$t('xiaosharw.qsrdqfjmc'),
            position: 'top'
          })
          return
        } else if (this.$refs.moudasds.operator === '') {
          this.$Toast({
            message: this.$t('nenbanben.qingshurumingzi'),
            position: 'top'
          })
          return
        }
        if (this.$refs.moudasds.roomflge || this.$refs.moudasds.operatoflage) {
          this.$Toast({
            message: this.$t('tsytongyong.qanxuqiushuru'),
            position: 'top'
          })
          return
        }
      }
      this.actitive++
      // console.log(this.$refs.Taksmove)
      // console.log(this.reload)
      const yy = new Date().getFullYear()
      const mm = new Date().getMonth() + 1
      const dd = new Date().getDate()
      const hh = new Date().getHours()
      const mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()
      const ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds()
      const gettime = yy + '-' + mm + '-' + dd + ' ' + hh + mf + ss
      // const gettime = 'Task-' + yy + '-' + mm + '-' + dd + '-' + hh + mf + ss
      // 当下一页按钮等于2时候相当于进入最后一个步骤把值取到传到子组件去
      if (this.actitive === 2) {
        // const reg = /(\s*:\s*)+/ig
        this.toestbs3 = {
          menjinpai: this.$refs.moudasds.menjinpai,
          checked2: this.$refs.moudasds.checked2,
          checked3: this.$refs.moudasds.checked3,
          dur: this.$refs.moudasds.dur,
          power_mode: this.$refs.moudasds.power_mode,
          tasknames: gettime,
          safety_auto_resume: this.$refs.moudasds.safety_auto_resume,
          paizhao: this.$refs.moudasds.paizhao,
          pirhf: this.$refs.moudasds.pirhf,
          roomno: this.$refs.moudasds.roomno,
          operator: this.$refs.moudasds.operator
        }
        // console.log(this.$refs.moudasds.operator, this.$refs.moudasds.operator.replace(reg, ''))
        this.$refs.moudasds.flge = false
        this.$refs.moudasds.noenlist = false
      }
      // 因为是共享组件 并且是父元素 所以只能根据判断条件给与不同的事件
      if (this.actitive > 2) {
        console.log(this.toestbs3)
        this.actitive = 2
        this.checkedpaduan = this.$refs.moudastms.checkedpaduan
        console.log(this.checkedpaduan)
        if (this.checkedpaduan) {
          const setsavetask = {
            cmd: 'save_task_config',
            task_config: {
              door_sensor_enabled: this.$refs.moudasds.menjinpai,
              power_mode: this.$refs.moudasds.power_mode,
              dur: this.$refs.moudasds.dur,
              pir_enabled: this.$refs.moudasds.pirhf,
              safety_auto_resume: this.$refs.moudasds.safety_auto_resume,
              map_name: this.$refs.moudasds.roomno,
              face_detect_enabled: this.$refs.moudasds.paizhao
            }
            // task_tags: [{
            //   name: 'operator',
            //   value: this.$refs.moudasds.operator
            // }]
          }
          console.log(setsavetask.task_config)
          try {
            // 如果按钮为true 则发送请求把数据保存发送到后台
            const resetots = await this.Api.getotsstate(JSON.stringify(setsavetask))
            console.log(resetots)
          } catch {
          }
        }
        // 拼接时间给与任务名称 并且发送请求创建任务并且跳转到任务倒计时页面
        // const yy = new Date().getFullYear()
        // const mm = new Date().getMonth() + 1
        // const dd = new Date().getDate()
        // const hh = new Date().getHours()
        // const mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()
        // const ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds()
        // const gettime = yy + '-' + mm + '-' + dd + ' ' + hh + mf + ss
        const getcretask = {
          cmd: 'create_task',
          task_name: this.toestbs3.tasknames,
          config: {
            door_sensor_enabled: this.$refs.moudasds.menjinpai,
            power_mode: this.$refs.moudasds.power_mode,
            dur: this.$refs.moudasds.dur,
            pir_enabled: this.$refs.moudasds.pirhf,
            safety_auto_resume: this.$refs.moudasds.safety_auto_resume,
            map_name: this.$refs.moudasds.roomno,
            face_detect_enabled: this.$refs.moudasds.paizhao
          },
          task_tags: [{
            name: 'operator',
            value: this.$refs.moudasds.operator
          }]
        }
        try {
          await this.Api.getotsstate(getcretask)
          this.reload()
          this.$router.push('/countdown')
          // this.restate()
          // this.reload()
        } catch (err) {
          if (err.response.status === 403) {
            this.$Toast({
              message: this.$t('tsytongyong.dqycjrenwu')
            })
          } else if (err.response.status === 412) {
            this.$Toast({
              message: this.$t('nenbanben.cjsbsjwtb')
            })
          } else if (err.response.status === 499) {
            this.$Toast({
              message: this.$t('nenbanben.jjanbaxqxjb')
            })
          } else if (err.response.status === 498) {
            this.$Toast({
              message: this.$t('nenbanben.zlqwdgg')
            })
          } else if (err.response.status === 497) {
            this.$Toast({
              message: this.$t('nenbanben.zqrybdykq')
            })
          }
        }
      }
    },
    nonemore() {
      this.$refs.moudasds.flge = false
      this.$Dialog.confirm({
        title: this.$t('xinzeng.shifouqxxjrenwu'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
        // 如果确认退出以后发送请求删除后台Token并且本地Token也删除
      // 延时500毫秒
      }).then(() => {
      //  当取消时回到第一步 下一次再进不会到后面的页面 并且重新获取配置文件数据，
        console.log(this.$refs, this.$refs.establishs.flge)
        // localStorage.removeItem('chongzuoanniushu')
        this.$refs.establishs.style.display = 'none'
        this.$refs.moudasds.getotsstatus()
        this.$refs.moudasds.pirtimejia = 0
        this.$refs.moudasds.flge = false
        this.actitive = 1
        this.$refs.moudasds.operator = ''
        this.$emit('nonemore', '')
      })
    }
  },
  components: {
    Estabson2,
    Estabson3
  },
  mounted() {
  }
}
</script>
<style lang="less" scoped>
