    background: #F5F8FA;
    .establibottom{
        width: 100%;
        background: #FFFFFF;
        .estabtmson{
            display: none;
            position: relative;
            .xswzhi{
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
              margin: auto;
              width: 0.4rem;
              height: 0.56rem;
            }
            .modemapdi{
              width: 100%;
              height: 5.43rem;
            }
        }
        .estabtmsonxs{
            display: block;
        }
    }
     .establishtop{
      padding-bottom: 0.08rem;
       span:nth-child(1){
      margin-left: 0.24rem;
      font-size: 0.14rem;
      font-weight: 400;
      color: rgba(0, 0, 0, 0.85);
      line-height: 0.22rem;
    }
       span:nth-child(2){
         display: inline-block;
         width: 2.68rem;
         text-align: center;
      font-size: 0.16rem;
      font-weight: 500;
      color: rgba(0, 0, 0, 0.85);
      line-height: 0.24rem;
    }
    }
    .establicenter{
        margin: 0.25rem 0px 0.04rem 0px;
        padding: 0 0 0 0.45rem;
        div{
        display: inline-block;
        width: 0.24rem;
        height: 0.24rem;
        border-radius: 50%;
         margin-right: 0.08rem;
         text-align: center;
         font-size: 0.14rem;
         font-weight: 400;
         color: rgba(0, 0, 0, 0.45);
         line-height: 0.24rem;
         border: 1px solid  rgba(0, 0, 0, 0.45);
        }
        .establicenterson1{
            border: none;
            background:  #36CFC9;
            border: 0.01rem solid  #36CFC9;
            color: #FFFFFF;
        }
        .establicenterson2{
            border: none;
            background:  #FFFFFF;
            border: 0.01rem solid  #36CFC9;
            color:#36CFC9 ;
                        img{
              // margin-top: 0.05rem;
              width: 0.14rem;
              height: 0.14rem;
              vertical-align: middle;
            }
        }
        div:nth-child(1){
            margin: 0 0.08rem 0 0.4rem;
        }
        .solidx{
             width: 2.1rem;
             height: 0.01rem;
             background: rgba(0, 0, 0, 0.25);
             display: inline-block;
             border: none;
        }
        .solidxblue{
            background:#36CFC9 ;
        }
    }
    .establicenterbtm{
        display: flex;
        justify-content: space-between;
        font-size: 0.12rem;
        font-weight: 400;
        padding: 0 0.4rem;
        color: rgba(0, 0, 0, 0.45);
        line-height: 0.2rem;
        div:nth-child(1){
          margin-left: 0.24rem;
        }
        .establicenterbtmson{
         font-weight: 500;
         color: rgba(0, 0, 0, 0.85);
         line-height: 0.2rem;
        }
    }
    .btnesta{
        position: fixed;
        bottom: 0.24rem;
        display: flex;
        .btn1{
            display: none;
            border: none;
        }
        .btn1bh{
            width: 1.2rem;
            height: 0.4rem;
            background: #DEF9F4;
            border-radius: 4px;
            display: block;
            margin-left: 0.6rem;
            font-size: 0.14rem;
            font-weight: 400;
            color: #17A69F;
            line-height: 0.22rem;
        }
        .btn2{
            width: 1.6rem;
            height: 0.4rem;
            background: linear-gradient(180deg, #0BE7E5 0%, #099FA5 100%);
            box-shadow: 0px 0.15rem 0.45rem 0px rgba(85, 155, 158, 0.15);
            border-radius: 4px;
            border: none;
            font-size: 0.14rem;
            font-weight: 400;
            color: #FFFFFF;
            line-height: 0.22rem;
            margin-left: 1.08rem;
        }
        .btn2bh{
            width: 1.2rem;
            height: 0.4rem;
            background: linear-gradient(180deg, #0BE7E5 0%, #099FA5 100%);
            box-shadow: 0px 0.15rem 0.45rem 0px rgba(85, 155, 158, 0.15);
            border-radius: 4px;
             margin-left: 0.16rem;
        }
    }
}
