<template>
    <div class="jqddialogg" ref="yinones">
        <div class="box">
            <div class="boxtop"><span @click="yinnone">{{$t('shezhiye.quxiao')}}</span><span>{{$t('shezhiye.jiqirenmc')}}</span>
            <span @click="accomplish">{{$t('shezhiye.genggai')}}</span></div>
            <div class="boxbotm">
             <div class="passwords"><span>{{$t('shezhiye.mingcheng')}}</span><input type="text" v-model="textwork" @focus="focus1" @blur="blur1" @input="newrobtname" maxlength="20" placeholder="UVBOT"></div>
             <div class="boxhavi" :class="{'boxhavi1':boxhavi111}"></div>
             <div class="nobod" v-show="flgeas">{{$t('shezhiye.qbysrzw')}}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    flgespan: true,
    textwork: '',
    flgeas: false,
    textworkbeiyong: '',
    boxhavi111: false
  }),
  inject: ['reload'],
  props: ['responrbt'],
  created() {
    // console.log(this.$props.responrbt.robot_name)
    // console.log(this.textwork = this.$props.responrbt.robot_name)
    // this.textwork = this.$props.responrbt.robot_name
    // this.robotname = this.$props.responrbt.robot_name
    this.robotname()
  },
  methods: {
    // 失去焦点时候 让缓动画的线条颜色变回去 聚焦的时候让线出来
    blur1() {
      this.boxhavi111 = false
    },
    focus1() {
      this.boxhavi111 = true
    },
    newrobtname() {
      const reg = /^[\w.]{1,20}$/
      if (!reg.test(this.textwork)) {
        this.flgeas = true
      } else {
        this.flgeas = false
      }
    },
    async robotname() {
      try {
        const getrobtseting = {
          cmd: 'get_robot_settings',
          robot_name: true
        }
        const resrobot = await this.Api.Statuto(getrobtseting)
        console.log(resrobot)
        // 备用当点击取消时候 把前面获取的值赋值给自身 这样就不用局部刷新了
        this.textwork = resrobot.data.robot_name
        this.textworkbeiyong = resrobot.data.robot_name
      } catch {
      }
    },
    rotbotforname() {
      return this.textwork
    },
    yinnone() {
      this.$refs.yinones.style.display = 'none'
      this.flgeas = false
      this.textwork = this.textworkbeiyong
    },
    async accomplish() {
      if (this.textwork === '') {
        this.$Toast({
          message: this.$t('tsytongyong.dqsrweikong'),
          position: 'top'
        })
        return
      }
      if (this.responrbt.robot_name === this.textwork) {
        this.$Toast({
          message: this.$t('tsytongyong.jqrmingziwxg'),
          position: 'top'
        })
        return
      }
      if (this.flgeas) {
        this.$Toast({
          message: this.$t('tsytongyong.qanxuqiushuru'),
          position: 'top'
        })
        return
      }
      const verifypad = {
        cmd: 'update_robot_settings',
        robot_name: this.textwork
      }
      try {
        const resrobotname = await this.Api.Statuto(verifypad)
        console.log(resrobotname)
        this.$Toast({
          message: this.$t('tsytongyong.xgchenggong'),
          position: 'top'
        })
        // this.$Toast.success(this.$t('tsytongyong.xgchenggong'))
        localStorage.setItem('activeshezhiye', 3)
        this.reload()
      } catch {
        this.$Toast({
          message: this.$t('tsytongyong.qingqiushibai'),
          position: 'top'
        })
        // this.$Toast.fail(this.$t('tsytongyong.qingqiushibai'))
      }
    }
  }
}
</script>
<style lang="less" scoped>
.jqddialogg{
    display: none;
    width: 100%;
    height: 6.67rem;
    background: rgba(0, 0, 0, 0.6);
    position: absolute;
    top:0;
     z-index: 3;
    .box{
      .nobod{
        margin-top: 0.12rem;
        padding-left: 0.4rem;
        color: red;
      }
        width: 3.2rem;
        height: 3.2rem;
        background: #ffffff;
        border-radius: 8px;
        margin: 0.93rem 0.23rem 0px 0.24rem;
        .boxtop{
            padding: 0.16rem 0.24rem 0px 0.24rem;
            display: flex;
            justify-content: space-between;
            span:nth-child(1){
             font-size: 0.14rem;
             font-family: PingFangSC-Regular, PingFang SC;
             font-weight: 400;
             color: rgba(0, 0, 0, 0.65);
             line-height: 0.22rem;
            }
            span:nth-child(2){
            font-size: 0.14rem;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: rgba(0, 0, 0, 0.85);
            line-height: 0.22rem;
            }
            span:nth-child(3){
             font-size: 0.14rem;
             font-family: PingFangSC-Regular, PingFang SC;
             font-weight: 400;
             color: #1890FF;
             line-height: 0.22rem;
            }
        }
        .boxbotm{
            .boxhavi{
                margin-top: 0.09rem;
                height: 0.01rem;
                background: rgba(0, 0, 0, 0.15);
                transition: all 2s ease-in;
            }
            .boxhavi1{
              background: #36CFC9;
              transition: all 2s ease-in;
            }
            margin:0.76rem 0.24rem 0px 0.24rem;
            }
            .passwords{
                margin-top: 0.16rem;
                 span{
                display: inline-block;
                   font-size: 0.14rem;
                   font-family: PingFangSC-Regular, PingFang SC;
                   font-weight: 500;
                   color: rgba(0, 0, 0, 0.85);
                   line-height: 0.22rem;
                   margin-right: 0.18rem;
            }
            input{
                border: none;
            }
            }
    }
}
</style>
