                <van-circle v-model="currentRate" :rate="fordurtske" :speed="100" default size="1.3rem" :clockwise="falgeclo" :color="colorjbs" layer-color='none'>
                <!-- <h4><img src="../../../../assets/renwuto.png" alt=""></h4> -->
