              <!-- <div class="wcstatiscenterson"><div><b>N/A</b></div>
            <div>{{$t('baogaoxqing.sjxlv')}}</div></div> -->
              <!-- <div class="wcstatiscenterson"><div><b>N/A</b></div>
            <div>{{$t('baogaoxqing.gjcishu')}}</div></div> -->
        </div>
        <div class="fontyangshi">{{$t('homes.fangjian')}}:{{restaskxq.map_name?restaskxq.map_name:$t('homes.weizhi')}}</div>
        <div class="havid"></div>
         <div class="fontyangshi">{{$t('xiaosharw.caozuoren')}}:{{caozuorenmingzi}}</div>
        <div class="havid"></div>
                <div class="fontyangshi">{{$t('baogaoxqing.kssj')}}： <span>{{Moment(restaskxq.begin_time).format(('YYYY-MM-DD HH:mm:ss'))}}</span></div>
        <div class="havid"></div>
                <div class="fontyangshi">{{$t('baogaoxqing.jssj')}}： <span>{{Moment(restaskxq.end_time).format(('YYYY-MM-DD HH:mm:ss'))}}</span></div>
        <div class="havid"></div>
                <div class="fontyangshi">{{$t('baogaoxqing.yjxssc')}}：<span>{{hh}}:{{ss}}:{{mm}}</span></div>
        <div class="havid"></div>
                <div class="fontyangshi">{{$t('baogaoxqing.sjxssc')}}： <span>{{sjhh}}:{{sjss}}:{{sjmm}}</span></div>
        <div class="havid"></div>
                <div class="fontyangshi">{{$t('baogaoxqing.xsms')}}： <span>{{restaskxq.power_mode==='LOW'?$t('homes.diglmoshi'):$t('homes.quanglmoshi')}}</span></div>
        <div class="havid"></div>
               <div class="fontyangshi">{{$t('baogaoxqing.aqsz')}}： <span>{{restaskxq.door_sensor_enabled?$t('xiaosharw.menjinpai')+' ':''}}{{restaskxq.pir_enabled?$t('xiaosharw.PIR')+' ':''}}{{restaskxq.remote_ctrl_enabled?$t('xiaosharw.yaokongqi')+ ' ':''}}</span></div>
        <div class="havid"></div>
    </div>
</template>
<script>
import Moment from 'moment'
export default {
  props: ['restaskxq', 'resdetaixq'],
  data: () => ({
    currentRate: 0,
    falgeclo: false,
    colorjbs: {
      '0%': '#FFA970',
      '100%': '#FF090A'
    },
    dataresult: '',
    mm: '',
    ss: '',
    hh: '',
    sjmm: '',
    sjss: '',
    sjhh: '',
    task_id: '',
    caozuorenmingzi: '',
    wclxs: '',
    fordurtske: ''
  }),
  created() {
    // this.dataresulth = this.$props.resdetaixq.disinfect_dur_config
    // // this.Math.floor(Number(resdetaixq.disinfect_dur_config)/3600)
    // if ((Math.floor(Number(this.dataresulth) / 3600)) < 10) {
    //   this.dataresulth = (Math.floor(Number(this.dataresulth) / 3600)) + '0'
    //   console.log(this.dataresulth)
    // }
    this.task_id = this.$route.params.taskid
    this.disinfect()
    this.dicaozuoren()
  },
  methods: {
    Moment,
    async dicaozuoren() {
      try {
        const resczr = {
          cmd: 'get_tags',
          task_id: Number(this.task_id)
        }
        const resczrmingzi = await this.Api.gethistoryte(resczr)
        console.log(resczrmingzi)
        this.caozuorenmingzi = resczrmingzi.data.task_tags[0].value
      } catch {
      }
    },
    async disinfect() {
      try {
        const taskreq = {
          cmd: 'get_details',
          task_id: Number(this.task_id),
          get_point_logs: true
        }
        const restak = await this.Api.gethistoryte(taskreq)
        console.log(restak.data.point_records[0])
        this.fordurtske = ((restak.data.point_records[0].disinfect_dur_config - restak.data.point_records[0].disinfect_dur) / (restak.data.point_records[0].disinfect_dur_config)) * 100
        this.wclxs = Math.floor((restak.data.point_records[0].disinfect_dur / restak.data.point_records[0].disinfect_dur_config) * 100)
        this.mm = restak.data.point_records[0].disinfect_dur_config % 60
        this.ss = Math.floor((restak.data.point_records[0].disinfect_dur_config / 60)) % 60
        this.hh = Math.floor((restak.data.point_records[0].disinfect_dur_config / 3600))
        this.sjmm = Math.floor((restak.data.point_records[0].disinfect_dur)) % 60
        this.sjss = Math.floor((restak.data.point_records[0].disinfect_dur / 60)) % 60
        this.sjhh = Math.floor((restak.data.point_records[0].disinfect_dur) / 3600)
        if (this.mm < 10) {
          this.mm = '0' + this.mm
        }
        if (this.ss < 10) {
          this.ss = '0' + this.ss
        }
        if (this.hh < 10) {
          this.hh = '0' + this.hh
        }
        if (this.sjss < 10) {
          this.sjss = '0' + this.sjss
        }
        if (this.sjmm < 10) {
          this.sjmm = '0' + this.sjmm
        }
        if (this.sjhh < 10) {
          this.sjhh = '0' + this.sjhh
        }
      } catch {
      }
    }
  }
}
</script>
