<template>
    <div class="compute">
        <div class="compubtn">
        <!-- <van-checkbox v-model="rdflage" shape="square" checked-color="#099FA5" disabled v-show="false"></van-checkbox> -->
        <img src="../../../assets/Icon／redianms.png" alt=""> <span>{{$t('shezhiye.redmoshi')}}</span>  <button class="butn1"  @click="dialogg">{{$t('shezhiye.shezhicanshu')}}</button>
        </div>
        <div class="compubtn">
        <img src="../../../assets/Icon／wangka.png" alt=""> <span>{{$t('shezhiye.wangsmoshi')}}</span>  <button class="butn1"  @click="wkdialogg">{{$t('shezhiye.shezhicanshu')}}</button>
        </div>
        <!-- <div><input type="text" v-model="doorsener" @input="zhengzejy"><button @click="tijiao">确认</button></div>
        <div v-show="falge">当前输入不正确请输入12位16进制(a-f,A-F,0-9)</div> -->
    </div>
</template>
<script>
export default {
  data: () => ({
    doorsener: '',
    falge: false
  }),
  created() {
  },
  methods: {
    zhengzejy() {
      const reg = /^[0-9a-fA-F]{12}$/
      if (!reg.test(this.doorsener)) {
        this.falge = true
      } else {
        this.falge = false
      }
    },
    tijiao() {
      if (this.doorsener === '') {
        alert('请不要输入为空')
        return
      }
      if (this.falge) {
        alert('请按要求输入')
        return
      }
      const newStr = this.doorsener.slice(0, 2) + ':' + this.doorsener.slice(2, 4) + ':' + this.doorsener.slice(4, 6) + ':' + this.doorsener.slice(6, 8) + ':' + this.doorsener.slice(8, 10) + ':' + this.doorsener.slice(10, 12)
      console.log(newStr)
    },
    // 点击网卡或者热点模式 显示对应的框
    dialogg() {
      this.$emit('nordi', '')
    },
    wkdialogg() {
      this.$emit('nowki', '')
    }
  }
}
</script>
