<script>
export default {
  data: () => ({
    robtname: '',
    stastsj: '',
    samm: '00',
    sass: '00',
    sshh: '00'
  }),
  // 接受父组件传来的值渲染
  props: ['robots', 'Robotversion'],
  created() {
    this.robotname()
    this.getstat()
  },
  methods: {
    async getstat() {
      try {
        const resgetsta = await this.Api.getstatsshuju(localStorage.getItem('token'))
        console.log(resgetsta)
        this.stastsj = resgetsta.data
        this.samm = resgetsta.data.sum_disinfect_dur % 60
        this.sass = (Math.floor(resgetsta.data.sum_disinfect_dur / 60)) % 60
        this.sshh = (Math.floor(resgetsta.data.sum_disinfect_dur / 3600))
        if (this.sass < 0) {
          this.sass = 0
        }
        if (this.sshh < 0) {
          this.sshh = 0
        }
        if (this.samm < 9) {
          this.samm = '0' + this.samm
        }
        if (this.sass < 9) {
          this.sass = '0' + this.sass
        }
        if (this.sshh < 9) {
          this.sshh = '0' + this.sshh
        }
        console.log(this.samm, this.sass, this.sshh)
      } catch {
      }
    },
