    // 获取机器人名字
    async robotname() {
      try {
        const gettings = {
          cmd: 'get_robot_settings',
          robot_name: true
        }
        const resname = await this.Api.Statuto(gettings)
        console.log(resname)
        this.robtname = resname.data.robot_name
      } catch {
      }
    }
  }
}
