<template>
    <div class="diagnose ">
    <div  @click="tztop" @touchstart="dian1=true" @touchend="dian1=false" :class="{diagtwocolor:dian1}">
    <div class="dianoseson ">
    <img src="../../../assets/IconsFilleddengguangshouming.png" alt="">
    <div class="tworema">
        <div>{{modemal==='MANUAL'?$t('homes.dyzdengguan'):$t('homes.shangdengguanshouming')}}</div>
        <div v-if="topbi>0">{{$t('richangweihu.yjksy')}}: {{Math.floor(topdengguan/60)}}h</div>
        <div v-else>{{$t('xinzeng.ygsmydq')}}</div>
        </div>
        <div class="threeimg" v-if="topbi>20"><div class="diansoncolor" :class="{diansotwocolor:topbi>20}">{{topbi}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        <div class="threeimg" v-else-if="5<topbi&&topbi<=20"><div class="diansoncolor" :class="{diansothreecolor:5<=topbi&&topbi<=20}">{{topbi}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        <div class="threeimg" v-else-if="topbi<=5"><div class="diansoncolor">{{topbi}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        </div>
    </div>
    <div @click="tzbot" @touchstart="dian2=true" @touchend="dian2=false" :class="{diagtwocolor:dian2}">
            <div class="dianoseson" ><img src="../../../assets/IconsFilledibudengguan.png" alt="">
    <div class="tworema">
        <div>{{modemal==='MANUAL'?$t('homes.derzudengguan'):$t('homes.xiadengguangshouming')}}</div>
        <div v-if="dibu>0">{{$t('richangweihu.yjksy')}}: {{Math.floor(botdenguan/60)}}h</div>
        <div v-else>{{$t('xinzeng.ygsmydq')}}</div>
        </div>
        <!-- <div class="threeimg"><div class="diansoncolor" :class="{diansotwocolor:dibu>20}">{{dibu}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div> -->
        <div class="threeimg" v-if="dibu>20"><div class="diansoncolor" :class="{diansotwocolor:dibu>20}">{{dibu}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        <div class="threeimg" v-else-if="5<dibu&&dibu<=20"><div class="diansoncolor" :class="{diansothreecolor:5<=dibu&&dibu<=20}">{{dibu}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        <div class="threeimg" v-else-if="dibu<=5"><div class="diansoncolor">{{dibu}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        </div>
    </div>
    <div @click="cpsyla" @touchstart="dian3=true" @touchend="dian3=false" :class="{diagtwocolor:dian3}">
    <div class="dianoseson"><img src="../../../assets/IconsFilledcipan.png" alt="">
    <div class="tworema">
        <div>{{$t('richangweihu.cipan')}}</div>
        <div v-if="cipan>0">{{$t('richangweihu.cprlsy')}}: {{Math.floor(disk_space/1000)}}G</div>
        <div v-else>{{$t('xinzeng.cckjyjl')}}</div>
        </div>
        <!-- <div class="threeimg"><div class="diansoncolor" :class="{diansotwocolor:cipan>20}">{{cipan==='NaN'?0:cipan}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div> -->
        <div class="threeimg" v-if="cipan>20"><div class="diansoncolor" :class="{diansotwocolor:cipan>20}">{{cipan}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        <div class="threeimg" v-else-if="5<cipan&&cipan<=20"><div class="diansoncolor" :class="{diansothreecolor:5<=cipan&&cipan<=20}">{{cipan}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        <div class="threeimg" v-else-if="cipan<=5"><div class="diansoncolor">{{cipan}}%</div><span><img src="../../../assets/youbianjiao2.png" alt=""></span></div>
        </div>
    </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    disk_space: '',
    topdengguan: '',
    botdenguan: '',
    cipan: '',
    topbi: '',
    dibu: '',
    modemal: '',
    dian1: false,
    dian2: false,
    dian3: false
  }),
  created() {
    this.dcdl()
    this.modes()
  },
  methods: {
    tztop() {
      this.$router.push(`/topdg/${this.topbi}/${this.modemal}/${this.topdengguan}`)
    },
    tzbot() {
      this.$router.push(`/botdg/${this.dibu}/${this.modemal}/${this.botdenguan}`)
    },
    cpsyla() {
      this.$router.push(`/cprlbl/${this.cipan}/${this.modemal}/${this.disk_space}`)
    },
    async dcdl() {
      try {
        const resdl = {
          cmd: 'get_component_status',
          disk_space: true,
          uv_lamps: true
        }
        const resqdl = await this.Api.Statuto(resdl)
        console.log(resqdl)
        this.disk_space = resqdl.data.disk_space[0]
        console.log(resqdl.data.disk_space[0], resqdl.data.disk_space[1])
        // this.cipan = resqdl.data.disk_space[0] / resqdl.data.disk_space[1]
        this.topbi = Math.floor(((resqdl.data.uv_lamps[0].status[1] - resqdl.data.uv_lamps[0].status[0]) / resqdl.data.uv_lamps[0].status[1]) * 100)
        this.topdengguan = (resqdl.data.uv_lamps[0].status[1] - resqdl.data.uv_lamps[0].status[0])
        this.botdenguan = (resqdl.data.uv_lamps[1].status[1] - resqdl.data.uv_lamps[1].status[0])
        this.dibu = Math.floor(((resqdl.data.uv_lamps[1].status[1] - resqdl.data.uv_lamps[1].status[0]) / resqdl.data.uv_lamps[1].status[1]) * 100)
        if (this.disk_space < 0) {
          this.disk_space = 0
        }
        if (this.botdenguan < 0) {
          this.botdenguan = 0
        }
        if (this.topdengguan < 0) {
          this.topdengguan = 0
        }
        if (this.topbi < 0) {
          this.topbi = 0
        }
        if (this.dibu < 0) {
          this.dibu = 0
        }
        this.cipan = Math.floor((this.disk_space / resqdl.data.disk_space[1]) * 100)
        if (isNaN(this.cipan)) {
          this.cipan = 0
        }
      } catch {
      }
    },
    async modes() {
      try {
        const huoqumoshi = {
          cmd: 'get_robot_info'
        }
        const resmode = await this.Api.Statuto(huoqumoshi)
        this.modemal = resmode.data.robot_mode
      } catch {
      }
    }
  }
}
</script>
<style lang="less" scoped>
.diagnose{
    background: #ffffff;
    margin-top: 0.2rem;
    height: 5.62rem;
    .diagtwocolor{
      background: #E6FFFB;
    }
    .dianoseson{
        display: flex;
       width: 3.28rem;
       padding: 0.2rem 0;
       margin-left: 0.24rem;
       border-bottom: 0.01rem solid rgba(0, 0, 0, 0.15);
       >img{
           width: 0.4rem;
           height: 0.4rem;
       }
       .tworema{
           width: 2rem;
           margin: 0 0.08rem;
           >div:nth-child(1){
           height: 0.22rem;
           font-size: 0.14rem;
           font-family: PingFangSC-Regular, PingFang SC;
           font-weight: 400;
           color: rgba(0, 0, 0, 0.85);
           line-height: 0.22rem;
           }
           >div:nth-child(2){
           font-size: 0.1rem;
           font-family: PingFangSC-Regular, PingFang SC;
           font-weight: 400;
           color: rgba(0, 0, 0, 0.65);
           line-height: 0.2rem;
           }
       }
       .threeimg{
           display: flex;
        .diansoncolor{
          margin-top: 0.1rem;
           width: 0.49rem;
           height: 0.26rem;
           background: #FFF1F0;
           border-radius: 2px;
           font-size: 0.14rem;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #FF4D4F;
           border: 0.01rem solid #FFA39E;
           line-height: 0.26rem;
           text-align: center;
       }
       .diansotwocolor{
        background: rgba(194, 254, 221, 0.29);
        border: 0.01rem solid #59BF9F;
        color: #17A69F;
       }
       .diansothreecolor{
        background: rgba(255, 227, 132, 0.29);
        border: 0.01rem solid yellow;
        color:#FF9912;
       }
       img{
           width: 0.16rem;
           height: 0.16rem;
           margin-left: 0.15rem;
           margin-top: 0.16rem;
       }
       }
    }
}
</style>
