<template>
    <div class="gcmsshezhiqu">
      <Upgrdtssc @upgrdtssc="upgrdtssc" @upgrdtycl="upgrdtycl" />
        <div class="navbar"><span @click="onClickLeft"><img src="../../../assets/zuojianjiaoo.png" alt=""></span><span>{{$t('gcmoshi.gongchengms')}}</span></div>
     <div class="zhuyemian" v-show="falgea">
         <!-- <div><span>工程模式Pir:</span><input type="text" v-model.trim="textareavaluepir" placeholder="暂无数据，请输入内容并提交">
        </div>
         <div><span>工程模式wifi:</span><input type="text" v-model.trim="textareavaluewifi" placeholder="暂无数据，请输入内容并提交">
        </div>
         <div><span>工程模式Al:</span><input type="text" v-model.trim="textareavalueal" placeholder="暂无数据，请输入内容并提交">
        </div> -->
        <table>
            <thead><tr><th>{{$t('gcmoshi.gcmsshezhi')}}</th><th>{{$t('gcmoshi.shezhidezhi')}}</th></tr></thead>
            <tbody>
            <tr><td>{{$t('gcmoshi.gcpir')}}:</td><td><input type="text" v-model.trim="textareavaluepir" :placeholder="$t('gcmoshi.zwsjqxg')"></td></tr>
            <tr><td>{{$t('gcmoshi.gcwifi')}}:</td><td class="wangluoshezhi"><div><span>{{textareavaluewifi?textareavaluewifi:$t('gcmoshi.zwsjdjxlk')}}</span><span @click="wififlge=!wififlge">
              <img v-show="!wififlge" src="../../../assets/gouxuan.png" alt="">
              <img v-show="wififlge" src="../../../assets/shangla.png" alt="">
              </span></div><ul v-show="wififlge"><li @click="wifitext(ele)" v-for="(ele, index) in wifixuanze" :key="index">{{ele}}</li></ul></td></tr>
            <!-- <tr><td>{{$t('gcmoshi.gcwifi')}}:</td><td><input type="text" v-model.trim="textareavaluewifi" :placeholder="$t('gcmoshi.zwsjqxg')"></td></tr> -->
            <tr><td>{{$t('gcmoshi.gcalrl')}}:</td><td><input type="text" v-model.trim="textareavalueal" :placeholder="$t('gcmoshi.zwsjqxg')"></td></tr>
             <tr><td>{{$t('gcmoshi.lanyapeizhi')}}</td></tr>
            <tr><td>{{$t('gcmoshi.scsaomiaoT1')}}:</td><td><input type="text" v-model.trim="lanyasaomiao1" :placeholder="$t('gcmoshi.zwsjqxg')"></td></tr>
            <tr><td>{{$t('gcmoshi.scsaomiaoT2')}}:</td><td><input type="text" v-model.trim="lanyasaomiao2" :placeholder="$t('gcmoshi.zwsjqxg')"></td></tr>
            <!-- tina add -->
            <tr><td>{{$t('gcmoshi.lymkgl')}}:</td><td class="wangluoshezhi"><div><span>{{textareavaluebluetooth?textareavaluebluetooth:$t('gcmoshi.zwsjdjxlk')}}</span><span @click="bluetooth=!bluetooth">
              <img v-show="!bluetooth" src="../../../assets/gouxuan.png" alt="">
              <img v-show="bluetooth" src="../../../assets/shangla.png" alt="">
              </span></div><ul v-show="bluetooth"><li @click="bluetoothtext(ele)" v-for="(ele, index) in bluetoothxuanze" :key="index">{{ele}}</li></ul></td></tr>

            <tr><td>{{$t('gcmoshi.uploadPatch')}}:</td><td><div class="upload-patch"><van-uploader accept='*/*' :after-read="afterRead">
            <van-button  class="upload-patch-btn" type="primary">{{$t('richangweihu.scwenjian')}}</van-button>
            </van-uploader></div></td></tr>
            <!-- tina add end-->
            </tbody>
        </table>
        <button @click="gongchengmoshi" :disabled="disblflge">{{$t('gcmoshi.gcmstjxg')}}</button>
        <div class="upan">{{$t('gcmoshi.Upan')}}:<button @click="upannew">{{$t('gcmoshi.xiufu')}}</button></div>
     </div>
     <div class="yanzhengceng" v-show="!falgea">
         <div>{{$t('gcmoshi.mmyanz')}}:<input type="password" v-model.trim="mima" :placeholder="$t('gcmoshi.qdjsrmm')"></div>
         <div class="diceng"><button @click="onClickLeft">{{$t('xiaosharw.quxiao')}}</button><button @click="yanzhengmm" :disblflge="disblflge">{{$t('xiaosharw.queren')}}</button></div>
          <div class="yincangceng" v-show="disblflge">
          <div>{{$t('xinzeng.zzxggcms')}}</div>
        </div>
         </div>
      <Upgrdtssc ref="upgrdtssc" />
    </div>
</template>
<script>
import Aixos from 'axios'
import Upgrdtssc from '../../maintain/upgrdtssc'
export default {
  components: {
    Upgrdtssc
  },
  data: () => ({
    mima: '',
    textareavaluepir: '',
    textareavaluewifi: '',
    textareavalueal: '',
    textareavaluebluetooth: '',
    falgea: false,
    disblflge: false,
    wifixuanze: [36, 40, 44, 48],
    bluetoothxuanze: [6.5, 10],
    wififlge: false,
    bluetooth: false,
    lanyasaomiao1: '',
    lanyasaomiao2: ''
  }),
  inject: ['reload'],
  created() {
  },
  methods: {
    async upannew() {
      const requpan = {
        cmd: 'repair_u_disk'
      }
      try {
        const resup = await this.Api.Hqshezhigcms(requpan)
        console.log(resup)
      } catch (error) {
        console.log(error)
      }
    },
    upgrdtycl() {
      this.$refs.upgrdtssc.$el.style.display = 'none'
    },
    async afterRead(file) {
      const uploadImg = await this.unlaodingwj(file.file)
      console.log(uploadImg, 111)
    },
    upgrdtssc() {
      this.$refs.upgrdtssc.$el.style.display = 'block'
    },
    unlaodingwj(file) {
      // this.$emit('upgrdtssc', '')
      this.upgrdtssc()
      const params = new FormData()
      params.append('upload_file', file)
      console.log('aafffa')
      const config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      Aixos.post('/non_api/file_upload_unsafe', params, config, { transformRequest: [(data, headers) => ({ payload: data })] }).then(res => {
        console.log(res.data.file_handle)
        this.filename = res.data.file_handle
        const reSupgrade = {
          cmd: 'exec_program',
          file_handle: this.filename
          // test: true
        }
        console.log(reSupgrade)
        this.Api.Hqshezhigcms(reSupgrade).then(res => {
          this.upgrdtycl()
          // setTimeout(() => {
          //   window.location.href = `${window.location.protocol + '//' + window.location.hostname + ':1996'}`
          // }, 2000)
          // window.location.href = `${window.location.protocol + '//' + window.location.hostname + ':1996'}`
          this.$Toast({
            message: this.$t('tsytongyong.yxbdbcg'),
            position: 'top'
          })
        }).catch(err => {
          console.log(err)
          // this.$emit('upgrdtycl', '')
          this.upgrdtycl()
          if (err.request.status === 411) {
            this.$Toast({
              message: this.$t('tsytongyong.sjbcuowu'),
              position: 'top'
            })
          } else if (err.request.status === 410) {
            this.$Toast({
              message: this.$t('tsytongyong.sjbbucz'),
              position: 'top'
            })
          } else if (err.request.status === 412) {
            this.$Toast({
              message: this.$t('tsytongyong.zxcxsb'),
              position: 'top'
            })
          } else if (err.request.status === 403) {
            this.$Toast({
              message: this.$t('tsytongyong.dqbuxunxcaozuo'),
              position: 'top'
            })
          } else if (err.request.status === 500) {
            this.$Toast({
              message: this.$t('tsytongyong.serverError'),
              position: 'top'
            })
          } else {
            this.$emit('upgrdtycl', '')
          }
        })
      })
    },
    wifitext(math) {
      this.textareavaluewifi = math
      this.wififlge = false
    },
    bluetoothtext(math) {
      this.textareavaluebluetooth = math
      this.bluetooth = false
    },
    async yanzhengmm() {
      if (this.mima === '') {
        this.$Toast({
          message: this.$t('tsytongyong.qbysrwk'),
          position: 'top'
        })
        return
      }
      const reqmmyz = {
        cmd: 'get_settings',
        pwd: this.mima
      }
      try {
        const resgcmshisj = await this.Api.Hqshezhigcms(reqmmyz)
        this.falgea = true
        console.log(resgcmshisj)
        if (resgcmshisj.data.settings === null) {
          console.log(1)
          this.textareavaluepir = 10
          this.textareavaluewifi = 44
          this.textareavalueal = 0.98
          this.lanyasaomiao1 = 60
          this.lanyasaomiao2 = 20
          this.textareavaluebluetooth = 6.5
        } else {
          console.log(2, resgcmshisj.data.settings)
          this.textareavaluepir = resgcmshisj.data.settings.pir.filter_time
          this.textareavaluewifi = resgcmshisj.data.settings.wifi['5g_channel']
          this.textareavalueal = resgcmshisj.data.settings.AI.detect_threshold
          this.lanyasaomiao1 = resgcmshisj.data.settings.doorsensor.T1
          this.lanyasaomiao2 = resgcmshisj.data.settings.doorsensor.T2
          this.textareavaluebluetooth = resgcmshisj.data.settings.bt.txpower
        }
      } catch (err) {
        if (err.request && err.request.status === 410) {
          this.$Toast({
            message: this.$t('tsytongyong.mmcw'),
            position: 'top'
          })
        } else {
          this.falgea = true
        }
      }
    },
    onClickLeft() {
      this.$router.go(-1)
    },
    async gongchengmoshi() {
      //   const reg = /^\w{1,3}$/
      const reg = /^[0-9]{1,3}$/
      if (!reg.test(Number(this.textareavaluewifi)) || !(Number(this.textareavaluewifi) > 0 && Number(this.textareavaluewifi) < 200)) {
        this.$Toast({
          message: this.$t('gcmoshi.gcmshwifiszsb'),
          position: 'top'
        })
        return
      }
      if (!(Number(this.textareavalueal) > 0 && Number(this.textareavalueal) < 1)) {
        this.$Toast({
          message: this.$t('gcmoshi.gcmshaiszsb'),
          position: 'top'
        })
        return
      }
      if (!reg.test(Number(this.textareavaluepir)) || !(Number(this.textareavaluepir) > 0 && Number(this.textareavaluepir) < 100)) {
        this.$Toast({
          message: this.$t('gcmoshi.gcmshpirszsb'),
          position: 'top'
        })
        return
      }
      if (!reg.test(Number(this.lanyasaomiao1)) || !(Number(this.lanyasaomiao1) > 9 && Number(this.lanyasaomiao1) < 601)) {
        this.$Toast({
          message: this.$t('gcmoshi.saomiaot1bhs'),
          position: 'top'
        })
        return
      }
      if (!reg.test(Number(this.lanyasaomiao2)) || !(Number(this.lanyasaomiao2) > 19 && Number(this.lanyasaomiao2) < 601)) {
        this.$Toast({
          message: this.$t('gcmoshi.saomiaot2bhs'),
          position: 'top'
        })
        return
      }
      if (!this.textareavaluebluetooth) {
        this.$Toast({
          message: this.$t('gcmoshi.bluetoothrequire'),
          position: 'top'
        })
        return
      }
      const textgcms = {
        cmd: 'update_settings',
        pwd: this.mima,
        settings: {
          version: '0.0.1',
          wifi: {
            '5g_channel': Number(this.textareavaluewifi)
          },
          pir: {
            filter_time: Number(this.textareavaluepir)
          },
          AI: {
            detect_threshold: Number(this.textareavalueal)
          },
          doorsensor: {
            T1: Number(this.lanyasaomiao1),
            T2: Number(this.lanyasaomiao2)
          },
          bt: {
            txpower: this.textareavaluebluetooth
          }
        }
      }
      this.disblflge = true
      try {
        const resseting = await this.Api.Hqshezhigcms(textgcms)
        this.disblflge = false
        this.$Dialog.alert({
          message: this.$t('gcmoshi.szgcmscg'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
        console.log(resseting)
        this.$router.push('/login')
      } catch {
        this.disblflge = false
        this.$Dialog.alert({
          message: this.$t('gcmoshi.szgcmssb'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
      }
    }
  }
}
</script>
<style lang="less" scoped>
