    font-size: 0.14rem;
    padding-top: 0.46rem;
    height: 6.21rem;
    position: relative;
    .upload-patch {
      width: 1.5rem;
      .upload-patch-btn {
        margin-left: 0 !important;
      }
    }
    .yincangceng{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 9;
    >div{
        // height: 0.3rem;
        width: 2rem;
        background: #ffffff;
        line-height: 0.3rem;
        margin: 2.5rem auto;
    }
}
    .yanzhengceng{
    margin: 0 0.5rem;
    background: #ffffff;
    line-height: 0.25rem;
    margin-top: 0.5rem;
    .diceng{
        margin-top: 0.1rem;
        display: flex;
        justify-content:space-between;
        button{
            background: none;
            border: 0.01rem solid #17A69F;
            color: #17A69F;
        }
    }
    }
     .navbar{
        background: #F5F8FA;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 0.48rem;
        line-height: 0.48rem;
        z-index: 100;
        img{
          width: 0.16rem;
          height: 0.16rem;
          margin-left: 0.18rem;
          vertical-align: middle;
        }
        span:nth-child(2){
          display: inline-block;
          width: 3.04rem;
          text-align: center;
          font-size: 0.16rem;
          font-family: PingFangSC-Medium, PingFang SC;
          font-weight: 500;
          color: rgba(0, 0, 0, 0.85);
        }
    }
    .zhuyemian{
        margin: 0 0.2rem;
        background: #ffffff;
        border-radius: 10px;
        margin-top: 0.5rem;
        table{
            input{
                width: 1.5rem;
            }
            .wangluoshezhi{
              position: relative;
              >ul{
                position: absolute;
                top: 0.28rem;
                right: 0;
                width: 0.5rem;
                text-align: center;
                border: 0.01rem solid #acacac;
                background: #ffffff;
                line-height: 0.22rem;
                z-index: 4;
                li{
                  border-bottom: 0.01rem solid #acacac;
                }
                li:nth-child(8){
                  border-bottom: none;
                }
              }
              span{
                display: inline-block;
              }
              span:nth-child(1){
                width: 1rem;
                margin-right: 0.2rem;
              }
              span:nth-child(2){
                width: 0.25rem;
                text-align: center;
                img{
                  width: 0.16rem;
                  height: 0.16rem;
                  vertical-align: middle;
                }
              }
            }
        }
     button{
         margin-left: 1.25rem;
         background: none;
         text-align: center;
         border: 0.01rem solid #17A69F;
         color: #17A69F;
         line-height: 0.25rem;
         margin-top: 0.2rem;
         margin-bottom: 0.2rem;
     }
     .upan{
       button{
         border: 0.01rem solid #acacac;
         color: #000000;
         margin-left: 0.25rem;
       }
     }
    }
}
</style>
