  :title="$t('baogaoxqing.baogaoxiangqing')"
  :left-text="''"
  left-arrow
  @click-left="onClickLeft"
  fixed
/> -->
<div class="navbar"><span @click="onClickLeft"><img src="../../assets/zuojianjiaoo.png" alt=""></span><span>{{$t('baogaoxqing.baogaoxiangqing')}}</span></div>
<van-tabs v-model="active" animated color="#36CFC9" line-width="0.56rem">
  <van-tab  :title="$t('baogaoxqing.tongji')">
    <Statistics :restaskxq="restaskxq" :resdetaixq="resdetaixq" />
  </van-tab>
   <!-- <van-tab  :title="$t('xinzeng.guiji')">
    <Tarck />
  </van-tab> -->
   <van-tab  :title="$t('baogaoxqing.mingxi')" >
    <Detail :restaskxq="restaskxq" />
  </van-tab>
   <van-tab :title="$t('baogaoxqing.luxiang')">
    <Videos  :restaskxq="restaskxq" />
  </van-tab>
</van-tabs>
    </div>
</template>
<script>
import Videos from './video'
import Detail from './detail'
import Statistics from './statistics'
// import Tarck from './tarck'
export default {
  data: () => ({
    active: 0,
    noguiji: false,
    task_id: '',
    restaskxq: '',
    resdetaixq: ''
  }),
  created() {
    this.task_id = this.$route.params.taskid
    this.gettaskjob()
  },
  methods: {
    async gettaskjob() {
      try {
        const detailsreq = {
          cmd: 'get_details',
          task_id: Number(this.task_id),
          get_point_logs: true
        }
        const taskreq = {
          cmd: 'get_task',
          task_id: Number(this.task_id)
        }
        const restaskfo = await this.Api.gethistoryte(taskreq)
        const resdetails = await this.Api.gethistoryte(detailsreq)
        this.restaskxq = restaskfo.data
        this.resdetaixq = resdetails.data.point_records[0]
        console.log(restaskfo)
        console.log(this.resdetaixq)
      } catch {
      }
    },
    onClickLeft() {
      this.$router.go(-1)
    }
  },
  components: {
    Videos,
    Detail,
    Statistics
    // Tarck
  }
}
</script>
