<template>
  <div class="details">
              <div class="detailstop">
           <img src="../../../assets/iconbgguanli.png" alt=""> {{restaskxq.task_name}}
        </div>
        <div class="detailcenter" v-for="(ele, index) in objlogrz" :key="ele.id">
            <div class="leftxian"></div>
            <div class="leftradius" :class="[{'lffraiushongse':ele.point_status==='ABORT'},{'leffradius':ele.point_status==='SKIP'}]"></div>
            <div class="detailcentertop"><span>{{$t('baogaoxqing.xsdian')}}: {{index+1}}</span> <img v-show="ele.point_status==='COMPLETE'" src="../../../assets/xqywanchengtu.png" alt="">
            <span v-if="ele.point_status==='COMPLETE'">{{Math.floor(ele.disinfect_dur_config/60)}}{{$t('xiaosharw.min')}}</span>
            <span v-else-if="ele.point_status==='ABORT'" class="abortcolor">{{$t('homes.zhongzhi')}}</span>
             <span v-else-if="ele.point_status==='SKIP'" class="abortcolor">{{$t('homes.zhongzhi')}}</span>
            </div>
            <p v-for="foele in ele.point_logs" :key="foele.id" :class="{pausesensor:foele.log_id==='pause_sensor:pir'||foele.log_id==='pause_sensor:face_detect'||foele.log_id==='pause_sensor:door_sensor'}">
            <span>{{Moment(foele.log_time).format(('YYYY-MM-DD HH:mm:ss '))}}</span>
            <span v-if="foele.log_id==='disinfect_begin'">{{$t('baogaoxqing.xsks')}}</span>
            <span v-else-if="foele.log_id==='pause_user'">{{$t('baogaoxqing.zt')}}</span>
            <span v-else-if="foele.log_id==='pause_sensor:pir'">{{$t('xinzeng.pirzt')}}</span>
            <span v-else-if="foele.log_id==='pause_sensor:face_detect'">{{$t('xinzeng.renliansbzt')}}</span>
            <span v-else-if="foele.log_id==='pause_sensor:door_sensor'">{{$t('xinzeng.mjpzt')}}</span>
            <span v-else-if="foele.log_id==='pause_sensor:door_sensor_disconnected'">{{$t('xinzeng.dksmmjp')}}</span>
            <span v-else-if="foele.log_id==='resume'">{{$t('baogaoxqing.hfxs')}}</span>
            <span v-else-if="foele.log_id==='complete'">{{$t('baogaoxqing.xswc')}}</span>
            <!-- tina newAdd -->
            <span v-else-if="foele.log_id==='record_video_fail'">{{$t('baogaoxqing.lzspsb')}}</span>
            <span v-else-if="foele.log_id==='take_pic_fail'">{{$t('baogaoxqing.pzsb')}}</span>
            <!-- tina newAdd end-->
            <span v-else-if="restaskxq.abort_reason==='SYSTEM_ERROR' || restaskxq.abort_reason==='BATTERY_LOW' || restaskxq.abort_reason==='SAFETY_SENSOR'">{{$t('xinzeng.beidzz')}}</span>
            <span v-else-if="restaskxq.abort_reason==='USER' || restaskxq.abort_reason==='RC' || restaskxq.abort_reason==='EMERGENCY_STOP'">{{$t('xinzeng.zdzz')}}</span>
            <br><span v-show="foele.log_id==='pause_sensor:pir'||foele.log_id==='pause_sensor:face_detect'||foele.log_id==='pause_sensor:door_sensor'|| foele.log_id==='pause_sensor:door_sensor_disconnected' &&foele.pictures.length>0">
              <img v-for="imgindex in foele.pictures" @click="nomeimgchakan($event,foele.pictures,index)" :src="`/non_api/task/picture?id=${imgindex}&App-Token=${token}`" alt="" :key="imgindex"></span>
            <!-- <img src="../../../assets/morenmap.png" alt="" @click="nomeimgchakan($event)"> -->
            </p>
        </div>
         <div class="imgfangda" v-show="imgfangdaf" @click="noneimg">
          <div ref="imgmarginleft">
            <img v-for="(foele, index) in imgfangdasrc" :src="`/non_api/task/picture?id=${foele}&App-Token=${token}`" alt="" class="yangshisrc" :key="index" @touchstart="touchstart" @touchmove='touchmove'>
          </div>
        </div>
  </div>
</template>
<script>
import Moment from 'moment'
export default {
  props: ['restaskxq'],
  data: () => ({
    task_id: '',
    objlogrz: [],
    token: localStorage.getItem('token'),
    imgfangdaf: false,
    imgfangdasrc: [],
    startPointX: 0,
    changePointX: 0,
    showIndex: 0
  }),
  created() {
    this.task_id = this.$route.params.taskid
    // 获取到信息后 在发送请求
    this.logrizhi()
  },
  methods: {
    Moment,
    touchmove(e) {
      if (this.startPointX === this.changePointX) {
        return
      }
      const currPointX = e.changedTouches[0].pageX
      const leftSlide = this.startPointX - currPointX
      if (leftSlide > 30 && this.showIndex < this.imgfangdasrc.length - 1) {
        this.show(++this.showIndex)
      } else if (leftSlide < -30 && this.showIndex > 0) {
        this.show(--this.showIndex)
      }
    },
    touchstart(e) {
      this.startPointX = e.changedTouches[0].pageX
    },
    show(index) {
      this.changePointX = this.startPointX
      this.$refs.imgmarginleft.style.marginLeft = -3.28 * index + 'rem'
    },
    noneimg() {
      this.imgfangdaf = false
    },
    // 点击图片把图片的src链接复制给大图
    nomeimgchakan (e, shuzu, index) {
      console.log(e.currentTarget.src, shuzu)
      this.imgfangdasrc = shuzu
      console.log(this.$refs.imgmarginleft)
      this.show(index)
      this.imgfangdaf = true
      this.showIndex = index
      // console.log(e.currentTarget.src)
      // this.imgfangdasrc = e.currentTarget.src
      // this.imgfangdaf = true
      // window.open(`/non_api/task/picture?id=${id}&App-Token=${this.token}`)
    },
    async logrizhi() {
      try {
        const detailsreq = {
          cmd: 'get_details',
          task_id: Number(this.task_id),
          get_point_logs: true
        }
        const resdetails = await this.Api.gethistoryte(detailsreq)
        console.log(resdetails.data.point_records)
        // 便利的数组 数据为
        this.objlogrz = resdetails.data.point_records
      } catch {
      }
    }
  }
}
</script>
<style lang="less" scoped>
.details{
    width: 3.28rem;
    height: 5.64rem;
    background: #ffffff;
    margin-left: 0.24rem;
    margin-top: 0.13rem;
    overflow: auto;
    position: relative;
    // .imgfangda{
    //   width: 100%;
    //   height: 100%;
    //   background: rgba(0, 0, 0, 0.65);
    //   position: absolute;
    //   left: 0;
    //   top: 0;
    //   .yangshisrc{
    //     width: 100%;
    //     height: 2.6rem;
    //   }
    // }
    .imgfangda{
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.65);
      position: absolute;
      overflow: hidden;
      left: 0;
      top: 0;
      >div{
        width:10.84rem;
        .yangshisrc{
        width: 3.28rem;
        height: 2.6rem;
      }
      }
    }
      .detailstop{
           font-size: 0.16rem;
           font-family: PingFangSC-Medium, PingFang SC;
           font-weight: 500;
           color: rgba(0, 0, 0, 0.85);
           line-height: 0.48rem;
        img{
            margin-left: 0.06rem;
            height: 0.36rem;
            width: 0.36rem;
            vertical-align: middle;
        }
    }
    .detailcenter{
        position: relative;
        height: 1.54rem;
        .leftxian{
            position: absolute;
            width: 0.01rem;
            height: 1.54rem;
            background: rgba(0, 0, 0, 0.15);
            left: 0.2rem;
            top: 0.14rem;
        }
        .leftradiusred{
          background: #FF3B30;
        }
        .leftradiusyellow{
          background: #FD9100;
        }
        .leftradius{
            position: absolute;
            border-radius: 50%;
            width: 0.08rem;
            height: 0.08rem;
            background: #37B069;
            left: 0.165rem;
            top: 0.08rem;
        }
        .leffradius{
          background: rgba(255, 169, 112, 1);
        }
        .lffraiushongse{
          background: rgba(255, 0, 0, 1);
        }
        .detailcentertop{
            display: flex;
                height: 0.22rem;
                font-size: 0.14rem;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: rgba(0, 0, 0, 0.85);
                line-height: 0.22rem;
                .abortcolor{
                  color: red;
                }
            span:nth-child(1){
                width: 2.3rem;
                margin-left: 0.32rem;
            }
            img{
                padding-top: 0.04rem;
                width: 0.16rem;
                height: 0.16rem;
            }
        }
        p{
             height: 0.2rem;
             font-size: 0.12rem;
             font-family: PingFangSC-Regular, PingFang SC;
             font-weight: 400;
             color: rgba(0, 0, 0, 0.65);
             line-height: 0.2rem;
             margin: 0.02rem 0 0.03rem 0.32rem;
        }
        .pausesensor{
      height: 0.65rem;
      img{
        width: 0.45rem;
        height: 0.45rem;
        margin-left: 0.05rem;
      }
    }
    }
}
</style>
