        </div>
</template>
<script>
export default {
  data: () => ({
    currentRate: 0,
    falgeclo: false,
    colorjbs: {
      '0%': '#23FAFF',
      '100%': '#04D2D7'
    },
    moredopowd: '',
    mapname: '',
    hh: '',
    ff: '',
    mm: '',
    sjhh: '',
    sjff: '',
    sjmm: ''
  }),
  props: ['recordstatus'],
  created() {
    // 此页面完成页面把获取到的数据渲染
    // this.gettime()
    // this.hh = this.$refs.recordstatus.disinfect_dur
    console.log(this.$props.recordstatus.disinfect_dur)
    this.hh = Math.floor(this.$props.recordstatus.disinfect_dur / 3600)
    if (this.hh < 10) {
      this.hh = '0' + this.hh
    }
    this.ff = Math.floor(this.$props.recordstatus.disinfect_dur / 60 % 60)
    if (this.ff < 10) {
      this.ff = '0' + this.ff
    }
    this.mm = Math.floor(this.$props.recordstatus.disinfect_dur % 60)
    if (this.mm < 10) {
      this.mm = '0' + this.mm
    }
    this.sjhh = Math.floor((this.$props.recordstatus.end_time - this.$props.recordstatus.begin_time) / 3600 / 1000)
    if (this.sjhh < 10) {
      this.sjhh = '0' + this.sjhh
    }
    this.sjff = Math.floor((this.$props.recordstatus.end_time - this.$props.recordstatus.begin_time) / 60 / 1000) % 60
    if (this.sjff < 10) {
      this.sjff = '0' + this.sjff
    }
    this.sjmm = Math.floor((this.$props.recordstatus.end_time - this.$props.recordstatus.begin_time) / 1000 % 60)
    if (this.sjmm < 10) {
      this.sjmm = '0' + this.sjmm
    }
  },
  methods: {
  }
}
</script>
