import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/login1'
import Home from '../views/homes'
import Wizardpage from '../views/wizardpage/'
import Maintain from '../views/maintain'
import Setting from '../views/setting'
import Disntask from '../views/disinfectiontask'
import Countdown from '../views/countdown'
import Tutorialwizard from '../views/tutorialwizard'
import Management from '../views/management'
import Reportetils from '../views/reportdetails'
import Wipedat from '../views/wipedata'
import Topdg from '../views/maintain/diagnose/topdg'
import Botdg from '../views/maintain/diagnose/botdg'
import Cpsyl from '../views/maintain/diagnose/cpsyl'
import Printdy from '../views/reportdetails/statistics/printdy'
import Gcmsshezhiqu from '../views/login1/Gcmsshezhiqu'

Vue.use(VueRouter)

const routes = [
  // 路由重定向指向首页
  {
    path: '/',
    redirect: '/login'
  },
  // 登录页面
  {
    path: '/login',
    component: Login
  },
  // 首页
  {
    path: '/home/:id',
    component: Home
  },
  // 开机向导
  {
    path: '/wizardpage/:id',
    component: Wizardpage
  },
  // 日常维护页
  {
    path: '/maintain/:id',
    component: Maintain
  },
  // 设置页面
  {
    path: '/setting/:id',
    component: Setting
  },
  // 消杀详情页
  {
    path: '/disntask/:id',
    component: Disntask
  },
  // 倒计时页面
  {
    path: '/countdown',
    component: Countdown
  },
  // 向导教程页
  {
    path: '/tutorialwizard',
    component: Tutorialwizard
  },
  // 报告管理
  {
    path: '/management/:id',
    component: Management,
    name: 'management',
    meta: {
      keepAlive: true,
      isBack: false // 此组件需要被缓存
    }
  },
  {
    path: '/reportdetails/:taskid',
    component: Reportetils,
    name: 'reportdetails'
  },
  {
    path: '/wipedata',
    component: Wipedat
  },
  {
    path: '/topdg/:dcdl/:moshi/:syxs',
    component: Topdg
  },
  {
    path: '/botdg/:dcdl/:moshi/:syxs',
    component: Botdg
  },
  {
    path: '/cprlbl/:cpsy/:moshi/:syxs',
    component: Cpsyl
  },
  {
    path: '/printdy/:taskid',
    component: Printdy
  },
  {
    path: '/gcmsshezhi',
    component: Gcmsshezhiqu
  }
]
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const router = new VueRouter({
  routes
})

export default router
