<template>
    <div class="zzxs">
    <div class="contents2one">
        <span>{{falge2?'0':''}}{{ss}}:{{falge1?'0':''}}{{ff}}:{{mm}}</span>
      </div>
      <div class="contents2two">
     <ul>
       <li><img src="../../../../../assets/Icon／MAP.png" alt=""></li>
       <li>{{$t('homes.fangjian')}}</li>
       <li><div class="van-ellipsis">{{mapname?mapname:$t('homes.weizhi')}}</div></li>
     </ul>
      <ul>
       <li><img src="../../../../../assets/Icon／State.png" alt=""></li>
       <li>{{$t('homes.zhuangtai')}}</li>
       <li>{{$t('homes.jxzhong')}}</li>
     </ul>
      <ul>
       <li><img src="../../../../../assets/Icon／Mode.png" alt=""></li>
       <li>{{$t('homes.moshi')}}</li>
       <li v-if="modes==='LOW'">{{$t('homes.diglmoshi')}}</li>
       <li v-else-if="modes==='FULL'">{{$t('homes.quanglmoshi')}}</li>
     </ul>
      </div>
    </div>
</template>
<script>
export default {
  data: () => ({
    mm: '00',
    ff: 0,
    ss: 0,
    falge1: true,
    falge2: true,
    dur: '',
    timerrms: null,
    mapname: ''
  }),
  props: ['modes'],
  inject: ['reload'],
  created() {
    this.gettime()
    this.timerrms = setInterval(async() => {
      const pamsrams = {
        cmd: 'get_task_state'
      }
      try {
        // 原来和消杀详情页一样 如果是暂停转态把数据返回出去定时器 并且一秒刷新一次根据状态码判定
        const resmin = await this.Api.getotsstate(pamsrams)
        if (resmin.data.status === 'DISINFECT') {
          localStorage.setItem('shifoujxrw', true)
          this.mm = (this.dur * 60 - resmin.data.disinfect_dur) % 60
          if (this.mm < 10) {
            this.mm = '0' + this.mm
          }
          this.ff = Math.floor(((this.dur * 60 - resmin.data.disinfect_dur) / 60) % 60)
          console.log(this.ff, this.mm)
          if (this.ff > 9) {
            this.falge1 = false
          }
          this.ss = Math.floor((this.dur * 60 - resmin.data.disinfect_dur) / 3600)
          if (this.ss > 9) {
            this.falge2 = false
          }
        } else if (resmin.data.status === 'PAUSE') {
          clearInterval(this.timerrms)
          this.reload()
        } else if (resmin.data.status === 'RESUME_COUNT_DOWN' || resmin.data.status === 'START_COUNT_DOWN') {
          clearInterval(this.timerrms)
          this.reload()
        }
      } catch (err) {
        if (err.request.status === 404) {
          clearInterval(this.timerrms)
          this.reload()
        }
      }
    }, 1000)
  },
  methods: {
    async gettime() {
      const gettask = {
        cmd: 'get_task_config'
      }
      try {
        const restime = await this.Api.getotsstate(gettask)
        console.log(restime.data.dur)
        this.mapname = restime.data.map_name
        this.dur = restime.data.dur
      } catch {
      }
    }
  },
  beforeDestroy() {
    clearInterval(this.timerrms)
  }
}
</script>

<style lang="less" scoped>
     .contents2one{
                width: 1.92rem;
                height: 1.18rem;
               padding: 0.28rem 0.6rem 0.22rem 0.6rem;
               background: url('../../../../../assets/sjysbj.png') no-repeat 0.6rem 0.28rem;
                background-size:1.92rem 1.18rem;
                //  background-attachment: fixed;
               span{
                 display: block;
                 margin-top: 0.72rem;
                 text-align: center;
                 font-size: 0.25rem;
                 font-family: DINCondensed-Bold, DINCondensed;
                 font-weight: bold;
                 color: rgba(0, 0, 0, 0.85);
                 line-height: 0.3rem;
                 height: 0.3rem;
               }
              }
              .contents2two{
                display: flex;
                justify-content: space-around;
                ul{
                  width: 0.72rem;
                  height: 0.8rem;
                  background: #E6FFFB;
                  li{
                  margin-left: 0.08rem;
                  }
                  li:nth-child(2){
                   font-size: 0.12rem;
                   font-family: PingFangSC-Regular, PingFang SC;
                   font-weight: 400;
                   color: rgba(0, 0, 0, 0.45);
                   line-height: 0.2rem;
                  }
                  li:nth-child(3){
                     font-size: 0.1rem;
                     font-family: PingFangSC-Medium, PingFang SC;
                     font-weight: 500;
                     color: rgba(0, 0, 0, 0.85);
                     line-height: 0.2rem;
                     margin-left: 0.08rem;
                  }
                  img{
                    padding-top: 0.06rem;
                  }
                }
                ul:nth-child(2){
                  li:nth-child(3){
                    margin-left: 0.03rem;
                  }
                }
              }
</style>
