<template>
    <div class="xsfod1">
      <Zzxs v-if="mustatus==='DISINFECT'" :modes="modes" />
      <Ztxss v-else-if="mustatus==='PAUSE'" :modes="modes" />
    </div>
</template>
<script>
import Zzxs from './zzxs/index'
import Ztxss from './ztxss/index'
export default {
  data: () => ({
    modes: ''
  }),
  props: ['mustatus'],
  created() {
    console.log(this.mustatus)
    this.gettime()
  },
  methods: {
    async gettime() {
      const gettask = {
        cmd: 'get_task_config'
      }
      try {
        const restime = await this.Api.getotsstate(gettask)
        console.log(restime)
        this.modes = restime.data.power_mode
      } catch {
      }
    }
  },
  components: {
    Zzxs,
    Ztxss
  }
}
</script>
<style lang="less" scoped>
.xsfod1{
}
</style>
