<template>
    <div class="shiqulog" ref="nonshiqu">
        <van-picker
  :title="$t('shezhiye.shiqushezhi')"
  show-toolbar
  :columns="columns"
  @confirm="onConfirm"
  @cancel="onCancel"
  :confirm-button-text="$t('xiaosharw.queren')"
  :cancel-button-text="$t('xiaosharw.quxiao')"
/>
    </div>
</template>
<script>
export default {
  inject: ['reload'],
  data: () => ({
    columns: null
  }),
  created() {
    const _this = this
    _this.columns = [
      '(UTC-12:00) ' + this.$t('shezhiye.shiqujian12'),
      '(UTC-11:00) ' + this.$t('shezhiye.shiqujian11'),
      '(UTC-10:00) ' + this.$t('shezhiye.shiqujian10'),
      '(UTC-09:00) ' + this.$t('shezhiye.shiqujian9'),
      '(UTC-08:00) ' + this.$t('shezhiye.shiqujian8'),
      '(UTC-07:00) ' + this.$t('shezhiye.shiqujian7'),
      '(UTC-06:00) ' + this.$t('shezhiye.shiqujian6'),
      '(UTC-05:00) ' + this.$t('shezhiye.shiqujian5'),
      '(UTC-04:00) ' + this.$t('shezhiye.shiqujian4'),
      '(UTC-03:00) ' + this.$t('shezhiye.shiqujian3'),
      '(UTC-02:00) ' + this.$t('shezhiye.shiqujian2'),
      '(UTC-01:00) ' + this.$t('shezhiye.shiqujian1'),
      '(UTC+00:00) ' + this.$t('shezhiye.shiquzheng0'),
      '(UTC+01:00) ' + this.$t('shezhiye.shiqujia1'),
      '(UTC+02:00) ' + this.$t('shezhiye.shiqujia2'),
      '(UTC+03:00) ' + this.$t('shezhiye.shiqujia3'),
      '(UTC+04:00) ' + this.$t('shezhiye.shiqujia4'),
      '(UTC+05:00) ' + this.$t('shezhiye.shiqujia5'),
      '(UTC+06:00) ' + this.$t('shezhiye.shiqujia6'),
      '(UTC+07:00) ' + this.$t('shezhiye.shiqujia7'),
      '(UTC+08:00) ' + this.$t('shezhiye.shiqujia8'),
      '(UTC+09:00) ' + this.$t('shezhiye.shiqujia9'),
      '(UTC+10:00) ' + this.$t('shezhiye.shiqujia10'),
      '(UTC+11:00) ' + this.$t('shezhiye.shiqujia11'),
      '(UTC+12:00) ' + this.$t('shezhiye.shiqujia12'),
      '(UTC+13:00) ' + this.$t('shezhiye.shiqujia13'),
      '(UTC+14:00) ' + this.$t('shezhiye.shiqujia14')]
  },
  methods: {
    async onConfirm(value) {
      const UTctime1 = value.split('UTC')
      const UTctime2 = UTctime1[1].split(' ')
      const UTctime3 = UTctime2[0].split(')')
      console.log(UTctime3[0])
      try {
        const settaz = {
          cmd: 'update_time_settings',
          tz: UTctime3[0]
        }
        // 设置时区 成功以后给模态框 用户必须点击以后才能关闭
        const gettztime = await this.Api.Statuto(settaz)
        console.log(gettztime)
        // this.$Toast({
        //   message: this.$t('shezhiye.xgshiqucg'),
        //   position: 'top'
        // })
        this.$dialog.alert({
          title: this.$t('tsytongyong.xgchenggong'),
          message: this.$t('shezhiye.xgshiqucg'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
        localStorage.setItem('activeshezhiye', 2)
        this.reload()
      } catch {
        // this.$Toast({
        //   message: this.$t('shezhiye.xgshiqusb'),
        //   position: 'top'
        // })
        // this.$Dialog.alert({
        //   title: this.$t('shezhiye.cuowu'),
        //   message: this.$t('tsytongyong.qingqiushibai')
        // })
        this.$dialog.alert({
          title: this.$t('shezhiye.cuowu'),
          message: this.$t('tsytongyong.qingqiushibai'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
      }
    },
    onCancel(value, index) {
      this.$refs.nonshiqu.style.display = 'none'
    }
  }
}
</script>
<style lang="less" scoped>
.shiqulog{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    background: #ffffff;
    z-index: 1;
    display: none;
}
</style>
