<template>
    <div class="homes">
        <div class="banner">
            <div class="pngfont">
            <img src="../../assets/iconone.png" alt="" @click="fn3">
            <div>ADIBOT-S</div>
            </div>
        </div>
        <Guardian :robots="robots" :Robotversion="Robotversion" />
        <Foods :robots="Robotversion" :nameboot="robots"/>
        <Absoluts ref="absoluts" :id="id" />
         <div class="robtoimgs">
           <img src="../../assets/UVBOTIMG.png" alt="" v-show="statusrobto==='MANUAL'&&shifoujxrw==='false'">
           <img src="../../assets/uvbotxsjx.png" alt="" v-show="statusrobto==='MANUAL'&&shifoujxrw==='true'">
           <img src="../../assets/robort.png" alt="" v-show="statusrobto==='STANDARD' || statusrobto==='ADVANCED'"></div>
        <div class="overlay" v-show="firstlogin==='true'" @click="firstfuction">
        </div>
         <div class="qipao" v-show="firstlogin==='true'" @click="firstfuction"><img src="../../assets/shouciqipao.png" alt=""></div>
         <div class="qipaozi" v-show="firstlogin==='true'" @click="firstfuction">{{$t('homes.shouciqipao')}}</div>
         <upLevel ref="upLevel" title="升级"></upLevel>
    </div>
</template>
<script>
import Guardian from './Guardian/index'
import Foods from './foods/index'
import Absoluts from '../../components/tutorabsoluts/index'
import upLevel from '@/components/upLevel/index'
let testverson = false
export default {
  data: () => ({
    id: '',
    robots: '',
    Robotversion: '',
    Statuto: [],
    firstlogin: localStorage.getItem('shouciactive'),
    statusrobto: '',
    shifoujxrw: localStorage.getItem('shifoujxrw') ? localStorage.getItem('shifoujxrw') : 'false'
    // msg1: 20
  }),
  props: ['fristloginname'],
  beforeRouteEnter(to, from, next) {
    next(() => {
      if (from.fullPath === '/login') {
        testverson = true
      }
    })
  },
  created() {
    this.id = this.$route.params.id
    this.apiaggrs()
    this.robtimg()
    console.log(localStorage.getItem('shouciactive'))
    // const timer = setInterval(() => {
    //   this.msg1--
    //   if (this.msg1 < 1) {
    //     clearInterval(timer)
    //   }
    // }, 1000)
  },
  mounted() {
    if (testverson === true) {
      console.log('我是从LOGIN页面来的')
      const paradereq = {
        cmd: 'check_ota_upgrade'
      }
      const gettaskreq = {
        cmd: 'get_task_state',
        include_detailed_state: false
      }
      this.Api.getotsstate(gettaskreq).then(() => {
      }).catch((err) => {
        if (err.request.status === 404) {
          this.Api.Statuto(paradereq).then((toparaders) => {
            if (toparaders.data.code === 0) {
              if (toparaders.data.isForced) {
                this.$refs.upLevel.showDialog('now', toparaders.data)
                return
              }
              if (!localStorage.getItem('hasUpLevel') || localStorage.getItem('hasUpLevel') !== toparaders.data.newVersion) {
                this.$refs.upLevel.showDialog('noNow', toparaders.data)
              }
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      })
    }
  },
  methods: {
    firstfuction() {
      localStorage.setItem('shouciactive', false)
      this.firstlogin = false
    },
    async robtimg() {
      const imgrotb = {
        cmd: 'get_robot_info'
      }
      const noimg = await this.Api.Statuto(imgrotb)
      console.log(noimg.data.robot_mode)
      this.statusrobto = noimg.data.robot_mode
    },
    async apiaggrs() {
      const pamaros = {
        cmd: '',
        requests: [{
          URI: '/api/sys',
          msg: {
            cmd: 'get_robot_info'
          }
        },
        {
          URI: '/api/sys',
          msg: {
            cmd: 'get_version_info'
          }
        }]
      }
      try {
        // 用api聚合请求机器人名字 版本号还有生命状态
        const res = await this.Api.Apiaggr(pamaros)
        console.log(res.data)
        // 机器人名字 然后传到子组件
        this.robots = res.data.responses[0].msg
        // 机器人版本号
        this.Robotversion = res.data.responses[1].msg
        // this.firstlogin = res.data.responses[2].msg.first_login
      } catch {
      }
    },
    fn3() {
      this.$refs.absoluts.$el.style.display = 'block'
    }
  },
  components: {
    Guardian,
    Foods,
    Absoluts,
    upLevel
  }
  // watch: {
  //   '$route.path'(newval, oldval) {
  //     console.log(newval, 1111, oldval)
  //   }
  // }
}
</script>
<style lang="less" scoped>
.homes{
    background: #FFFFFF;
    box-shadow: 0px 0.15rem 0.45rem 0px rgba(85, 155, 158, 0.15);
    position: relative;
    height: 6.67rem;
    font-size: 0.12rem;
    .robtoimgs{
          position: absolute;
          width: 3.28rem;
          height: 2.4rem;
          left: 0.24rem;
          top: 0.45rem;
          background: #FFFFFF;
          border-radius: 8px;
          img{
            width: 100%;
            height: 100%;
          }
    }
    .banner{
        width: 100%;
        height: 2.84rem;
        background: url('../../assets/banner.png') no-repeat;
                background-size:cover;
        //  background-attachment: fixed;
                margin-bottom: 0.22rem;
        .pngfont{
          font-size: 0.14rem;
          height: 0.25rem;
          display: flex;
          justify-content: space-between;
          padding-top: 0.13rem;
          margin-left: 0.24rem;
          margin-right: 0.24rem;
            img{
                // margin: 0.13rem 0 0 0.32rem;
                vertical-align:bottom;
                width: 0.2rem;
                height: 0.2rem;
            }
            div{
                display: inline-block;
                width: 1.01rem;
                height: 0.22rem;
                font-size: 0.14rem;
                line-height: 0.22rem;
                text-align: right;
                // margin: 0.13rem 0 0 1.93rem;
            }
            color: #FFFFFF;
        }
    }
    .overlay{
      width: 100%;
      height: 100%;
      background: #000000;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0.6;
      z-index: 2;
    }
      .qipao{
        img{
          width: 2.65rem;
          height: 1.45rem;
          position: absolute;
          left: 0.15rem;
          top: -0.13rem;
          z-index: 2;
        }
      }
      .qipaozi{
        position: absolute;
        left: 0.8rem;
        top: 0.18rem;
        font-size: 0.12rem;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(0, 0, 0, 0.85);
        line-height: 0.2rem;
         white-space: normal;
         word-wrap:break-word;
         white-space:pre-wrap;
         z-index: 3;
         width: 1.5rem;
      }
}
</style>
