<template>
    <div class="setting">
      <div class="settingtop">
          <img src="../../assets/tongyongmu1.png" alt="" @click="blocka">
          <span>{{$t('shezhiye.xitongshezhi')}}</span>
      </div>
          <van-tabs v-model="activeshezhiye" animated color="#13A8A8" line-width="0.56rem" background="#F5F8FA" :swipeable="true">
           <van-tab :title="$t('shezhiye.wangluo')">
               <Compute @nordi="rdialo" @nowki="wkdialo "/>
            </van-tab>
         <van-tab :title="$t('shezhiye.anquan')">
              <Safety @safdialo="safdialo" :responrbt="responrbt" @bobaojiange="bobaojiange" @hfrtszchuxian="hfrtszchuxian" @lichangshezhi="lichangshezhi"/>
          </van-tab>
            <van-tab :title="$t('shezhiye.peidui')">
           <Bluetooth />
          </van-tab>
         <van-tab :title="$t('shezhiye.gengduo')">
               <Norswk ref="norswk" @nordialog="nordialog" @shiqulog="timeshiqulog" @timelog="logtime" :responrbt="responrbt" :dataversion="dataversion" />
        </van-tab>
          </van-tabs>
          <Notimelog ref="lotimes"/>
          <Absoluts ref="absoluts" :id="id" />
          <Rddialogg ref="rddialogg" :getshujuwkrd="getshujuwkrd" @wlyccchuxian="wlyccchuxian" @wlyccyincang="wlyccyincang" />
          <Wkdialogg ref="wkdialogg" :getshujuwkrd="getshujuwkrd" @wlyccchuxian="wlyccchuxian" @wlyccyincang="wlyccyincang" />
          <Safedialogg ref="safedialogg" />
          <Noshiqu ref="shiqulog" />
          <Nordialogg  ref="nordialogg" :responrbt="responrbt"  @nordname="nordname" />
          <Bobaojg ref="bobaots" />
          <Wldengdaic ref="wlycc" />
          <Hfrtrq ref="hfrtrqshezhi" />
          <Lichangjishu ref="lichangshezhio" />
    </div>
</template>
<script>
import Lichangjishu from './Lcjishu'
import Bluetooth from './Bluetooth'
import Hfrtrq from './huifushezhi'
import Wldengdaic from './wangluoddc'
import Compute from './compute/index'
import Absoluts from '../../components/tutorabsoluts/index'
import Rddialogg from './rddialogg/index'
import Wkdialogg from './wkdialogg/index'
import Safety from './safety/index'
import Safedialogg from './safedialogg/index'
import Norswk from './norswk/index'
import Nordialogg from './nodialogg/index'
import Notimelog from './Notimelog/index'
import Noshiqu from './Noshiqulog/index'
import Bobaojg from './bobaojg/index'
export default {
  data: () => ({
    activeshezhiye: localStorage.getItem('activeshezhiye') ? Number(localStorage.getItem('activeshezhiye')) : 0,
    id: '',
    responrbt: '',
    dataversion: '',
    getshujuwkrd: ''
  }),
  created() {
    this.id = this.$route.params.id
    this.getrobot()
    this.robotversion()
  },
  methods: {
    // 获取机器人名字
    async robotversion() {
      const versions = {
        cmd: 'get_robot_info'
      }
      const versionbot = await this.Api.Statuto(versions)
      console.log(versionbot.data.robot_mode)
      this.dataversion = versionbot.data.robot_mode
    },
    // 点击触发离场页面并传值
    lichangshezhi(valuse) {
      console.log(valuse)
      this.$refs.lichangshezhio.$el.style.display = 'block'
      this.$refs.lichangshezhio.morenzhiindex(valuse)
    },
    // 点击恢复设置页面并传值
    hfrtszchuxian(valuse) {
      this.$refs.hfrtrqshezhi.$el.style.display = 'block'
      this.$refs.hfrtrqshezhi.morenzhiindex(valuse)
    },
    // 隐藏
    wlyccyincang() {
      this.$refs.wlycc.$el.style.display = 'none'
    },
    // 出现
    wlyccchuxian() {
      this.$refs.wlycc.$el.style.display = 'block'
    },
    // 点击播报页面并传值
    bobaojiange(valuse) {
      this.$refs.bobaots.$el.style.display = 'block'
      this.$refs.bobaots.morenzhiindex(valuse)
    },
    // 时区页面显示
    timeshiqulog() {
      this.$refs.shiqulog.$el.style.display = 'block'
    },
    // 时间设置页面
    logtime() {
      this.$refs.lotimes.$el.style.display = 'block'
    },
    blocka() {
      this.$refs.absoluts.$el.style.display = 'block'
    },
    // 热点模式页面
    rdialo(value) {
      this.$refs.rddialogg.$el.style.display = 'block'
      this.getshujuwkrd = value
    },
    // 网卡模式页面
    wkdialo(value) {
      this.$refs.wkdialogg.$el.style.display = 'block'
      this.getshujuwkrd = value
    },
    safdialo() {
      this.$refs.safedialogg.$el.style.display = 'block'
    },
    nordialog() {
      this.$refs.nordialogg.$el.style.display = 'block'
    },
    nordname(newrobtoname) {
    // this.responrbt.robot_name = newrobtoname
      console.log(this.$refs.norswk.setname(newrobtoname))
    },
    // 已经无意义的获取值 因为只能获取第一个数据
    async getrobot() {
      try {
        const getrobtseting = {
          cmd: 'get_robot_settings',
          robot_name: true,
          remote_ctrl_enabled: true,
          door_sensor_enabled: true,
          volume: true,
          recharge_low_limit: true,
          lang: true
        }
        const resrobot = await this.Api.Statuto(getrobtseting)
        console.log(resrobot.data)
        this.responrbt = resrobot.data
      } catch {
      }
    }
  },
  components: {
    Wldengdaic,
    Compute,
    Absoluts,
    Rddialogg,
    Wkdialogg,
    Safety,
    Safedialogg,
    Norswk,
    Nordialogg,
    Notimelog,
    Noshiqu,
    Bobaojg,
    Hfrtrq,
    Bluetooth,
    Lichangjishu
  }
}
</script>
<style lang="less" scoped>
.setting{
         background: #F5F8FA;
         width: 100%;
         height: 100%;
         font-size: 0.14rem;
         .settingtop{
             padding-top: 0.13rem;
             img{
                 vertical-align:bottom;
                 margin-left: 0.24rem;
                 height: 0.24rem;
                 width: 0.24rem;
             }
             span{
               display: inline-block;
               width: 2.9rem;
               text-align: center;
             font-size: 0.16rem;
             font-family: PingFangSC-Medium, PingFang SC;
             font-weight: 600;
             color: rgba(0, 0, 0, 0.85);
             line-height: 0.24rem;
             }
         }
}
</style>
