<script>
import Moment from 'moment'
export default {
  props: ['restaskxq', 'resdetaixq'],
  data: () => ({
    currentRate: 0,
    falgeclo: false,
    colorjbs: {
      '0%': '#23FAFF',
      '100%': '#04D2D7'
    },
    task_id: '',
    mm: '',
    ss: '',
    hh: '',
    sjmm: '',
    sjss: '',
    sjhh: '',
    caozuorenmingzi: ''
  }),
  created() {
    this.task_id = this.$route.params.taskid
    // this.resdetaiils = this.$props.resdetaixq
    // console.log(this.$props.resdetaixq.disinfect_dur_config, this.resdetaiils.disinfect_dur_config)
    // this.mm = this.$props.resdetaixq.disinfect_dur_config % 60
    // this.ss = Math.floor((this.$props.resdetaixq.disinfect_dur_config / 60)) % 60
    // this.hh = Math.floor((this.$props.resdetaixq.disinfect_dur_config / 3600))
    // if (this.mm < 9) {
    //   this.mm = '0' + this.mm
    //   console.log(this.mm)
    // }
    // if (this.ss < 9) {
    //   this.ss = '0' + this.ss
    // }
    // if (this.hh < 9) {
    //   this.hh = String('0' + this.hh)
    // }
    // console.log(this.$props.resdetaixq)
    this.disinfect()
    this.dicaozuoren()
  },
  methods: {
    Moment,
    async dicaozuoren() {
      try {
        const resczr = {
          cmd: 'get_tags',
          task_id: Number(this.task_id)
        }
        const resczrmingzi = await this.Api.gethistoryte(resczr)
        console.log(resczrmingzi)
        this.caozuorenmingzi = resczrmingzi.data.task_tags[0].value
      } catch {
      }
    },
    async disinfect() {
      try {
        const taskreq = {
          cmd: 'get_details',
          task_id: Number(this.task_id),
          get_point_logs: true
        }
        // 实际运行的时间 一个总时间
        const restak = await this.Api.gethistoryte(taskreq)
        console.log(restak.data.point_records[0])
        this.mm = restak.data.point_records[0].disinfect_dur_config % 60
        this.ss = Math.floor((restak.data.point_records[0].disinfect_dur_config / 60)) % 60
        this.hh = Math.floor((restak.data.point_records[0].disinfect_dur_config / 3600))
        this.sjmm = Math.floor((restak.data.point_records[0].end_time - restak.data.point_records[0].begin_time) / 1000) % 60
        this.sjss = Math.floor((restak.data.point_records[0].end_time - restak.data.point_records[0].begin_time) / 60 / 1000) % 60
        this.sjhh = Math.floor((restak.data.point_records[0].end_time - restak.data.point_records[0].begin_time) / 3600 / 1000)
        if (this.mm < 9) {
          this.mm = '0' + this.mm
        }
        if (this.ss < 9) {
          this.ss = '0' + this.ss
        }
        if (this.hh < 9) {
          this.hh = '0' + this.hh
        }
        if (this.sjss < 9) {
          this.sjss = '0' + this.sjss
        }
        if (this.sjmm < 9) {
          this.sjmm = '0' + this.sjmm
        }
        if (this.sjhh < 9) {
          this.sjhh = '0' + this.sjhh
        }
      } catch {
      }
    }
  },
  mounted() {
  }
}
</script>
