<template>
  <div class="passwordycc" ref="passwordf">
    <div class="passdiv">
      <div class="qsrmima">{{$t('richangweihu.srmm')}}</div>
      <input type="password" :placeholder="$t('user.mima')" v-focus v-model="mathpass" @input="nonefot" maxlength="32">
      <div class="nobod" v-show="!flgeas"></div>
      <div class="nobod" v-show="flgeas">{{$t('tsytongyong.shifouanxushuru')}}</div>
      <div class="passbtom">
     <span @click="nonme">{{$t('xiaosharw.quxiao')}}</span> <span @click="jymima">{{$t('xiaosharw.queren')}}</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: () => ({
    mathpass: '',
    flgeas: false
  }),
  methods: {
    nonefot() {
      const reg = /^[\w]{6,32}$/
      if (!reg.test(this.mathpass)) {
        this.flgeas = true
      } else {
        this.flgeas = false
      }
    },
    async jymima() {
      if (this.flgeas) {
        this.$Toast({
          message: this.$t('tsytongyong.qanxuqiushuru'),
          position: 'top'
        })
        return
      }
      if (this.mathpass === '') {
        this.$Toast({
          message: this.$t('tsytongyong.qbysrwk'),
          position: 'top'
        })
        return
      }
      try {
        const verifypad = {
          cmd: 'verify_pwd',
          username: localStorage.getItem('usenameID'),
          pwd: this.mathpass
        }
        const resresult = await this.Api.getusername(verifypad)
        if (!(resresult.data.result)) {
          this.$Toast({
            message: this.$t('tsytongyong.mmcw'),
            position: 'top'
          })
        } else {
          // const dlchaxun = {
          //   cmd: 'get_component_status',
          //   battery_level: true,
          //   charge_cycles: false,
          //   disk_space: false,
          //   uv_lamps: false
          // }
          // const dlshu = await this.Api.Statuto(dlchaxun)
          // console.log(dlshu)
          // this.$Toast({
          //   message: this.$t('tsytongyong.qbzzgdl')
          // })
          try {
            const resetfactory = {
              cmd: 'factory_reset'
            }
            const resonfact = await this.Api.Statuto(resetfactory)
            console.log(resonfact)
            localStorage.setItem('nomewipedatas', true)
            localStorage.removeItem('token')
            this.$router.replace('/wipedata')
          } catch (err) {
            if (err.request.status === 403) {
              this.$Toast({
                message: this.$t('tsytongyong.zzzxxsrw')
              })
            }
          }
        }
      } catch (error) {
        console.log(error)
      }
    },
    nonme() {
      this.$refs.passwordf.style.display = 'none'
      this.mathpass = ''
      this.flgeas = false
    }
  },
  directives: {
    focus: {
      inserted(el) {
        el.focus()
      }
    }
  }
}
</script>
<style lang="less" scoped>
.passwordycc{
    height:6.67rem;
    width: 100%;
    background: rgba(0, 0, 0, 0.6);
    position: absolute;
    top: 0;
    z-index: 1;
    display: none;
    .passdiv{
      margin: 1.01rem 0 0 0.53rem;
      width: 2.7rem;
      height: 2rem;
      background: #ffffff;
      padding-top: 0.19rem;
      box-sizing: border-box;
      border-radius: 13px;
      .nobod{
        height: 0.4rem;
        color: red;
        margin-bottom: 0.14rem;
        padding: 0 0.1rem;
      }
      .passbtom{
         height: 0.42rem;
         border-top: 0.01rem solid rgba(0, 0, 0, 0.45);
         span{
           display: inline-block;
           width: 48%;
           text-align: center;
           height: 0.42rem;
           font-size: 0.16rem;
           font-family: PingFangSC-Regular, PingFang SC;
           font-weight: 400;
           color: rgba(0, 0, 0, 0.65);
           line-height: 0.42rem;
         }
         span:nth-child(1){
           border-right: 0.01rem solid rgba(0, 0, 0, 0.45) ;
         }
         span:nth-child(2){
          color: rgba(24, 144, 255, 1);
          width: 49%;
         }
      }
      input{
        width: 2.3rem;
        height: 0.28rem;
        background: #FFFFFF;
        border: 0.01rem solid #979797;
        margin-left: 0.13rem;
        margin-bottom: 0.12rem;
        padding-left: 0.1rem;
      }
      .qsrmima{
        margin-bottom: 0.17rem;
         height: 0.24rem;
         font-size: 0.16rem;
         font-family: PingFangSC-Medium, PingFang SC;
         font-weight: 500;
         color: rgba(0, 0, 0, 0.85);
         line-height: 0.24rem;
         text-align: center;
      }
    }
}
</style>
