<template>
    <div class="countdown">
        <div class="dingbuzi">
          <img src="../../assets/tongyongmu1.png" alt="" @click="fn3">
          <div>{{$t('xiaosharw.xsrw')}}</div>
        </div>
        <div class="conttop">
            <div>{{$t('daojishiyemian.renwumingcheng')}}：{{taskname}}</div>
                 <h3>{{$t('daojishiyemian.lirenwukshy')}}</h3>
        <h4><img src="../../assets/Countdown.gif" alt="">
        <span>{{msg}}</span></h4>
        <h5>{{$t('daojishiyemian.qingrenyuanlk')}}</h5>
        </div>
           <div class="bottomgaid">
        <div class="bottomgaidson"  @click="abotask">
            <img src="../../assets/tongyongstop.png" alt="">
            <div>{{$t('homes.zhongzhi')}}</div>
        </div>
    </div>
    <Absoluts ref="absoluts"  />
    </div>
</template>
<script>
import Absoluts from '../../components/tutorabsoluts'
export default {
  data: () => ({
    msg: '',
    taskname: '',
    timer: null,
    showtime: false
  }),
  inject: ['reload'],
  created() {
    // 设置定时器 每一秒执行一次 刷新状态
    this.timer = setInterval(async() => {
    // 获取现在的状态一直刷新 如果不是正在倒计时和恢复倒计时就立即跳转到消杀页面
      try {
        const params = {
          cmd: 'get_task_state',
          include_points: false
        }
        // 后台返回的状态码 跳转消杀页面并且清除定时器
        const res = await this.Api.getotsstate(params)
        this.taskname = res.data.task_name
        if (res.data.status === 'START_COUNT_DOWN' || res.data.status === 'RESUME_COUNT_DOWN') {
          this.msg = res.data.count_down_state.remain
          // 倒计时
        } else {
          const id = localStorage.getItem('usenameID')
          this.$router.replace(`/disntask/${id}`)
          clearInterval(this.timer)
          this.timer = null
        }
      } catch (err) {
        // 如果后台接受的状态码为404说明此时没有消杀任务就要跳回消杀详情页面
        if (err.request.status === 404) {
          clearInterval(this.timer)
          this.timer = null
          const id = localStorage.getItem('usenameID')
          this.$router.replace(`/disntask/${id}`)
        }
      }
    }, 1000)
  },
  methods: {
  // 发送终止消杀请求
    fn3() {
      this.$refs.absoluts.$el.style.display = 'block'
    },
    abotask() {
      // try {
      //   const abortask = {
      //     cmd: 'abort_task'
      //   }
      //   const resabort = this.Api.getotsstate(abortask)
      //   console.log(resabort)
      // } catch {
      // }
      this.$Dialog.confirm({
        title: this.$t('tsytongyong.xiaoshazhongzhi'),
        message: this.$t('tsytongyong.shifouzhongzhixs'),
        confirmButtonText: this.$t('xiaosharw.queren'),
        cancelButtonText: this.$t('xiaosharw.quxiao')
        // 如果确认退出以后发送请求删除后台Token并且本地Token也删除
        // 延时500毫秒
      }).then(() => {
        const abortask = {
          cmd: 'abort_task'
        }
        this.Api.getotsstate(abortask).then((res) => {
          console.log(res)
        })
        // this.reload()
      })
    }
  },
  components: {
    Absoluts
  },
  beforeDestroy() {
    clearInterval(this.timer)
    this.timer = null
  }
}
</script>
<style lang="less" scoped>
.countdown{
    width: 100%;
    height: 6.67rem;
    position: relative;
    padding-top: 0.16rem;
    box-sizing: border-box;
    font-size: 0.12rem;
    .dingbuzi{
      font-size: 0.15rem;
      margin-bottom: 0.1rem;
      img{
        width: 0.24rem;
        height: 0.24rem;
        margin-left: 0.24rem;
        vertical-align:middle;
      }
          div{
              display: inline-block;
              width: 2.8rem;
              text-align: center;
              font-size: 0.16rem;
              font-family: PingFangSC-Medium, PingFang SC;
              font-weight: 500;
              color: rgba(0, 0, 0, 0.85);
              line-height: 0.24rem;
          }
    }
    .conttop{
        width: 3.28rem;
        height: 3.44rem;
        background: #FFFFFF;
        box-shadow: 0px 0.15rem 0.45rem 0px rgba(85, 155, 158, 0.15);
        border-radius: 8px;
        margin: 0px 0px 0px 0.24rem;
        box-sizing: border-box;
        text-align: center;
        div{
          padding-top: 0.2rem;
        }
        h3{
        font-size: 0.14rem;
        font-weight: 600;
        }
        h4{
        font-size: 0.28rem;
        font-weight: 700;
        color: red;
        margin: 0;
        position: relative;
        img{
          width: 2rem;
        }
        span{
          position: absolute;
          top: 0.55rem;
          left: 0.64rem;
          width: 2rem;
          height: 2rem;
          font-size: 0.8rem;
          color:#13A8A8 ;
        }
        }
        h5{
             margin-top: 0.2rem;
        font-size: 0.14rem;
        font-weight: 600;
        }
    }
    .bottomgaid{
        position: fixed;
        bottom: 0;
        height: 0.72rem;
        background: #13A8A8;
        width: 100%;
        padding-top: 0.17rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-around;
        .bottomgaidson{
            text-align: center;
            img{
              width: 0.24rem;
              height: 0.24rem;
            }
            div{
                font-size: 0.12rem;
                font-family: PingFangSC-Medium, PingFang SC;
                font-weight: 500;
                color: #FFFFFF;
                line-height: 0.2rem;
            }
        }
    }
}
</style>
