<template>
<div class="rtrqhf" ref="rentirqhfsj">
<van-picker
  :title="$t('shezhiye.szrtzdhfsjycc')"
  show-toolbar
  :columns="columns"
  @confirm="onConfirm"
  @cancel="onCancel"
  :confirm-button-text="$t('xiaosharw.queren')"
  :cancel-button-text="$t('xiaosharw.quxiao')"
/>
<!-- <textarea name="" id="" cols="10" rows="10"></textarea> -->
    </div>
</template>
<script>
export default {
  data: () => ({
    // columns: null
    columns: [{
      values: [10, 20, 30, 40, 50, 60],
      defaultIndex: 0
    }]
  }),
  inject: ['reload'],
  created() {
    // const _this = this
    // _this.columns = [10, 20, 30, 40, 50, 60]
  },
  methods: {
    // 父组件点击的时候触发 把默认下标改成传过来的对应的下标值
    morenzhiindex(index) {
      // console.log(this.columns[0].values.indexOf(index))
      this.columns[0].defaultIndex = this.columns[0].values.indexOf(index)
    },
    onCancel(value, index) {
      // 点击取消的时候 把它隐藏起来
      this.$refs.rentirqhfsj.style.display = 'none'
    },
    async onConfirm(value) {
      console.log(value)
      try {
        const reqbbjg = {
          cmd: 'update_robot_settings',
          auto_resume_delay: value[0]
        }
        // 设置语音播报间隔为10,20,30，60
        const nmome = await this.Api.Statuto(reqbbjg)
        console.log(nmome)
        this.$Dialog.alert({
          message: this.$t('shezhiye.xgrqhfsjcg'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
        // 完成以后 设置页面的tab默认值为1 并且局部刷新一下
        localStorage.setItem('activeshezhiye', 1)
        this.reload()
      } catch {
        this.$Dialog.alert({
          title: this.$t('shezhiye.xgrqhfsjsb'),
          message: this.$t('tsytongyong.qingqiushibai'),
          confirmButtonText: this.$t('xiaosharw.wozhidaole')
        })
      }
    }
  }
}
</script>
<style lang="less" scoped>
.rtrqhf{
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    background: #ffffff;
    z-index: 1;
    display: none;
}
</style>
