<template>
    <div class="printdy">
        <h3><img @click="fanhui" src="../../../../assets/zuojianjiaoo.png" alt=""> <span>{{$t('dayinbaogao.dybaogao')}}</span></h3>
        <div id="pdfDom">
        <div class="jibenzhaiyaoo">
            <div><h4>{{$t('dayinbaogao.jibenzhaiyao')}}</h4></div>
            <div><span>{{$t('xiaosharw.xsrwmingcheng')}}:</span><span>{{jbzhaiyao.task_name}}</span></div>
            <div><span>{{$t('homes.zhuangtai')}}:</span><span v-if="jbzhaiyao.task_status==='COMPLETE'">{{$t('homes.wancheng')}}</span><span v-else-if="jbzhaiyao.task_status==='ABORT'">{{$t('homes.zhongzhi')}}</span></div>
            <div><span>{{$t('homes.fangjian')}}:</span><span>{{jbzhaiyao.map_name?jbzhaiyao.map_name:$t('homes.weizhi')}}</span></div>
            <div><span>{{$t('xiaosharw.caozuoren')}}:</span><span>{{caozuorenmingzi}}</span></div>
        </div>
           <div class="tongjixinxi">
            <h4>{{$t('dayinbaogao.tjixinxi')}}</h4>
            <div><span>{{$t('baogaoxqing.xsdian')}}:</span><span>1/1</span></div>
            <div><span>{{$t('baogaoxqing.wclv')}}:</span><span>{{xswcl}}%</span></div>
            <div><span>{{$t('baogaoxqing.gjcishu')}}:</span><span>N/A</span></div>
            <div><span>{{$t('baogaoxqing.kssj')}}:</span><span>{{Moment(jbzhaiyao.begin_time).format(('YYYY-MM-DD HH:mm:ss'))}}</span></div>
            <div><span>{{$t('baogaoxqing.jssj')}}:</span><span>{{Moment(jbzhaiyao.end_time).format(('YYYY-MM-DD HH:mm:ss'))}}</span></div>
            <div><span>{{$t('baogaoxqing.yjxssc')}}:</span><span>{{hh}}:{{ss}}:{{mm}}</span></div>
            <div><span>{{$t('baogaoxqing.sjxssc')}}:</span><span>{{sjhh}}:{{sjss}}:{{sjmm}}</span></div>
        </div>
        </div>
        <div class="tishiwufa">{{$t('dayinbaogao.rgwfxzqjp')}}</div>
        <div class="printbottom"><button @click="toGetPdf">{{$t('dayinbaogao.xzbaogao')}}</button></div>
    </div>
</template>
<script>
import Moment from 'moment'
export default {
  data: () => ({
    id: '',
    jbzhaiyao: '',
    sjxx: '',
    caozuorenmingzi: '',
    xswcl: '',
    mm: '',
    ss: '',
    hh: '',
    sjmm: '',
    sjss: '',
    sjhh: ''
  }),
  created() {
    this.id = this.$route.params.taskid
    this.gettaskjob()
  },
  methods: {
    Moment,
    toGetPdf() {
      localStorage.setItem('pdfrenwuming', this.jbzhaiyao.task_name)
      setTimeout(() => {
        window.scrollTo(0, 0)
        this.getPdf(this.title)
      }, 500)
    },
    fanhui() {
      this.$router.go(-1)
    },
    async gettaskjob() {
      try {
        const resczr = {
          cmd: 'get_tags',
          task_id: Number(this.id)
        }
        const resczrmingzi = await this.Api.gethistoryte(resczr)
        console.log(resczrmingzi)
        this.caozuorenmingzi = resczrmingzi.data.task_tags[0].value
      } catch {
      }
      try {
        const detailsreq = {
          cmd: 'get_details',
          task_id: Number(this.id),
          get_point_logs: true
        }
        // const restaskfo = await this.Api.gethistoryte(taskreq)
        const resdetails = await this.Api.gethistoryte(detailsreq)
        this.xswcl = Math.floor((resdetails.data.point_records[0].disinfect_dur / resdetails.data.point_records[0].disinfect_dur_config) * 100)
        this.mm = resdetails.data.point_records[0].disinfect_dur_config % 60
        this.ss = Math.floor((resdetails.data.point_records[0].disinfect_dur_config / 60)) % 60
        this.hh = Math.floor((resdetails.data.point_records[0].disinfect_dur_config / 3600))
        this.sjmm = Math.floor((resdetails.data.point_records[0].disinfect_dur)) % 60
        this.sjss = Math.floor((resdetails.data.point_records[0].disinfect_dur / 60)) % 60
        this.sjhh = Math.floor((resdetails.data.point_records[0].disinfect_dur) / 3600)
        if (this.mm < 10) {
          this.mm = '0' + this.mm
        }
        if (this.ss < 10) {
          this.ss = '0' + this.ss
        }
        if (this.hh < 10) {
          this.hh = '0' + this.hh
        }
        if (this.sjss < 10) {
          this.sjss = '0' + this.sjss
        }
        if (this.sjmm < 10) {
          this.sjmm = '0' + this.sjmm
        }
        if (this.sjhh < 10) {
          this.sjhh = '0' + this.sjhh
        }
        console.log(resdetails)
      } catch {
      }
      try {
        const taskreq = {
          cmd: 'get_task',
          task_id: Number(this.id)
        }
        const restaskfo = await this.Api.gethistoryte(taskreq)
        this.jbzhaiyao = restaskfo.data
        console.log(restaskfo)
      } catch {
      }
    }
  }
}
</script>
<style lang="less" scoped>
.printdy{
    font-size: 0.14rem;
    >h3{
        display: flex;
        img{
            margin-top: 0.03rem;
            width: 0.16rem;
            height: 0.16rem;
            margin-left: 0.2rem;
        }
        span{
            width: 3rem;
            text-align: center;
        }
    }
    .jibenzhaiyaoo{
        margin: 0 0.2rem;
        h4{
        // border-top: 0.01rem solid #acacac;
        border-bottom: 0.01rem solid #acacac;
        line-height: 0.22rem;
        margin: 0;
        }
        div{
            border-bottom: 0.01rem solid #acacac;
            height: 0.3;
            line-height: 0.3rem;
            span{
                display: inline-block;
            }
            span:nth-child(1){
                width: 45%;
            }
        }
        >div:nth-child(2n){
            background: rgba(0, 0, 0, 0.15);
        }
    }
    .tongjixinxi{
        margin: 0 0.2rem;
        margin-top: 0.05rem;
         >h4{
        border-bottom: 0.01rem solid #acacac;
        line-height: 0.22rem;
        margin: 0;
        }
        div{
            border-bottom: 0.01rem solid #acacac;
            height: 0.3rem;
            line-height: 0.3rem;
            span{
                display: inline-block;
            }
            span:nth-child(1){
                width: 45%;
            }
        }
        >div:nth-child(2n){
            background: rgba(0, 0, 0, 0.15);
        }
    }
    .printbottom{
        margin-top: 0.8rem;
        text-align: center;
        button{
            display: inline-block;
            background:none;
            border: 0.01rem solid #17A69F;
            color: #17A69F;
        }
    }
    .tishiwufa{
      margin-top: 0.1rem;
      text-align: center;
      color: red;
    }
}
</style>
