<script>
import Showpivot from './showpivot'
import Xsfodd1 from './xsfodd1'
import Xsfodd2 from './xsfodd2'
import Xsfodd3 from './xsfodd3'
export default {
  data: () => ({
    active: 1,
    showpivot: 60,
    bottom_lamps: '',
    battery_level: '',
    charge_cycles: '',
    disk_space: '',
    top_lamps: '',
    types: '',
    timerss: null,
    mustuatus: '',
    recordstatus: '',
    ipdz: '',
    STAipdz: '',
    booleanzhi: false,
    freq: ''
  }),
  props: ['robots', 'nameboot'],
  created() {
    this.statuto()
    this.getsstuatus()
    this.robotAP()
    this.robotSTA()
  },
  methods: {
    async robotAP() {
      try {
        const robtip = {
          cmd: 'get_network_settings',
          mode: 'AP'
        }
        const resAPip = await this.Api.Statuto(robtip)
        console.log(resAPip)
        this.freq = resAPip.data.freq
        this.ipdz = resAPip.data.ip_addr ? resAPip.data.ip_addr : 'N/A'
      } catch {
      }
    },
    async robotSTA() {
      try {
        const robSTAip = {
          cmd: 'get_network_settings',
          mode: 'STA'
        }
        // 获取机器人IP地址
        // const resAPip = await this.Api.Statuto(robtip)
        const resSTAip = await this.Api.Statuto(robSTAip)
        this.freq = resSTAip.data.freq
        if (resSTAip.data.enabled) {
          this.STAipdz = resSTAip.data.ip_addr
        } else {
          // this.ipdz = resAPip.data.ip_addr
        }
      } catch {
      }
    },
    async statuto() {
      // if (this.$props.nameboot.robot_mode === 'MANUAL') {
      //   this.booleanzhi = false
      // } else {
      //   this.booleanzhi = true
      // }
      const paoms = {
        cmd: 'get_component_status',
        battery_level: false,
        charge_cycles: false,
        disk_space: true,
        uv_lamps: true
      }
      try {
        // 请求生命组件的各类值
        const resa = await this.Api.Statuto(paoms)
        console.log(resa)
        this.bottom_lamps = resa.data.uv_lamps[1].status
        this.top_lamps = resa.data.uv_lamps[0].status
        // this.battery_level = resa.data.battery_level
        // this.charge_cycles = resa.data.charge_cycles
        this.disk_space = resa.data.disk_space
        // if (this.charge_cycles[0] === -1) {
        //   this.charge_cycles[0] = 0
        // }
        if (this.disk_space[0] <= -1) {
          this.disk_space[0] = 0
        }
      } catch {
      }
    },
    // 与消杀详情页面逻辑一样判定是否正在有消杀任务 如果有消杀任务并且为OTS类型
    async getsstuatus() {
      const params = {
        cmd: 'get_task_state',
        include_detailed_state: false
      }
      try {
        const res = await this.Api.gettaskstate(params)
        if (res.data.task_type === 'OTS') {
          try {
            const pamsrams = {
              cmd: 'get_task_state'
            }
            const taskSstate = await this.Api.getotsstate(pamsrams)
            console.log(taskSstate)
            if (taskSstate.data.status === 'DISINFECT' || taskSstate.data.status === 'PAUSE') {
              // 如果为正在消杀或者暂停时候
              this.types = 'A'
              this.mustuatus = taskSstate.data.status
            } else if (taskSstate.data.status === 'START_COUNT_DOWN' || taskSstate.data.status === 'RESUME_COUNT_DOWN') {
              // 如果为消杀倒计时和恢复消杀倒计时时
              this.$router.push('/countdown')
            }
          } catch {
          }
        }
      } catch (err) {
        // 如果没有消杀任务为404在发送请求是否有历史记录 如果有显示B 如果没有则直接走C
        if (err.request.status === 404) {
          try {
            const pamolatest = {
              cmd: 'get_latest'
            }
            localStorage.setItem('shifoujxrw', false)
            const resa = await this.Api.gethistoryte(pamolatest)
            this.recordstatus = resa.data
            this.types = 'B'
            console.log(resa.data)
          } catch {
            localStorage.setItem('shifoujxrw', false)
            this.types = 'C'
          }
        }
      }
    },
    fnpush() {
      const id = localStorage.getItem('usenameID')
      this.$router.push(`/disntask/${id}`)
    }
  },
  components: {
    Showpivot,
    Xsfodd1,
    Xsfodd2,
    Xsfodd3
  }
}
</script>
