<template>
    <div class="xsfod3">
        <div class="topimg">
          <img src="../../../../../assets/redzuidiceng.png" alt="" class="imgdiceng">
            <img src="../../../../../assets/zzdyuandian.png" alt="">
            <!-- <van-circle v-model="currentRate" :rate="30" :speed="100" :text="text" > -->
            <div class="topabsulote">
                <van-circle v-model="currentRate" :rate="fordurtske" :speed="100" default size="1.3rem" :clockwise="falgeclo" :color="colorjbs" layer-color='none'>
                <h4><img v-if="recordstatus.abort_reason==='213'" src="../../../../../assets/zhongzhitubiaoa.png" alt="">
                <img v-else-if="recordstatus.abort_reason==='USER' || recordstatus.abort_reason==='RC' || recordstatus.abort_reason==='EMERGENCY_STOP'" src="../../../../../assets/tubiaozhongzhi.png" alt="">
                <img v-else-if="recordstatus.abort_reason==='SYSTEM_ERROR' || recordstatus.abort_reason==='BATTERY_LOW' || recordstatus.abort_reason==='SAFETY_SENSOR'" src="../../../../../assets/beidongzz.png" alt=""></h4>
                <div class="topabfont">{{$t('homes.zhongzhi')}}</div>
                <div class="bottomfont">
                    <span>{{$t('homes.leiji')}}：</span><span>{{recordstatus.disinfect_dur}}S</span>
                </div> </van-circle>
                <!-- <h4>×</h4>
                <div class="topabfont">中止</div>
                <div class="bottomfont">
                    <span>累计：</span><span>{{recordstatus.disinfect_dur}}min</span>
                </div> -->
            </div>
        </div>
        <div class="xsfod3bottom">
           <div class="xfd3son1">
            <div class="xfd3ssson"><span>{{$t('homes.ksshijian')}}：</span><span>{{begtime}}</span></div>
            <div class="xfd3ssson"><span>{{$t('homes.jsshijian')}}：</span><span>{{endtime}}</span></div>
           </div>
           <div class="xfd3son2">
               <img src="../../../../../assets/doubleright.png" alt="">
           </div>
        </div>
    </div>
</template>
<script>
import Moment from 'moment'
export default {
  data: () => ({
    begtime: '',
    endtime: '',
    currentRate: 0,
    falgeclo: false,
    colorjbs: {
      '0%': '#FFA970',
      '100%': '#FF090A'
    },
    fordurtske: null
  }),
  props: ['recordstatus'],
  created() {
    console.log(this.recordstatus)
    // 用moment转化后台给的开始时间戳和结束时间戳
    this.begtime = Moment(this.recordstatus.begin_time).format(('YYYYMMDD HH:mm'))
    this.endtime = Moment(this.recordstatus.end_time).format(('YYYYMMDD HH:mm'))
    this.radiusmath()
  },
  methods: {
    async radiusmath() {
      const gettask = {
        cmd: 'get_details',
        task_id: Number(this.recordstatus.id),
        get_point_logs: true
      }
      try {
        const restime = await this.Api.gethistoryte(gettask)
        console.log(restime)
        this.fordurtske = ((restime.data.point_records[0].disinfect_dur_config - this.$props.recordstatus.disinfect_dur) / (restime.data.point_records[0].disinfect_dur_config)) * 100
      } catch {
      }
    }
  }
}
</script>
