<template>
    <div class="disitask">
      <div class="tasktop">
     <img src="../../assets/tongyongmu1.png" alt="" @click="downshow">
     <div>{{$t('xiaosharw.xsrw')}}</div>
        </div>
        <Bldask v-if="nontype==='A'" :mustatus="mustuatus" :taskreason="taskreason"  @gettaskstate="gettaskstate"/>
        <Achievdask v-else-if="nontype==='B'" :recordstatus="recordstatus"/>
        <Nodask v-else-if="nontype==='C'" />
        <Absoluts ref="absolutesa" :id="id"  />
    </div>
</template>
<script>
import Absoluts from '../../components/tutorabsoluts/index'
import Nodask from './nodask/index'
import Bldask from './bldask/index'
import Achievdask from './achievedask'
export default {
  data: () => ({
    id: '',
    nontype: '',
    mustuatus: '',
    recordstatus: '',
    modefulllow: '',
    taskreason: ''
  }),
  inject: ['reload'],
  created() {
    this.id = this.$route.params.id
    this.gettaskstate()
  },
  methods: {
    downshow() {
      this.$refs.absolutesa.$el.style.display = 'block'
    },
    async gettaskstate() {
      const params = {
        cmd: 'get_task_state',
        include_detailed_state: false
      }
      // 先发送请求 是否有正在消杀任务 在判断是否是OTS 在判定是什么模式把子组件显示出来
      try {
        const res = await this.Api.gettaskstate(params)
        if (res.data.task_type === 'OTS') {
          try {
            const pamsrams = {
              cmd: 'get_task_state'
            }
            const taskSstate = await this.Api.getotsstate(pamsrams)
            this.taskreason = taskSstate.data.pause_reason
            console.log(taskSstate)
            // this.nontype = ''
            if (taskSstate.data.status === 'DISINFECT' || taskSstate.data.status === 'PAUSE') {
              this.nontype = 'A'
              this.mustuatus = taskSstate.data.status
            } else if (taskSstate.data.status === 'START_COUNT_DOWN' || taskSstate.data.status === 'RESUME_COUNT_DOWN') {
              this.$router.push('/countdown')
            } else if (taskSstate.data.status === 'ABORT' || taskSstate.data.status === 'COMPLETE') {
              this.reload()
              localStorage.setItem('shifoujxrw', false)
            }
          } catch {
            this.reload()
          }
        }
      } catch (err) {
        // console.log(err)
        // 如果是404在发送请求看是否有历史记录如果没有历史记录继续返回404显示没有任务页面
        if (err.request.status === 404) {
          try {
            const pamolatest = {
              cmd: 'get_latest'
            }
            const resa = await this.Api.gethistoryte(pamolatest)
            this.nontype = 'B'
            localStorage.setItem('shifoujxrw', false)
            console.log(resa, 'zuijinrenwu')
            this.modefulllow = resa.data.power_mode
            this.recordstatus = resa.data
          } catch (err) {
            if (err.request.status === 404) {
              this.nontype = 'C'
            }
          }
        }
      }
    }
  },
  components: {
    Nodask,
    Absoluts,
    Bldask,
    Achievdask
  }
}
</script>
<style lang="less" scoped>
.disitask{
   height: 6.67rem;
   box-sizing: border-box;
            font-size: 0.14rem;
            overflow: none;
            position: relative;
        .tasktop{
          padding-top: 0.13rem;
            height: 0.28rem;
            width: 100%;
            // text-align: center;
            img{
                margin-left: 0.24rem;
                vertical-align:middle;
                width: 0.24rem;
                height: 0.24rem;
            }
          div{
              display: inline-block;
              width: 2.9rem;
              text-align: center;
              font-size: 0.16rem;
              font-family: PingFangSC-Medium, PingFang SC;
              font-weight: 500;
              color: rgba(0, 0, 0, 0.85);
              line-height: 0.24rem;
          }
        }
}
</style>
